[data-theme="dark"] {
    .maps-container {
        .options svg {
            background-color: var(--phone-color-primary);
            color: white;
            &:hover {
                background-color: #191919;
            }
        }
    }
}

.maps-container {
    margin-top: 0.25rem;
    height: 99%;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;
    overflow: hidden;

    .map-wrapper {
        position: absolute;
        height: 115%;
        width: 115%;

        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 9;

        .map {
            .leaflet-tile {
                mix-blend-mode: unset;
            }
            height: 100%;
            width: 100%;
            background: rgb(20, 61, 107);

            .leaflet-popup,
            .leaflet-popup-content-wrapper {
                background-color: var(--phone-color-primary);
                border-radius: 15px;
                color: var(--phone-text-primary);
            }

            .location-popup {
                display: flex;
                flex-direction: column;
                align-items: center;
                background-color: var(--phone-color-primary);

                .name {
                    font-size: 16px;
                    font-weight: 600;
                    color: var(--phone-text-primary);
                }

                .description {
                    font-size: 14px;
                    font-weight: 400;
                    font-style: italic;
                    color: var(--phone-text-secondary);
                }

                .button {
                    margin-top: 1rem;

                    width: 5rem;
                    height: 2rem;

                    background-color: var(--phone-color-blue);
                    color: #f7f7f6;
                    font-size: 15px;
                    font-weight: 500;

                    border-radius: 10px;

                    display: flex;
                    align-items: center;
                    justify-content: center;

                    cursor: pointer;
                }
            }
        }
    }

    .options {
        position: absolute;
        z-index: 400;

        top: 10rem;
        right: 2.5rem;

        display: flex;
        flex-direction: column;
        align-items: center;

        svg {
            padding: 0.8rem;
            background-color: #f7f7f6;

            font-size: 24px;
            color: #6c6c6c;

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:first-child {
                border-bottom: 1px solid #6c6c6c;
                border-top-left-radius: 15px;
                border-top-right-radius: 15px;
            }

            &:last-child {
                border-bottom-left-radius: 15px;
                border-bottom-right-radius: 15px;
            }

            &:hover {
                filter: brightness(0.85);
            }
        }
    }

    .bottom-search {
        position: absolute;
        bottom: 0;

        z-index: 10;
        background-color: #f7f7f6;

        width: 85%;

        border-radius: 15px;
        padding: 1.25rem 1.5rem;
        padding-bottom: 3.5rem;

        display: flex;
        flex-direction: column;
        align-items: center;

        &.expanded {
            .search-results {
                .item {
                    display: flex;
                    align-items: center;

                    .name {
                        color: #000000;
                    }

                    .distance {
                        color: #6c6c6c;
                    }
                }
            }
        }
    }
}
