@import "styles/Colors.scss";
@import "styles/Animations.scss";

* {
    user-select: none;
}

body {
    overflow: hidden;
}

.phoneVisbility {
    transition: all 0.75s ease-in-out;
    visibility: hidden;
}

.container {
    height: 100vh;
    width: 100%;
    display: flex;

    justify-content: flex-end;
    align-items: flex-end;

    font-family: "Roboto";
    font-style: normal;
}

.full-phone {
    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;
    width: 29rem;
    height: 58.5rem;
    margin: 1rem;

    transition: all 0.5s ease-in-out;

    overflow: hidden;
    backface-visibility: hidden;
    transform-origin: bottom right;
}

.frame {
    transition: 0.5s;
    position: absolute;

    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    width: 118.6%;
    height: 118.6%;

    z-index: 999;
    pointer-events: none;

    // opacity: ;
}

.phone-container {
    position: relative;

    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    // height: 97.5%;
    // width: 94%;

    width: 88.75%;
    height: 95.25%;
    overflow: hidden;

    // border-radius: 45px;
    border-radius: 1rem;

    backface-visibility: hidden;
    background-color: var(--phone-color-primary);

    .no-battery {
        height: 100%;
        width: 100%;

        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            color: rgba(255, 255, 255, 0.5);
            font-size: 75px;
        }

        img {
            width: 10rem;
            height: 10rem;
            object-fit: cover;
        }
    }

    .white-screen {
        position: absolute;
        z-index: 9999;

        background-color: #ffffff;
        width: 100%;
        height: 100%;

        border-radius: 50px;
    }

    .screenshot-container {
        position: absolute;
        bottom: 2.5rem;
        left: 1.5rem;

        z-index: 999;

        height: 13rem;
        width: 6rem;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;

            border-radius: 22px;
            border: 6px solid #ffffff;

            cursor: pointer;
        }
    }
}

.brightness {
    position: absolute;
    width: 95%;
    height: 95%;
    background-color: rgba(0, 0, 0, 1);
    border-radius: 50px;

    z-index: 999;

    pointer-events: none;
}

.number {
    color: var(--phone-color-blue);
    cursor: pointer;

    font-weight: 600;

    &:hover {
        text-decoration: underline;
    }
}

.app-container {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;

    background-position: center;
    background-size: cover;
    position: relative;

    iframe {
        width: 100%;
        height: 100%;
        border: none;

        overflow: hidden;

        &::-webkit-scrollbar {
            display: none;
        }
    }
}
