@import "../../../styles/Colors.scss";

.clock-alert {
    position: absolute;

    height: 100%;
    width: 100%;
    z-index: 98;

    background-color: rgba(0, 0, 0, 0.85);
    backdrop-filter: blur(15px);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;

    .title {
        margin-top: -5rem;

        font-size: 38px;
        font-weight: 400;
        color: #ffffff;
    }

    .button {
        display: flex;
        justify-content: center;
        align-items: center;

        width: 50%;
        height: 4rem;
        border-radius: 35px;

        font-size: 20px;
        color: #ffffff;
        background-color: var(--phone-color-orange);

        cursor: pointer;
        transition: all 0.2s ease-in-out;

        &:hover {
            filter: brightness(0.7);
        }
    }
}
