@import "../../../styles/Colors.scss";
@import "../../../styles/Animations.scss";

[data-theme="light"] .context-buttons {
    section {
        div {
            &:hover:not([data-disabled="true"]) {
                background-color: #00000034;
            }
        }
    }
}

[data-theme="dark"] .context-buttons {
    section {
        div {
            &:hover:not([data-disabled="true"]) {
                background-color: #ffffff34;
            }
        }
    }
}

.context-container {
    position: absolute;

    right: 0;

    width: 100%;
    height: 100%;

    z-index: 999;

    border-radius: 40px;

    background-color: rgba(0, 0, 0, 0.6);

    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}

.context-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    gap: 0.75rem;

    width: 100%;

    margin-bottom: 2.5rem;

    section {
        background-color: var(--phone-color-primary);
        border-radius: 15px;

        width: 90%;

        div {
            height: 3.75rem;
            display: flex;
            align-items: center;
            justify-content: center;

            color: var(--phone-color-blue);
            font-size: 20px;
            font-weight: 500;

            border-bottom: 1px solid var(--phone-text-secondary);

            &.red {
                color: var(--phone-color-red);
            }

            &.title {
                font-size: 14px;
                color: var(--phone-text-secondary);
                height: 3rem;
            }

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &[data-disabled="true"] {
                filter: opacity(0.5);
                cursor: not-allowed;
            }

            &:nth-child(1) {
                border-radius: 15px 15px 0 0;
            }

            &:last-child {
                border-bottom: none;
                border-radius: 0 0 15px 15px;
            }

            &:only-child {
                border-radius: 15px;
            }

            &.cancel {
                font-weight: 500;
                border-radius: 15px;
            }
        }
    }
}
