.app {
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.2rem 0.4rem;
    cursor: pointer;

    text-align: center;

    .appIcon {

        &:hover{
            filter: brightness(75%);
        }
    }

    &[data-editmode="true"] {
        animation-delay: -0.22s;
        animation-duration: 0.3s;

        &:nth-child(2n) {
            animation-name: appJiggle;
            animation-iteration-count: infinite;
            transform-origin: 50% 10%;
        }

        &:nth-child(2n-1) {
            animation-name: appJiggle2;
            animation-iteration-count: infinite;
            animation-direction: alternate;
            transform-origin: 30% 5%;
        }
    }

    &[data-downloading="true"] {
        .progress-bar-container {
            position: absolute;
            height: 50%;
            width: 50%;
            z-index: 1;

            left: 0;
            right: 0;
            top: 0;
            bottom: 1.5rem;
            margin: auto;

            .progress-bar {
                background-color: rgba(230, 230, 230, 0.2);
                border-radius: 50%;
                border: 4px solid rgba(255, 255, 255, 0.5);
            }
        }

        .appIcon {
            filter: brightness(0.5);
        }
    }

    .appIcon {
        border-radius: 12.5px;
        box-shadow: rgba(59, 59, 59, 0.3) 0px 0px 3px;

        width: 4rem;
        height: 4rem;

        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .appName {
        margin-top: 0.3rem;
        font-size: 13px;
        font-weight: 400;
    }

    .notifications {
        position: absolute;
        top: -0.35rem;
        right: 0.2rem;

        width: 1.8rem;
        height: 1.8rem;
        border-radius: 50%;

        display: flex;
        align-items: center;
        justify-content: center;

        background-color: var(--phone-color-red);
        font-size: 16px;
    }
}

.appDelete {
    position: absolute;
    top: -0.25rem;
    left: 0.2rem;
    width: 1.7rem;
    height: 1.7rem;
    background-color: rgba(167, 167, 167, 0.9);
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    .appDeleteBlur {
        position: absolute;

        width: 100%;
        height: 100%;
        border-radius: 50%;
    }

    i {
        position: absolute;

        color: #000000;
        font-size: 17px;
    }
}
