@import "styles/Animations.scss";

.showimage-container {
    width: 100%;
    height: 100%;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;

    background-color: rgba(0, 0, 0, 0.85);

    display: flex;
    justify-content: center;
    align-items: center;

    .showimage-image {
        height: 50%;
        width: 50%;
        max-width: 40rem;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 0.5rem;

        animation: zoomIn 0.2s cubic-bezier(0.19, 1, 0.22, 1);

        img,
        video {

            max-height: 150%;
            object-fit: contain;
        }

        .text {
            font-family: "Roboto";
            font-style: normal;


            font-size: 13px;
            color: rgb(201, 201, 201);

            cursor: pointer;

            &:hover {
                text-decoration: underline;
            }
        }

    }

}
