.switch {
    position: relative;
    display: inline-block;
    width: 3rem;
    height: 1.75rem;

    input {
        display: none;
        height: 0;
        width: 0;

        &:checked + .slider {
            background-color: #57d26e;
        }

        &:focus + .slider {
            box-shadow: 0 0 1px #57d26e;
        }
    }

    &.light {
        .slider {
            background-color: #ccc;
        }
    }

    &.dark {
        .slider {
            background-color: #4b4b4d;
        }
    }

    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transition: 0.4s;
        border-radius: 34px;

        &::before {
            border-radius: 50%;
            position: absolute;
            content: "";
            height: 1.5rem;
            width: 1.5rem;
            left: 2px;
            bottom: 2px;
            background-color: white;
            -webkit-transition: 0.4s;
            transition: 0.4s;
        }
    }

    input:checked + .slider:before {
        -webkit-transform: translateX(1.1rem);
        -ms-transform: translateX(1.1rem);
        transform: translateX(1.1rem);
    }
}
