.header {
    position: absolute;
    top: 0;
    width: 81.5%;

    padding: 1.2rem 2.5rem;
    padding-top: 0.9rem;

    font-weight: 500;
    font-size: 18px;
    line-height: 23px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;

    transition: 0.5s;

    z-index: 99;

    pointer-events: none;

    .header-editMode {
        width: 97%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        pointer-events: all;

        .button {
            border-radius: 20px;

            height: 1.5rem;
            width: auto;
            padding: 0 0.9rem;

            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(167, 167, 167, 0.75);
            backdrop-filter: blur(10px);

            font-size: 16px;
            font-weight: bold;
            color: #000000;

            cursor: pointer;

            &:first-child {
                font-size: 25px;
                font-weight: 400;

                padding: 0 1.75rem;
            }
        }
    }

    .time {
        display: flex;
        align-items: center;

        margin-left: 0.8rem;

        &.incall {
            display: flex;
            align-items: center;
            justify-content: center;

            background-color: #08e045bf;
            transition: all 0.5s;

            padding: 0 1rem;
            border-radius: 20px;

            color: #ffffff;

            cursor: pointer;
            pointer-events: all;

            margin-left: 0;
        }
    }

    .right {
        position: relative;

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.3rem;

        margin-right: 0.4rem;
        font-weight: 500;

        .top {
            display: flex;
            flex-direction: row;
            align-items: center;
            font-weight: 500;
            height: 1em;

            gap: 0.3rem;

            width: 100%;

            &[data-theme="dark"] {
                .battery {
                    border: 2px solid rgba(30, 30, 30, 0.5);

                    .battery-bar {
                        background-color: #000000;
                    }
                }
                .battery-plup {
                    background-color: rgba(30, 30, 30, 0.5);
                }

                .service {
                    div {
                        background-color: rgba(30, 30, 30, 0.5);

                        &.active {
                            background-color: #000000;
                        }
                    }
                }
            }
        }

        svg {
            font-size: 20px;

            &.battery {
                font-size: 27px;
            }
        }

        .service {
            display: flex;
            flex-direction: row;

            align-items: flex-end;
            gap: 0.2rem;

            div {
                background-color: rgba(255, 255, 255, 0.5);
                height: 0.8rem;
                width: 0.21em;

                border-radius: 1px;

                &:nth-child(1) {
                    height: 0.25rem;
                }

                &:nth-child(2) {
                    height: 0.45rem;
                }

                &:nth-child(3) {
                    height: 0.65rem;
                }

                &.active {
                    background-color: #ffffff;
                }
            }
        }

        .control-centre-bar {
            width: 100%;
            height: 0.2rem;

            cursor: pointer;

            opacity: 0.8;

            pointer-events: all;
            border-radius: 5px;
        }
    }
}

.battery-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.1rem;

    &[data-big="true"] {
        .battery {
            width: 8rem;
            height: 3rem;

            border-radius: 10px;

            .battery-bar {
                border-radius: 5px;
                height: 100%;
                margin-left: 0.2rem;
            }
        }

        .battery-plup {
            height: 1rem;
            width: 0.3rem;
            margin-bottom: 0.025rem;

            background-color: rgba(255, 255, 255, 0.5);

            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
        }
    }

    .battery {
        width: 1.5rem;
        height: 0.7rem;
        border-radius: 5px;

        border: 2px solid rgba(255, 255, 255, 0.5);
        backdrop-filter: blur(5px);

        position: relative;

        display: flex;
        align-items: center;
        justify-content: flex-start;

        svg {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            margin: auto;
            z-index: 1;

            font-size: 13px;
            color: #ffffff;
        }

        .battery-bar-wrapper {
            display: flex;
            align-items: center;

            width: 100%;
            height: 80%;

            margin: 0.075rem;
        }

        .battery-bar {
            height: 100%;

            background-color: #ffffff;
            border-radius: 3px;

            &[data-savemode="true"] {
                background-color: var(--phone-color-yellow);
            }

            &[data-low="true"] {
                background-color: var(--phone-color-red);
            }

            &[data-charging="true"] {
                background-color: var(--phone-color-green);
            }
        }
    }

    .battery-plup {
        height: 0.4rem;
        width: 0.15rem;
        margin-bottom: 0.025rem;

        background-color: rgba(255, 255, 255, 0.5);

        border-top-right-radius: 2.5px;
        border-bottom-right-radius: 2.5px;
    }
}
