@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

[data-theme="light"] {
    .appstore-container {
        .button {
            background-color: rgba(118, 118, 128, 0.2);

            &:hover {
                background-color: rgba(118, 118, 128, 0.4);
            }
        }
    }
}

[data-theme="dark"] {
    .appstore-container {
        .button {
            background-color: rgba(180, 180, 180, 0.2);

            &:hover {
                background-color: rgba(180, 180, 180, 0.4);
            }
        }
    }
}

.appstore-container {
    position: relative;
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: var(--phone-color-primary);

    .right {
        animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    }

    .left {
        animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    }

    .appstore-header {
        width: 90%;
        margin-top: 3.5rem;
        height: 6%;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0.5rem;

        &[data-hidden="true"] {
            visibility: none;
        }

        .info {
            display: flex;
            flex-direction: column;

            &:hover{
                filter: brightness(90%);
            }

            .date {
                font-weight: 400;
                font-size: 15px;
                color: var(--phone-text-secondary);
            }

            .back {
                margin-left: -0.75rem;

                display: flex;
                align-items: center;

                color: var(--phone-color-blue);
                font-size: 20px;
                font-weight: 400;
                cursor: pointer;

                svg {
                    font-size: 30px;
                }
            }

            .title {
                color: var(--phone-text-primary);
                font-size: 30px;
                font-weight: 600;
                font-style: bold;
            }
        }

        svg {
            color: var(--phone-color-blue);

            font-size: 45px;
        }
    }

    .appstore-wrapper {
        height: 78%;
        width: 90%;

        overflow-y: auto;
        overflow-x: hidden;

        max-height: 100%;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    .appstore-footer {
        display: flex;
        justify-content: center;
        gap: 3rem;

        height: 14%;
        width: 100%;

        background-color: var(--phone-color-highlight);
        border-radius: 5px;

        .item {
            margin-top: 0.75rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 0.25rem;

            color: var(--phone-text-secondary);
            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:hover {
                color: var(--phone-color-blue);

                svg {
                    fill: var(--phone-color-blue);
                }

                .name {
                    font-weight: 500;
                }
            }

            svg {
                font-size: 30px;
                fill: var(--phone-text-secondary);
            }

            .name {
                font-size: 16px;
                font-weight: 400;
            }

            &[data-active="true"] {
                color: var(--phone-color-blue);

                svg {
                    fill: var(--phone-color-blue);
                }

                .name {
                    font-weight: 500;
                }
            }
        }
    }

    .appstore-app {
        width: 100%;

        animation: zoomIn 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    }

    .appstore-page {
        width: 100%;

        max-height: 37.5rem;

        display: flex;
        flex-direction: column;
        align-items: center;

        section {
            display: flex;
            flex-direction: column;
            margin-top: 1rem;
            width: 97%;

            .title {
                font-size: 22px;
                font-weight: 600;
                color: var(--phone-text-primary);
            }

            .apps {
                display: flex;
                flex-direction: column;
                align-items: center;

                width: 100%;

                overflow-y: auto;
                overflow-x: hidden;

                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }
    }

    .appstore-search {
        width: 100%;

        .searchbox {
            width: 90%;
            margin-bottom: 1rem;
        }

        .app-list {
            display: flex;
            flex-direction: column;
            align-items: center;

            max-height: 37.5rem;
            width: 100%;

            overflow-y: auto;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                display: none;
            }
        }
    }

    .app-item {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;

        padding: 1rem 0;

        border-bottom: 1px solid var(--phone-color-border);

        &:hover{
            filter: brightness(90%);
        }

        &:last-child {
            border-bottom: none;
        }

        .upper {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            height: 100%;

            .app-info {
                display: flex;
                align-items: center;
                gap: 0.5rem;
                height: 100%;

                &[data-big="true"] {
                    display: grid;
                    justify-content: end;
                    grid-auto-flow: column;

                    gap: 0.75rem;
                    height: 100%;

                    .app-icon {
                        width: 100px;
                        height: 100px;
                        border-radius: 20px;
                    }

                    .app-content {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        justify-content: space-between;
                        height: 100%;

                        .app-name {
                            font-size: 20px;
                        }

                        .app-description {
                            color: var(--phone-text-secondary);
                            font-size: 16px;
                            font-weight: 400;
                        }
                    }
                }

                .app-icon {
                    width: 60px;
                    height: 60px;
                    border-radius: 10px;
                }

                .app-content {
                    .app-name {
                        font-size: 18px;
                        font-weight: 400;

                        color: var(--phone-text-primary);
                    }

                    .app-description {
                        color: var(--phone-text-secondary);
                        font-size: 16px;
                        font-weight: 400;
                    }
                }
            }

            .button {
                border-radius: 15px;
                padding: 0.3rem 1.3rem;

                color: var(--phone-color-blue);
                font-size: 16px;
                font-weight: 600;
                text-transform: uppercase;

                width: auto;
                text-align: center;

                cursor: pointer;
                transition: all 0.2s ease-in-out;
            }

            svg {
                margin-right: 1.5rem;
            }
        }

        .images {
            max-width: 100%;

            display: grid;
            grid-template-columns: repeat(3, 1fr);

            gap: 0.25rem;

            &[data-big="true"] {
                display: flex;
                flex-direction: row;
                overflow-x: scroll;
                scroll-behavior: smooth;

                img {
                    max-height: 100%;
                    width: 100%;

                    border-radius: 20px;
                }

                &::-webkit-scrollbar {
                    display: none;
                }
            }

            img {
                border-radius: 15px;

                height: 16rem;
                width: 7.5rem;
            }
        }

        section {
            width: 100%;

            margin-top: 1rem;

            .title {
                font-size: 20px;
                font-weight: 500;
                color: var(--phone-text-primary);
            }

            .items {
                margin-top: 0.5rem;
                width: 100%;

                display: flex;
                flex-direction: column;
                gap: 0.5rem;

                .item {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;

                    width: 100%;
                    border-bottom: 1px solid var(--phone-color-border);
                    padding: 0.5rem 0;

                    .title {
                        color: var(--phone-text-secondary);
                        font-size: 16px;
                        font-weight: 400;
                    }

                    .value {
                        color: var(--phone-text-primary);
                        font-size: 16px;
                        font-weight: 500;
                    }
                }
            }
        }
    }
}
