@import "./Colors.scss";
@import "./Animations.scss";

.lockscreen {
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    position: absolute;
    z-index: 95;

    overflow-y: auto;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;

    background-position: center;
    background-size: cover;

    border-radius: 25px;

    &::-webkit-scrollbar {
        display: none;
    }

    .top {
        position: relative;

        scroll-snap-align: center;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        padding-top: 15%;

        font-weight: 300;

        color: #ffffff;

        svg {
            font-size: 40px;
            margin-bottom: 1rem;
        }

        .time {
            font-size: 90px;
        }

        .date {
            font-size: 22px;
        }
    }

    .music-player-container {
        width: 85%;

        background-color: rgba(20, 20, 20, 0.7);
        // background-color: rgba(200, 200, 200, 0.6);

        backdrop-filter: blur(15px);
        border-radius: 20px;
        padding: 1rem;

        margin-top: 1.5rem;

        .music-player-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;

            width: 100%;
        }

        .music-player-header {
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 0.75rem;

            width: 100%;

            .artwork {
                height: 5.5rem;
                width: 5.5rem;
                border-radius: 7px;
            }

            .music-player-info {
                display: flex;
                flex-direction: column;
                color: #ffffff;

                .device {
                    opacity: 0.65;
                    font-size: 12px;
                    font-weight: 300;
                }

                .title {
                    font-size: 18px;
                    font-weight: 400;

                    // font-family: Roboto;
                }

                .artist {
                    margin-top: 0.1rem;
                    font-size: 16px;
                    opacity: 0.85;
                    font-weight: 400;
                }
            }
        }

        .duration-slider {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 0.3rem;

            width: 100%;

            input {
                width: 100%;

                // style it
                -webkit-appearance: none;
                background: transparent;
                outline: none;
                -webkit-transition: 0.2s;
                transition: opacity 0.2s;

                &::-webkit-slider-runnable-track {
                    width: 100%;
                    height: 0.25rem;
                    cursor: pointer;

                    backdrop-filter: blur(15px);

                    border-radius: 3px;
                }

                &::-webkit-slider-thumb {
                    margin-top: -0.2rem;
                    width: 0.75rem;
                    height: 0.75rem;
                    cursor: pointer;

                    background: #ffffff;
                    border-radius: 50%;
                    -webkit-appearance: none;
                }

                &:focus {
                    outline: none;
                }
            }

            .duration {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;

                width: 100%;

                font-size: 12px;
                font-weight: 300;
                color: #ffffff;

                font-weight: 300;
                opacity: 0.5;
            }
        }

        .music-player-actions {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            width: 65%;

            margin-top: -0.5rem;

            svg {
                background-color: inherit;
                color: #ffffff;
                padding: 0;

                font-size: 35px;
                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    transform: scale(1.1);
                    filter: brightness(0.7);
                }
            }

            .pause {
                svg {
                    font-size: 45px;
                    stroke-width: 20;
                }
            }
        }

        .volume-slider {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 0.5rem;

            width: 90%;

            svg {
                opacity: 0.4;
                color: #ffffff;
                font-size: 30px;
            }

            input {
                width: 100%;

                // style it
                -webkit-appearance: none;
                background: transparent;
                outline: none;
                -webkit-transition: 0.2s;
                transition: opacity 0.2s;

                &::-webkit-slider-runnable-track {
                    width: 100%;
                    height: 0.25rem;
                    cursor: pointer;

                    backdrop-filter: blur(15px);

                    border-radius: 3px;
                }

                &::-webkit-slider-thumb {
                    margin-top: -0.55rem;
                    width: 1.5rem;
                    height: 1.5rem;
                    cursor: pointer;

                    background: #ffffff;
                    border-radius: 50%;
                    -webkit-appearance: none;
                }

                &:focus {
                    outline: none;
                }
            }
        }
    }

    .actions {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        margin-bottom: 3.5rem;
        z-index: 8;
        opacity: 1;

        width: 70%;

        transition: all 0.3s ease-in-out;
        cursor: pointer;

        &.hidden {
            opacity: 0;
        }

        svg {
            color: #ffffff;
            padding: 1rem;
            border-radius: 50%;
            font-size: 30px;
            background: rgba(0, 0, 0, 0.35);

            transition: all 0.2s ease-in-out;

            &:nth-child(1) {
                rotate: -45deg;
            }

            &:hover {
                filter: brightness(1.3);
                scale: 1.03;
            }

            &[data-active="true"] {
                background-color: #ffffff;
                color: #000000;
            }
        }
    }

    .pin-wrapper {
        position: relative;
        padding-top: 15%;

        backdrop-filter: blur(10px);
        background: rgba(0, 0, 0, 0.35);

        width: 100%;
        height: 100%;

        display: flex;
        align-items: center;
        justify-content: center;

        .pin {
            animation: slideDown 1s cubic-bezier(0.19, 1, 0.22, 1);

            width: 80%;
            margin-bottom: 5rem;

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            font-weight: 300;

            color: #ffffff;

            .title {
                margin-top: 5rem;

                font-size: 24px;
                font-weight: 400;
            }

            .numbers {
                margin-top: 5rem;
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                -ms-grid-columns: repeat(3, 1fr);

                gap: 2rem;

                div {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    background: rgba(145, 145, 145, 0.2);

                    border-radius: 50%;
                    height: 5rem;
                    width: 5rem;

                    text-align: center;
                    font-size: 34px;
                    font-family: "Roboto";
                    font-weight: 400;

                    &:hover {
                        cursor: pointer;
                        background: rgba(145, 145, 145, 0.55);
                    }

                    span {
                        font-size: 12px;
                        font-weight: 400;
                        letter-spacing: 3px;
                    }
                }
            }

            .bottom {
                z-index: 1;
                margin-top: 6rem;

                font-size: 17px;

                &:hover {
                    cursor: pointer;
                }
            }
        }
    }
}
