@import "styles/Colors.scss";

[data-theme="dark"] {
    .faceid {
        filter: invert(100%);
    }
}

.setupContainer {
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    overflow: hidden;

    .titleContainer {
        margin: auto;

        font-weight: 500;
        font-size: 4rem;

        color: var(--phone-text-primary);

        &.small {
            font-size: 2rem;
        }
    }

    .footer {
        margin-bottom: 2rem;
        font-weight: 400;
        font-size: 17px;

        color: var(--phone-text-primary);
    }

    .chooseLanguage {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 5rem;

        .globe {
            padding-top: 10rem;
            font-size: 90px;
            font-weight: 300;
            color: var(--phone-color-blue);
        }

        .languages {
            width: 100%;

            display: flex;
            flex-direction: column;
            align-items: center;

            gap: 0.75rem;

            overflow-y: auto;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                display: none;
            }

            padding-bottom: 3rem;

            .item {
                width: 70%;

                background-color: var(--phone-color-highlight2);

                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;

                padding: 0.75rem 1rem;
                border-radius: 5px;

                font-weight: 500;
                font-size: 18px;

                cursor: pointer;

                .label {
                    color: var(--phone-text-primary);
                }

                i {
                    font-size: 18px;
                    color: $grey-light;
                }
            }
        }
    }

    .passcode {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        margin-top: 40%;

        width: 100%;
        gap: 1rem;

        .title {
            color: var(--phone-text-primary);
        }

        .subtitle {
            color: var(--phone-text-primary);
        }

        .lock {
            font-size: 70px;
            font-weight: 400;
            color: var(--phone-color-blue);
            margin-bottom: 1rem;
        }

        .input {
            position: absolute;
        }

        .highlight {
            margin-top: 1rem;
        }

        .buttons {
            width: 90%;
            margin-top: auto;
            background-color: var(--phone-color-highlight2);

            gap: 0.5rem;
            padding: 1rem;
            padding-bottom: 4rem;
            margin-bottom: 0.5rem;
        }
    }

    .highlight {
        font-size: 17px;
        font-weight: 400;
        color: var(--phone-color-blue);
        cursor: pointer;
    }

    .faceId {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        margin-top: 40%;

        width: 100%;
        gap: 1rem;

        .subtitle {
            color: var(--phone-text-primary);
        }

        img {
            width: 40%;
            heigth: 40%;
            margin-bottom: 1rem;
        }

        .button {
            display: flex;
            align-items: center;
            justify-content: center;

            width: 70%;
            height: 3rem;
            border-radius: 15px;

            background-color: var(--phone-color-blue);
            color: #ffffff;

            font-weight: 500;
            font-size: 18px;
            cursor: pointer;

            margin-top: 15rem;
        }
    }

    .appearance {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1rem;
        margin-top: 5rem;

        .title {
            font-size: 25px;
            color: var(--phone-text-primary);
        }

        .subtitle {
            width: 100%;
            font-size: 18px;

            color: var(--phone-text-primary);
        }

        .modes {
            margin-top: 2rem;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            gap: 2rem;
            padding-bottom: 7rem;

            font-size: 17px;

            .mode-text {
                color: var(--phone-text-primary);
            }

            .mode {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 1rem;

                img {
                    width: 8rem;
                }
            }
        }

        .button {
            display: flex;
            align-items: center;
            justify-content: center;

            width: 100%;
            height: 3rem;
            border-radius: 15px;

            background-color: var(--phone-color-blue);
            color: #ffffff;

            font-weight: 500;
            font-size: 18px;
            cursor: pointer;
        }
    }

    .title {
        font-size: 22px;
        font-weight: 600;
        color: var(--phone-text-primary);
    }

    .subtitle {
        width: 70%;
        text-align: center;
    }

    transition: all 2s ease-in-out;
}

.hide {
    opacity: 0;
}

.show {
    opacity: 1;
}

.slideIn {
    transform: translateX(100%);
    -webkit-transform: translateX(100%);

    animation: slide-in 0.3s forwards;
    -webkit-animation: slide-in 0.3s forwards;
}

@keyframes slide-in {
    100% {
        transform: translateX(0%);
    }
}
