.widget {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.75rem;

    cursor: pointer;

    // height: 10rem;
    height: 100%;
    // height: 15rem;

    .widgetname {
        font-size: 13px;
        font-weight: 500;
        text-align: center;
    }

    div {
        width: 90%;
        border-radius: 20px;
    }

    .appDelete {
        width: 1.5rem;
    }

    &.editMode {
        // animation-delay: -0.2s;
        animation-duration: 0.4s;

        &:nth-child(2n) {
            animation-name: widgetJiggle;
            animation-iteration-count: infinite;
            transform-origin: 50% 10%;
        }

        &:nth-child(2n-1) {
            animation-name: widgetJiggle2;
            animation-iteration-count: infinite;
            animation-direction: alternate;
            transform-origin: 30% 5%;
        }
    }
}

.half {
    grid-column: span 2;
    // grid-column: 1 / -3;
    grid-row: span 2;

    .weather {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;

        gap: 1rem;
        padding: 0.8rem;
        height: 100%;

        .weatherInfo {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;

            .temperature {
                font-size: 42.5px;
                font-weight: 300;
                color: rgba(255, 255, 255, 0.8);
            }
        }

        .weatherType {
            display: flex;
            flex-direction: column;
            gap: 0.25rem;
            width: 95%;

            font-size: 12px;
            font-weight: 600;
            color: rgba(255, 255, 255, 0.8);

            .weatherSymbol {
                display: flex;

                img {
                    width: 15px;
                    height: 15px;
                }
            }
        }
    }

    .music {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        gap: 1rem;
        padding: 0.8rem;
        height: 100%;

        .cover {
            height: 90px;
            border-radius: 15px;
        }

        .info {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            gap: 0.1rem;

            .title {
                font-size: 16px;
                font-style: bold;
                font-weight: 500;

                color: rgb(255, 255, 255);
            }
        }
    }
}

.full {
    grid-column: span 4;
    grid-row: span 2;
    height: 100%;

    .weather {
        display: flex;
        flex-direction: column;
        align-items: center;

        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;

        gap: 0.5rem;
        padding: 1.2rem;

        .weatherInfo {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;

            .temperature {
                font-size: 40px;
                font-weight: 300;
                color: rgba(255, 255, 255, 0.8);
            }

            .weatherSymbol {
                display: flex;
                justify-content: flex-end;
                align-items: flex-start;

                img {
                    width: 25px;
                    height: 25px;
                }
            }
        }

        .weatherContent {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            gap: 1rem;
            width: 100%;

            .item {
                display: flex;
                flex-direction: column;

                align-items: center;
                justify-content: center;
                gap: 0.5rem;

                text-align: center;

                color: #fff;

                img {
                    width: 20px;
                    height: 20px;
                }

                .time {
                    font-size: 13px;
                    font-weight: 400;

                    opacity: 0.7;
                    letter-spacing: 0.5px;

                    span {
                        font-size: 13px;
                    }
                }

                .temp {
                    font-size: 18px;
                    font-weight: 400;

                    opacity: 0.9;
                }
            }
        }
    }

    .music {
        display: flex;
        gap: 1rem;
        padding: 1.2rem;
        height: 100%;

        position: relative;

        .cover {
            height: 90px;
            border-radius: 15px;
        }

        .info {
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 0.1rem;

            .playing {
                font-size: 11px;

                font-weight: 500;
                color: rgba(255, 255, 255, 0.75);
            }

            .title {
                font-size: 16px;
                font-style: bold;
                font-weight: 500;

                color: rgb(255, 255, 255);
            }

            .artist {
                font-size: 13px;
                font-weight: 500;
                color: rgba(255, 255, 255, 0.75);
            }
        }

        .icon {
            position: absolute;
            z-index: 99;
            // right: 0;
            margin-left: 18.5rem;

            i {
                font-size: 20px;
                color: #ffffff;
                font-weight: 600;
            }
        }
    }
}
