@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

.news-container {
    position: relative;
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;

    background-color: #ffffff;


    .gta-w,
    .gta-s {
        color: #fff;
    }

    .gta-u {
        color: #000000;
    }

    .gta-r {
        color: #f33c3c;
    }

    .gta-g {
        color: #68b868;
    }

    .gta-b {
        color: #5db6e5;
    }

    .gta-y {
        color: #d9b549
    }

    .gta-o {
        color: #d16f48;
    }

    .gta-p {
        color: #8466e2;
    }

    .gta-h {
        font-weight: bold;
    }

    .news-header {
        width: 100%;
        margin-top: 0.25rem;
        padding-top: 4rem;
        padding-bottom: 1rem;

        display: flex;
        flex-direction: column;
        align-items: center;

        background-color: #b83030;

        .searchbox {
            width: 80%;
        }
    }

    .add {
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 1;

        position: absolute;
        bottom: 2rem;
        right: 2rem;

        height: 4.5rem;
        width: 4.5rem;
        border-radius: 50%;

        background-color: #ffd53e;
        color: #000000;

        font-size: 30px;

        cursor: pointer;
        transition: all 0.2s ease-in-out;

        &:hover {
            transform: scale(1.1);
        }
    }

    .news-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;

        width: 100%;
        height: 100%;
        position: relative;

        .posts {
            width: 90%;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;

            overflow-y: auto;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                display: none;
            }

            max-width: 100%;

            padding-bottom: 15rem;

            .post {
                display: flex;
                flex-direction: column;
                gap: 1rem;

                border: 3px solid rgba(200, 200, 200, 0.4);
                border-radius: 10px;
                padding: 1rem;
                width: 90%;

                .post-header {
                    color: #8e8e93;
                    font-size: 16px;
                    display: flex;
                    justify-content: space-between;
                }

                .post-content {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;

                    .info {
                        display: flex;
                        flex-direction: column;
                        gap: 0.5rem;

                        .description {
                            color: #000000;
                            font-size: 16px;
                            font-weight: 400;
                        }

                        max-width: 60%;
                    }

                    .image {
                        width: 7rem;
                        height: 7rem;

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;

                            border-radius: 6px;
                            cursor: pointer;
                        }
                    }
                }

                .post-footer {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    width: 100%;

                    .price {
                        margin-top: 1rem;

                        font-size: 24px;
                        color: #000000;
                        font-weight: 500;
                    }

                    .buttons {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 0.5rem;

                        .button {
                            height: 2.5rem;
                            padding: 0 1.75rem;
                            border-radius: 7px;

                            background-color: #ffd53e;

                            font-size: 18px;
                            font-weight: 500;

                            cursor: pointer;
                            transition: all 0.2s ease-in-out;

                            display: flex;
                            align-items: center;
                            justify-content: center;

                            &.red {
                                background-color: var(--phone-color-red);
                                color: #ffffff;
                                font-size: 25px;
                            }

                            &:hover {
                                filter: brightness(0.8);
                            }
                        }
                    }
                }
            }
        }
    }

    .new-post-container {
        position: absolute;
        bottom: 0;
        z-index: 1;

        width: 100%;
        height: 93%;

        background-color: #ffffff;

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.5rem;

        animation: slideUp 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        .new-post-header {
            display: flex;
            align-items: center;
            justify-content: space-between;

            width: 100%;
            padding: 1rem 0;

            text-align: center;

            background-color: #ffd53e;

            border-bottom: 1px solid rgba(200, 200, 200, 0.4);

            .cancel {
                color: var(--phone-color-blue);
                font-size: 18px;
                font-weight: 400;

                cursor: pointer;
            }

            .title {
                color: #000000;
                font-size: 18px;
                font-weight: 500;
            }

            div {
                flex: 1;
            }
        }

        .new-post-body {
            width: 100%;

            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;
            margin-right: 1rem;

            .item {
                display: flex;
                flex-direction: column;
                width: 80%;

                .title {
                    color: #000000;
                    font-size: 18px;
                    font-weight: 500;
                }

                .image {
                    width: 50%;
                    height: 9rem;

                    background-color: rgba(241, 241, 241, 0.656);
                    border: 3px dashed rgba(200, 200, 200, 0.4);
                    border-radius: 6px;

                    display: flex;
                    align-items: center;
                    justify-content: center;

                    align-self: center;
                    justify-self: center;

                    font-size: 45px;
                    color: #8e8e93;

                    cursor: pointer;

                    background-size: cover;
                    background-position: center;
                }

                input,
                textarea {
                    width: 100%;

                    padding: 0.75rem;
                    border: none;

                    font-family: Roboto;
                    font-size: 16px;

                    background-color: rgba(241, 241, 241, 0.656);

                    resize: none;
                    border-radius: 3px;

                    &::-webkit-inner-spin-button,
                    &::-webkit-outer-spin-button {
                        -webkit-appearance: none;
                        margin: 0;
                    }

                    &:focus {
                        outline: none;
                    }
                }
            }

            .button {
                margin-top: 4rem;

                padding: 0.7rem 1.75rem;
                border-radius: 7px;
                background-color: #ffd53e;

                font-size: 18px;
                font-weight: 500;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    filter: brightness(0.8);
                }
            }
        }
    }
}