@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

@mixin hideScrollbars {
    &::-webkit-scrollbar {
        display: none;
    }
}

.clock-container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: #000000;

    .clock-body {
        overflow: hidden;
        height: 90%;
        width: 100%;

        display: flex;
        justify-content: center;

        .picker {
            width: 100%;
            height: 6.5rem;

            position: relative;

            display: flex;
            flex-direction: row;
            gap: 1rem;
            justify-content: center;

            position: relative;
            font-weight: 400;
            margin-bottom: 1.5rem;

            &.darker {
                .selected {
                    background-color: rgba(80, 80, 80, 0.3);
                }

                &:before {
                    top: -2em;
                    background: linear-gradient(rgba(15, 15, 15, 1), rgba(15, 15, 15, 0.4));
                }
                &:after {
                    bottom: -2em;
                    height: 4em;
                    background: linear-gradient(rgba(15, 15, 15, 0.4), rgba(15, 15, 15, 1));
                }
            }

            .selected {
                position: absolute;
                top: 0;
                bottom: 0;
                margin: auto;

                width: 92%;
                height: 2.5rem;
                border-radius: 10px;

                background-color: rgba(80, 80, 80, 0.3);
            }

            div {
                display: flex;
                flex-direction: row;
                align-items: center;

                .label {
                    font-size: 20px;
                    color: #ffffff;
                    z-index: 9;
                }
            }

            ul {
                @include hideScrollbars();
                max-height: 10em;
                overflow-y: scroll;
                margin-right: 0.4rem;
                scroll-snap-type: y mandatory;

                list-style: none;
            }

            li {
                display: flex;
                align-items: center;
                justify-content: center;

                scroll-snap-align: center;
                height: 2rem;
                text-align: right;
                word-spacing: 0.2em;

                font-size: 22px;
                // color: #ffffff;
            }

            &:before,
            &:after {
                content: "";
                position: absolute;
                width: 100%;
                height: 4em;
                pointer-events: none;
            }
            &:before {
                top: -2em;
                background: linear-gradient(black, rgba(black, 0.4));
            }
            &:after {
                bottom: -2em;
                height: 4em;
                background: linear-gradient(rgba(black, 0.4), black);
            }
        }

        .actions {
            width: 88%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            margin-top: 5rem;

            .button {
                display: flex;
                align-items: center;
                justify-content: center;

                height: 6rem;
                width: 6rem;

                // background-color: green;
                border-radius: 50%;

                cursor: pointer;

                &.disabled {
                    cursor: not-allowed;
                }

                .button-inner {
                    height: 95%;
                    width: 95%;

                    display: flex;
                    align-items: center;
                    justify-content: center;

                    border-radius: 50%;
                    font-size: 20px;

                    transition: all 0.2s ease-in-out;

                    &:hover {
                        filter: brightness(0.8);
                    }
                }

                &:first-child {
                    border: 3px solid rgba(50, 50, 50, 0.9);
                    color: rgba(200, 200, 200, 0.8);

                    .button-inner {
                        background-color: rgba(50, 50, 50, 0.9);
                    }

                    &.disabled {
                        // cursor: not-allowed;

                        border: 3px solid rgba(30, 30, 30, 0.9);

                        .button-inner {
                            color: rgba(180, 180, 180, 0.8);
                            background-color: rgba(30, 30, 30, 0.9);
                        }
                    }
                }

                &:last-child {
                    border: 3px solid var(--phone-color-green-secondary);

                    .button-inner {
                        color: #26973d;
                        background-color: var(--phone-color-green-secondary);
                    }

                    &.active {
                        border: 3px solid rgba(121, 19, 19, 0.7);

                        .button-inner {
                            color: var(--phone-color-red);
                            background-color: rgba(121, 19, 19, 0.7);
                        }
                    }
                }
            }
        }

        .stopwatch-container {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;

            .clock {
                margin-top: 12rem;

                color: #ffffff;
                font-size: 80px;
                font-weight: 200;
            }

            .laps {
                display: flex;
                flex-direction: column;
                width: 100%;

                margin-top: 2rem;
                border-top: 1px solid rgba(60, 60, 60, 0.8);

                max-height: 17rem;
                overflow-y: auto;
                overflow-x: hidden;

                &::-webkit-scrollbar {
                    display: none;
                }

                .lap {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;

                    width: 95%;
                    padding: 1rem 1.25rem;

                    font-size: 20px;
                    font-weight: 300;

                    color: #ffffff;

                    border-bottom: 1px solid rgba(60, 60, 60, 0.8);

                    .time {
                        flex: 1;
                        text-align: right;

                        font-size: 20px;

                        margin-right: 1.5rem;
                    }
                }
            }
        }

        .timer-container {
            margin-top: 10rem;
            width: 100%;

            display: flex;
            flex-direction: column;
            align-items: center;

            color: rgba(255, 255, 255, 0.8);

            .timer-wrapper {
                position: absolute;
                top: 4rem;

                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                height: 19rem;
                width: 19rem;
                border-radius: 100%;

                .border-wrapper {
                    height: 100%;
                    width: 100%;

                    position: absolute;
                    z-index: 10;

                    transition: all 0.2s ease-in-out;
                }

                .timer {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    height: 95%;
                    width: 95%;

                    border-radius: 100%;
                    background-color: #000000;
                }

                .timer-text {
                    font-size: 65px;
                    font-weight: 300;
                }

                .when-done {
                    display: flex;
                    align-items: center;
                    gap: 0.3rem;

                    font-size: 16px;
                    color: rgba(180, 180, 180, 0.8);

                    svg {
                        font-size: 18px;
                    }
                }
            }

            .margin {
                margin-bottom: 8rem;
            }
        }
    }

    .clock-footer {
        position: absolute;
        bottom: 0;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 2.5rem;

        width: 100%;
        padding-top: 1rem;
        padding-bottom: 3rem;

        div {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 0.5rem;

            color: rgb(130, 130, 130);
            font-size: 15px;
            font-weight: 600;
            text-align: center;

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:hover {
                color: var(--phone-color-orange);
            }

            &.active {
                color: var(--phone-color-orange);
            }

            svg {
                font-size: 32px;
            }
        }
    }

    .alarm-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        .new-alarm {
            display: flex;
            flex-direction: column;
            align-items: center;

            position: absolute;
            bottom: 0;
            z-index: 1;

            width: 96%;
            height: 93%;
            border-top-left-radius: 25px;
            border-top-right-radius: 25px;

            background: rgb(15, 15, 15);

            animation: slideUp 0.5s cubic-bezier(0.19, 1, 0.22, 1);

            .new-alarm-header {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                width: 90%;
                margin-top: 1rem;

                .cancel,
                .save {
                    font-size: 18px;
                    color: var(--phone-color-orange);
                    cursor: pointer;
                }

                .title {
                    text-align: center;
                    font-size: 20px;
                    font-weight: 400;
                    color: #ffffff;
                }
            }

            .new-alarm-content {
                display: flex;
                flex-direction: column;
                align-items: center;

                margin-top: 4rem;
                width: 100%;
                color: rgba(255, 255, 255, 0.8);

                .settings {
                    margin-top: 1rem;
                    width: 90%;

                    border-radius: 8px;

                    background-color: rgba(80, 80, 80, 0.3);

                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    // gap: 1rem;

                    .item {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: space-between;

                        width: 90%;
                        padding: 1rem 1.25rem;

                        border-bottom: 1px solid rgba(60, 60, 60, 0.8);

                        &:last-child {
                            border-bottom: none;
                        }

                        .label {
                            font-size: 18px;
                            font-weight: 300;
                            color: #ffffff;
                        }

                        input {
                            height: 100%;
                            text-align: right;

                            font-size: 18px;
                            background-color: transparent;
                            border: none;

                            color: #87878a;
                            font-family: Roboto;

                            &:focus {
                                outline: none;
                            }
                        }
                    }
                }
            }
        }

        .alarm-header {
            width: 88%;
            display: flex;
            flex-direction: column;
            margin-top: 4rem;
            gap: 1.25rem;

            .alarm-header-top {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                color: var(--phone-color-orange);

                cursor: pointer;

                .edit {
                    font-size: 20px;
                }

                svg {
                    font-size: 32px;
                }
            }

            .alarm-title {
                color: #ffffff;
                font-size: 28px;
                font-weight: 500;
            }
        }

        .alarm-content {
            display: flex;
            flex-direction: column;
            // align-items: center;
            width: 100%;

            .alarm {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                width: 88%;
                padding: 1rem 1.5rem;

                &.active {
                    .alarm-info {
                        color: #ffffff;
                    }
                }

                .alarm-wrapper {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 1rem;

                    svg {
                        font-size: 27px;
                        color: var(--phone-color-red);
                    }
                }

                .alarm-info {
                    display: flex;
                    flex-direction: column;
                    color: #767679;

                    .time {
                        font-size: 50px;
                        font-weight: 300;
                    }
                }

                svg {
                    font-size: 20px;
                    color: #3d3d3d;

                    cursor: pointer;
                }
            }
        }
    }

    .worldclock-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        .worldclock-header {
            width: 88%;
            display: flex;
            margin-top: 4rem;

            .worldclock-title {
                color: #ffffff;
                font-size: 28px;
                font-weight: 500;
            }
        }

        .worldclock-content {
            display: flex;
            flex-direction: column;
            width: 100%;
            max-height: 83%;
            overflow-y: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            .worldclock {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                width: 88%;
                padding: 1rem 1.5rem;

                .worldclock-location {
                    display: flex;
                    flex-direction: column;
                    gap: 0.25rem;

                    color: #767679;
                    font-weight: 500;
                    font-size: 16px;

                    .location {
                        font-size: 32px;
                        font-weight: 300;
                        color: #ffffff;
                    }
                }

                .time {
                    font-size: 40px;
                    font-weight: 300;
                    color: #ffffff;
                }
            }
        }
    }
}
