@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

.phone-app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 100%;

    background-color: var(--phone-color-primary);

    .wrapper {
        width: 100%;
        height: 88%;
        display: flex;
        flex-direction: column;

        .slide {
            height: 100%;
            width: 100%;
            display: flex;
            flex-direction: column;

            &.right {
                animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
            }

            &.left {
                animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
            }

            &.up {
                animation: slideUp 0.5s cubic-bezier(0.19, 1, 0.22, 1);
            }
        }

        .contacts-header {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;

            padding: 0 5%;

            margin-top: 4.5rem;

            .disabled {
                color: var(--phone-text-secondary);
            }

            span {
                cursor: pointer;
                color: var(--phone-color-blue);
                font-size: 18px;

                &:hover{
                    filter: brightness(90%);
                }

                svg {
                    font-size: 15px;
                }

                &.disabled {
                    color: var(--phone-text-secondary);
                }
            }

            .back {
                color: var(--phone-color-blue);

                display: flex;
                align-items: center;
                gap: 0.35rem;

                font-size: 18px;

                cursor: pointer;

                &:hover{
                    filter: brightness(90%)
                }
            }

            .items {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                width: 100%;

                .title {
                    color: var(--phone-text-primary);
                    font-size: 30px;
                    font-weight: 600;
                    font-style: bold;
                }

                svg {
                    font-size: 35px;

                    color: var(--phone-color-blue);

                    cursor: pointer;
                    &:hover {
                        filter: brightness(90%);
                    }
                }
            }
        }

        .content {
            max-height: 100%;
            overflow: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            .keypad-container {
                .inputbox {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 0.5rem;

                    margin-top: 9rem;

                    .input {
                        text-align: center;
                        height: 4rem;
                        font-size: 40px;

                        color: var(--phone-text-primary);
                    }

                    span {
                        font-size: 20px;
                        color: var(--phone-color-blue);
                        cursor: pointer;

                        // set the height to the character height
                        height: 1.5rem;
                    }
                }

                .keypad-wrapper {
                    margin-top: 2rem;

                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    .keypad {
                        padding: 2rem;

                        display: grid;
                        align-items: center;

                        grid-template-columns: repeat(3, 1fr);
                        -ms-grid-columns: repeat(3, 1fr);

                        gap: 1rem 2rem;

                        div:not(.delete) {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            justify-content: center;

                            background-color: var(--phone-highlight-opacity35);

                            color: var(--phone-text-primary);

                            border-radius: 50%;
                            height: 5rem;
                            width: 5rem;

                            text-align: center;
                            font-size: 32px;
                            font-family: "Roboto";
                            font-weight: 400;

                            &:hover {
                                cursor: pointer;
                                background-color: var(--phone-highlight-opacity55);
                            }

                            span {
                                font-size: 12px;
                                font-weight: 400;
                                letter-spacing: 3px;
                            }

                            &.call {
                                color: $white-primary;
                                background-color: var(--phone-color-green);

                                svg {
                                    font-size: 33px;
                                }

                                &:hover {
                                    filter: brightness(0.9);
                                }
                            }
                        }

                        .delete {
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            svg {
                                font-size: 40px;

                                color: var(--phone-text-primary);

                                cursor: pointer;
                                &:hover {
                                    filter: brightness(0.7);
                                }
                            }
                        }
                    }
                }
            }

            .favourite {
                display: flex;
                flex-direction: column;

                .item {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    padding: 0.7rem 5%;

                    cursor: pointer;
                    transition: all 0.1s ease-in-out;

                    color: var(--phone-text-primary);

                    .user {
                        display: flex;
                        align-items: center;
                        gap: 0.5rem;
                        transition: all 0.3s ease-in-out;

                        svg {
                            font-size: 28px;
                            color: var(--phone-color-red);
                            font-weight: 600;
                            margin-right: 0.25rem;
                        }

                        .avatar {
                            display: flex;

                            width: 3rem;
                            height: 3rem;
                            border-radius: 50%;

                            justify-content: center;
                            align-items: center;
                            font-size: 18px;
                            font-weight: 400;

                            background-position: center;
                            background-size: cover;
                            background-repeat: no-repeat;

                            background-color: #bbbbbd;
                            color: $white-primary;
                        }

                        .info {
                            display: flex;
                            flex-direction: column;
                            font-size: 18px;
                            font-weight: 400;

                            span {
                                color: $grey-light;
                                font-size: 14px;
                                font-weight: 400;
                            }
                        }
                    }

                    svg {
                        margin-right: 0.1rem;

                        font-size: 28px;

                        font-weight: 300;
                        color: var(--phone-color-blue);

                        &:hover {
                            filter: brightness(0.7);
                        }
                    }
                }
            }

            .recent-calls {
                display: flex;
                flex-direction: column;

                .item {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    padding: 0.7rem 5%;

                    cursor: pointer;
                    transition: all 0.1s ease-in-out;

                    border-top: 1px solid var(--phone-color-border);

                    &.missed {
                        .user {
                            color: var(--phone-color-red);
                        }
                    }

                    .user {
                        display: flex;
                        flex-direction: column;
                        font-size: 18px;
                        font-weight: 400;

                        color: var(--phone-text-primary);

                        span {
                            color: var(--phone-text-secondary);
                            font-size: 14px;
                            font-weight: 400;
                        }
                    }

                    .info {
                        display: flex;
                        align-items: center;
                        gap: 0.5rem;

                        padding-right: 0.1rem;

                        font-size: 14px;
                        font-weight: 400;
                        color: $grey-light;

                        svg {
                            font-size: 28px;

                            font-weight: 300;
                            color: var(--phone-color-blue);

                            &:hover {
                                filter: brightness(0.7);
                            }
                        }
                    }
                }
            }

            .letters {
                position: absolute;
                height: 100%;

                display: flex;
                flex-direction: column;
                align-items: center;

                right: 0;
                top: 28%;
                z-index: 10;

                margin-right: 15px;

                .letter {
                    width: 20px;
                    text-align: center;
                    color: var(--phone-color-blue);
                    font-size: 14px;
                    cursor: pointer;

                    &:active {
                        color: var(--phone-color-blue);
                    }
                }
            }

            .divider {
                margin-bottom: 0.25rem;

                font-size: 15px;
                font-weight: 500;

                padding: 0 5%;
                padding-top: 1.5rem;

                border-top: 1px solid var(--phone-color-border);
                color: var(--phone-text-secondary);
            }

            .no-border {
                border: none;
                padding-top: 0;
            }

            .contact {
                cursor: pointer;
                transition: all 0.1s ease-in-out;

                padding: 0.7rem 5%;

                font-size: 18px;
                font-weight: 400;

                color: var(--phone-text-primary);

                &:hover:not(.nohover) {
                    background-color: var(--phone-color-hover);
                }

                &.details {
                    cursor: auto;
                }

                &.border {
                    border-top: 1px solid var(--phone-color-border);

                    &:last-child {
                        border-bottom: 1px solid var(--phone-color-border);
                    }
                }

                .user {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 0.5rem;

                    .name {
                        font-size: 27px;
                        font-weight: 400;
                        color: var(--phone-text-primary);
                    }

                    span {
                        cursor: pointer;
                        color: var(--phone-color-blue);
                        font-size: 18px;
                        font-weight: 400;

                        &:hover{
                            filter: brightness(90%)
                        }

                        &.add {
                            margin-top: -1rem;
                        }
                    }

                    svg {
                        color: var(--phone-text-secondary);
                        font-size: 150px;

                        cursor: pointer;
                        padding: 0;
                        margin: 0;

                        &.big {
                            font-size: 200px;
                        }
                    }
                }

                .actions {
                    margin-top: 1.5rem;

                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 1rem;

                    .item {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;

                        gap: 0.5rem;

                        flex: 1;
                        padding: 0.5rem;

                        border-radius: 10px;

                        background-color: var(--phone-color-highlight);
                        color: var(--phone-color-blue);

                        font-size: 14px;
                        font-weight: 400;

                        svg {
                            font-size: 28px;
                        }

                        i {
                            font-weight: 600;
                            font-size: 16px;
                        }

                        cursor: pointer;
                        transition: all 0.1s ease-in-out;
                        &:hover {
                            filter: brightness(0.9);
                        }
                    }

                    [data-disabled="true"] {
                        filter: brightness(0.9);

                        pointer-events: none;
                        cursor: not-allowed;
                    }

                    // &.disabled {
                    //     .item {
                    //         filter: brightness(0.9);

                    //         pointer-events: none;
                    //         cursor: not-allowed;
                    //     }
                    // }
                }

                .contact-info {
                    margin-top: 1.5rem;

                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    border-radius: 7.5px;
                    background-color: var(--phone-color-highlight);

                    .item {
                        display: flex;
                        flex-direction: column;
                        gap: 0.25rem;
                        width: 90%;

                        padding: 0.75rem;
                        border-top: 1px solid var(--phone-color-border);

                        font-size: 17px;
                        color: var(--phone-color-blue);
                        font-weight: 400;

                        &.red {
                            color: var(--phone-color-red);
                        }

                        .title {
                            font-size: 14px;
                            color: var(--phone-text-primary);
                        }

                        .value {
                            font-size: 18px;
                            color: var(--phone-color-blue);
                            user-select: all;
                        }

                        &:first-child {
                            border-top: none;
                        }

                        input {
                            width: 100%;
                            height: 100%;
                            border: none;

                            font-size: 18px;
                            font-weight: 400;

                            border: none;
                            background-color: transparent;

                            background-color: var(--phone-color-highlight);

                            color: var(--phone-text-primary);

                            &:focus,
                            &:active {
                                border: none;
                                outline: none;
                            }

                            &.phone_number {
                                color: var(--phone-color-blue);
                                -webkit-appearance: none;
                            }

                            &.phone_number::-webkit-inner-spin-button,
                            &.phone_number::-webkit-outer-spin-button {
                                -webkit-appearance: none;
                                margin: 0;
                            }
                        }

                        cursor: pointer;
                        transition: all 0.1s ease-in-out;

                        &:hover:not(.input) {
                            filter: brightness(0.9);
                        }
                    }
                }

                .profile {
                    width: 75%;
                    display: flex;
                    flex-direction: row;
                    align-items: center;

                    padding: 0.75rem 0;
                    border-radius: 10px;
                    gap: 0.75rem;

                    .profile-info {
                        display: flex;
                        flex-direction: column;
                        color: var(--phone-text-primary);

                        .name {
                            font-size: 20px;
                            font-weight: 500;
                        }

                        .info {
                            font-size: 14px;
                            font-weight: 400;
                        }
                    }

                    i {
                        margin-left: auto;
                        margin-right: 0.5rem;
                    }
                }
            }

            .total-contacts {
                padding-top: 1rem;
                padding-bottom: 2rem;

                font-size: 17px;
                font-weight: 600;
                text-align: center;
            }
        }
    }

    .profile-image {
        display: flex;

        width: 4rem;
        height: 4rem;
        border-radius: 50%;

        justify-content: center;
        align-items: center;
        font-size: 23px;
        font-weight: 400;

        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        // background-color: #bbbbbd;
        background-color: var(--phone-text-secondary);

        color: $white-primary;

        &.big {
            width: 5rem;
            height: 5rem;
            font-size: 22px;
        }

        &.bigger {
            width: 9rem;
            height: 9rem;
            font-size: 50px;
        }

        &.custom {
            background-color: transparent;
        }
    }

    .footer {
        height: 11%;
        width: 100%;

        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 2.5rem;

        border-top: 1px solid var(--phone-color-border);

        .item {
            padding-top: 0.4rem;

            display: flex;
            flex-direction: column;
            align-items: center;

            cursor: pointer;
            transition: all 0.1s ease-in-out;

            gap: 0.2rem;

            color: var(--phone-text-secondary);

            &.active {
                color: var(--phone-color-blue);
            }

            &:hover {
                color: var(--phone-color-blue);
                filter: brightness(0.8);
            }

            svg {
                font-size: 30px;
            }

            span {
                font-size: 14px;
            }
        }
    }
}
