@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

.photos-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    height: 100%;


    background-color: var(--phone-color-primary);

    .zoomOut {
        margin-top: 1rem;
        width: 100%;
        height: 100%;

        position: relative;

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;

        animation: zoomOut 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    }

    .title {
        font-size: 22px;
        font-weight: 600;
        font-style: bold;

        margin-right: auto;
        margin-left: 3rem;

        color: var(--phone-text-primary);
    }

    .photos-header {
        margin-top: 15%;
        width: 88%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        .select {
            cursor: pointer;

            color: var(--phone-color-blue);
            font-size: 17px;

            text-align: right;

            &:hover{
                filter: brightness(0.90);
            }
        }

        .title {
            text-align: center;
            width: 60%;

            font-size: 20px;
            font-weight: 700;
            margin: 0;

            background-color: transparent;
            border: none;

            &:active,
            &:focus {
                outline: none;
            }
        }

        div {
            flex: 1 1 0px;
        }
    }

    .photos-content {
        max-height: 42rem;
        width: 100%;

        overflow-y: scroll;
        &::-webkit-scrollbar {
            display: none;
        }

        .images {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 0.15rem;

            .image {
                position: relative;
                width: 100%;
                height: 100%;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    filter: brightness(0.8);
                }

                .img,
                video {
                    width: 6.28rem;
                    height: 6rem;

                    cursor: pointer;

                    background-size: cover;
                    background-position: center;

                    object-fit: cover;

                    filter: brightness(0.9);
                }

                .video-duration {
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    margin: 0.5rem;

                    font-size: 16px;
                    color: #ffffff;
                    font-weight: 500;
                }

                .checkbox {
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    margin: 0.5rem;
                }
            }
        }

        .info {
            display: flex;
            justify-content: center;
            align-items: center;

            margin: 1.5rem 0;
            font-size: 15px;
            font-weight: 400;

            color: var(--phone-text-primary);
        }
    }

    .photos-bottom {
        width: 80%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        padding: 1rem 0;
        padding-bottom: 3rem;

        position: absolute;
        bottom: 0;

        .selected {
            font-size: 15px;
            font-weight: 500;
            text-align: center;
            margin-left: 1.5rem;

            color: var(--phone-text-primary);
        }

        svg {
            cursor: pointer;
            color: var(--phone-color-blue);
            font-size: 28px;
            text-align: right;
        }

        transition: 0.5s;
    }

    .activephoto-container {
        display: flex;
        flex-direction: column;
        align-items: center;

        position: relative;

        height: 100%;
        width: 100%;

        animation: zoomIn 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        .photo-top {
            position: absolute;
            top: 0;

            width: 95%;
            padding: 0 1rem;
            padding-bottom: 1rem;
            padding-top: 15%;

            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            background-color: var(--phone-color-primary);

            .date {
                display: flex;
                flex-direction: column;
                text-align: center;
                gap: 0.25rem;

                font-size: 19px;
                font-weight: 400;

                color: var(--phone-text-primary);

                span {
                    font-size: 14px;
                    font-weight: 400;
                }
            }

            div {
                flex: 1 1 0px;
            }

            svg {
                cursor: pointer;

                color: var(--phone-color-blue);
                font-size: 30px;
                text-align: right;
            }
        }

        .image-overflow {
            display: flex;
            flex-direction: row;
            gap: 0;

            width: 100%;
            height: 100%;

            overflow-y: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            img,
            video {
                width: 100%;

                min-width: 100%;
                max-width: 100%;

                object-fit: contain;
                pointer-events: none;
            }
        }

        .photo-bottom-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: var(--phone-color-primary);
            position: absolute;
            bottom: 0;

            width: 100%;

            .photo-carousel {
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;

                overflow-x: auto;
                gap: 0.1rem;
                padding-top: 1rem;

                width: 100%;

                background-color: var(--phone-color-primary);

                img,
                video {
                    object-fit: cover;
                    height: 4rem;
                    width: 2rem;

                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        filter: brightness(0.8);
                    }
                }
            }

            .photo-bottom {
                // position: absolute;

                width: 80%;
                padding: 1.5rem 2.5rem;

                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;

                svg {
                    cursor: pointer;

                    color: var(--phone-color-blue);
                    font-size: 28px;
                }
            }
        }
    }
}
