@import "styles/Colors.scss";

@keyframes fadeIn {
    0% {
        transform: translateY(-30%);
        border-radius: 25px;
    }

    100% {
        transform: translateY(0);
    }
}

.controlSlider {
    position: absolute;

    width: 200%;
    height: 50%;
    background-color: #ffffff;

    border-radius: 20px;

    -webkit-appearance: none;
    transform: rotate(-90deg);

    &:focus {
        outline: none;
    }
}

.controlSlider::-webkit-slider-thumb {
    -webkit-appearance: none;

    margin: 0 1rem;
    height: 1.75rem;
    width: 1.75rem;
    border-radius: 50%;
    background: transparent;
}

.controlSlider::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
}

.control-centre-container {
    backdrop-filter: blur(15px);

    position: absolute;
    z-index: 97;
    height: 56rem;
    width: 100%;

    border-radius: 25px;
}

.control-centre {
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: rgba(0, 0, 0, 0.7);

    border-radius: 25px;

    .control-centre-body {
        margin-top: 6rem;

        width: 80%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 1rem;

        .col {
            width: 100%;
            height: 4.5rem;

            background-color: #00000073;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 20px;

            &.icon {
                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    background-color: rgba(255, 255, 255, 0.2);
                }

                &.active {
                    background-color: #ffffff;

                    svg {
                        color: var(--phone-color-blue);
                    }
                }

                svg {
                    font-size: 40px;
                    color: rgba(255, 255, 255, 0.9);
                }
            }

            &.col-4 {
                grid-column: span 2;
                grid-row: span 2;
                height: 10rem;
                width: 100%;

                display: flex;
                align-items: center;
                justify-content: center;
            }

            .group {
                width: 90%;
                height: 90%;

                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-template-rows: repeat(2, 1fr);
                align-items: center;

                .item {
                    justify-self: center;

                    width: 3.8rem;
                    height: 3.8rem;

                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    background-color: rgba(20, 20, 20, 0.5);
                    border-radius: 50%;

                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    svg {
                        font-size: 28px;
                        color: rgba(255, 255, 255, 0.9);
                    }

                    &:hover:not(.disabled) {
                        filter: brightness(0.6);
                    }

                    &.disabled {
                        cursor: not-allowed;
                        filter: brightness(0.5);
                    }

                    &.orange {
                        background-color: var(--phone-color-orange);
                    }

                    &.purple {
                        background-color: #5958c0;
                    }
                }
            }

            .music-player {
                width: 90%;
                height: 90%;

                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                gap: 1rem;

                .title {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    .song {
                        color: #ffffff;
                        font-size: 18px;
                        font-weight: 500;
                    }

                    .artist {
                        color: #cccccc;
                        font-size: 16px;
                    }
                }

                .controls {
                    width: 80%;

                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    svg {
                        color: #ffffff;
                        font-size: 28px;

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            filter: brightness(0.7);
                        }
                    }

                    div {
                        svg {
                            transform: scale(1.1);
                            stroke-width: 20;
                        }
                    }
                }
            }
        }

        .slider {
            grid-row: span 2;

            width: 100%;
            height: 100%;

            display: flex;
            align-items: center;
            justify-content: center;

            position: relative;
            cursor: pointer;

            svg {
                position: absolute;
                bottom: 1.5rem;

                font-size: 35px;
                color: rgba(20, 20, 20, 0.5);
                cursor: pointer;

                pointer-events: none;
            }
        }
    }
}
