.indicator-container {
    position: absolute;

    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 10rem;
    height: 10px;

    z-index: 999;

    display: flex;
    align-items: center;
    justify-content: center;

    // margin-bottom: 1rem;
    padding: 1rem 0;
    padding-bottom: 0.5rem;

    &:hover {
        cursor: pointer;
        transition: 0.5s;

        margin-bottom: 0.1rem;
    }

    .home-indicator {
        width: 9rem;
        height: 5px;
        border-radius: 4px;
    }
}
