@import "../../../../styles/Colors.scss";

.home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    width: 100%;
    height: 100%;

    background-position: center;
    background-size: cover;
    position: relative;

    .blur-overlay {
        position: absolute;

        width: 100%;
        height: 100%;

        backdrop-filter: blur(15px) brightness(0.75);
    }

    .home-header {
        width: 85%;

        margin-top: 4rem;

        color: #ffffff;
        font-size: 28px;
        font-weight: 500;

        text-align: left;
        z-index: 1;

        display: flex;
        align-items: center;
        justify-content: space-between;

        .close {
            cursor: pointer;
            font-size: 30px;

            color: var(--phone-color-hover);
        }

        .title {
            display: flex;
            align-items: center;
            gap: 0.5rem;
        }
    }

    .home-wrapper {
        width: 85%;
        display: flex;
        flex-direction: column;
        z-index: 1;

        .title {
            font-size: 18px;
            color: #ffffff;
            font-weight: 500;

            margin-bottom: 0.75rem;
            margin-top: 1.5rem;

            display: flex;
            align-items: center;
            gap: 0.2rem;

            svg {
                font-size: 22px;
                opacity: 0.5;
            }
        }

        .category {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 1rem;

            &.scroll {
                overflow-y: auto;
                overflow-x: none;
                max-height: 100%;

                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }

        .items {
            display: flex;
            flex-direction: column;
            gap: 1rem;
        }

        .item {
            width: 100%;
            height: 11rem;

            background-color: var(--phone-highlight-opacity45);

            backdrop-filter: blur(30px);

            border-radius: 20px;

            display: flex;
            align-items: flex-end;

            cursor: pointer;
            transition: all 0.3s ease-in-out;

            &.small {
                display: flex;
                align-items: center;
                height: 5rem;

                .icon {
                    margin-left: 0.75rem;
                    position: inherit;
                }

                &:hover {
                    filter: brightness(1.2);
                }
            }

            &.full {
                // make it take up 2 columns
                grid-row: span 2;
            }

            &.active {
                background-color: #ffffff;
                filter: brightness(0.9);

                .icon {
                    background-color: #dddddd;
                    opacity: 0.8;

                    &.blue {
                        background-color: #66e4e5;
                        color: #ffffff;
                    }

                    &.yellow {
                        background-color: var(--phone-color-yellow);
                        color: #ffffff;
                    }
                }

                .info {
                    .title {
                        color: #000000;
                        opacity: 1;
                    }

                    .value {
                        color: #000000;
                        opacity: 0.6;
                    }
                }
            }

            .icon {
                position: absolute;
                left: 0.75rem;
                top: 0.75rem;

                width: 3rem;
                height: 3rem;
                border-radius: 50%;

                background-color: var(--phone-highlight-opacity45);

                display: flex;
                justify-content: center;
                align-items: center;

                opacity: 1;

                color: #ffffff;

                &.blue {
                    color: #66e4e5;
                }

                &.yellow {
                    color: var(--phone-color-yellow);
                }

                svg {
                    font-size: 28px;
                }
            }

            .info {
                display: flex;
                flex-direction: column;
                padding: 0.75rem;

                .title {
                    color: #fafafa;
                    opacity: 0.9;
                    font-size: 18px;
                    font-weight: 500;

                    margin: 0;
                }

                .value {
                    color: #ffffff;
                    opacity: 0.4;

                    font-size: 16px;
                    font-weight: 400;
                }
            }

            &:hover:not(.active, .small) {
                background-color: #ffffff;

                .icon {
                    background-color: #dddddd;
                    opacity: 0.8;
                }

                .info {
                    .title {
                        color: #000000;
                        opacity: 1;
                    }

                    .value {
                        color: #000000;
                        opacity: 0.6;
                    }
                }
            }
        }
    }
}
