.music-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;

    background-color: var(--phone-color-primary);

    .slide {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;

        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .music-header {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 4.5rem;

        .search {
            width: 90%;

            display: flex;
            flex-direction: column;
            gap: 0.5rem;
        }

        .title {
            font-size: 30px;
            font-weight: 600;
            color: var(--phone-text-primary);
        }

        .left {
            width: 90%;
            display: flex;
        }

        .space {
            width: 90%;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        svg {
            font-size: 35px;
            color: var(--phone-color-pink);
            cursor: pointer;
        }
    }

    .music-body {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;

        .library {
            display: flex;
            flex-direction: column;
            align-items: center;

            width: 100%;
            max-height: 55rem;

            overflow-y: scroll;
            overflow-x: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            .playlist {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                width: 100%;
                padding: 1rem 0;

                border-bottom: 1px solid var(--phone-color-border);

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    background-color: var(--phone-color-hover);
                }

                .playlist-details {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 1rem;

                    margin: 0 1rem;

                    img {
                        height: 7.5rem;
                        width: 7.5rem;
                        object-fit: cover;
                        border-radius: 5px;
                    }

                    .playlist-cover {
                        height: 7.5rem;
                        width: 7.5rem;
                        border-radius: 5px;

                        &[data-grid="true"] {
                            display: grid;
                            grid-template-columns: repeat(2, 1fr);
                            grid-template-rows: repeat(2, 1fr);

                            img {
                                border-radius: 0;

                                &:nth-child(1) {
                                    border-top-left-radius: 5px;
                                }

                                &:nth-child(2) {
                                    border-top-right-radius: 5px;
                                }

                                &:nth-child(3) {
                                    border-bottom-left-radius: 5px;
                                }

                                &:nth-child(4) {
                                    border-bottom-right-radius: 5px;
                                }
                            }
                        }

                        img {
                            width: 100%;
                            height: 100%;
                            border-radius: 5px;
                        }
                    }

                    .text {
                        .title {
                            font-size: 18px;
                            color: var(--phone-text-primary);
                        }

                        .subtitle {
                            font-size: 15px;
                            color: var(--phone-text-secondary);
                        }
                    }
                }

                svg {
                    font-size: 20px;
                    color: var(--phone-text-secondary);
                    cursor: pointer;

                    margin-right: 1.75rem;
                }
            }
        }

        .search-results {
            width: 100%;
            max-height: 38rem;
            display: flex;
            flex-direction: column;
            // gap: 1rem;

            margin-top: 2rem;

            overflow-y: auto;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                display: none;
            }

            .item {
                width: 90%;
                display: flex;
                align-items: center;
                justify-content: space-between;

                padding: 0.5rem 5%;
                border-bottom: 1px solid var(--phone-color-border);

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    background-color: var(--phone-color-hover);
                }

                .item-info {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;

                    img {
                        height: 4rem;
                        width: 4rem;
                        border-radius: 5px;
                        object-fit: cover;

                        &[data-artist="true"] {
                            border-radius: 50%;
                        }
                    }

                    .text {
                        font-weight: 400;

                        .title {
                            color: var(--phone-text-primary);
                            font-size: 17px;
                        }

                        .subtitle {
                            color: var(--phone-text-secondary);
                            font-size: 15px;
                        }
                    }
                }

                svg {
                    color: var(--phone-text-primary);
                    font-size: 20px;
                }

                &[data-playing="true"] {
                    .text {
                        .title {
                            color: var(--phone-color-pink);
                        }

                        .subtitle {
                            color: var(--phone-color-pink);
                            opacity: 0.75;
                        }
                    }

                    svg {
                        color: var(--phone-color-pink);
                    }
                }
            }
        }

        .artist-profile {
            max-height: 55rem;
            overflow-y: auto;
            overflow-x: hidden;

            width: 100%;

            &::-webkit-scrollbar {
                display: none;
            }

            .avatar {
                width: 100%;
                height: 15rem !important;

                background-size: cover;
                background-position: center;

                display: flex;
                flex-direction: column;
                justify-content: space-between;
                filter: brightness(0.8);

                .top {
                    width: 100%;

                    margin-top: 2.5rem;

                    svg {
                        padding: 0.25rem;
                        margin: 0 1rem;

                        border-radius: 50%;

                        color: var(--phone-text-primary);
                        background-color: var(--phone-color-opacity);

                        font-size: 25px;

                        cursor: pointer;
                    }
                }

                .title {
                    margin-left: 1rem;
                    margin-bottom: 0.5rem;

                    font-size: 35px;
                    font-weight: 500;
                    color: var(--phone-text-primary);
                }
            }

            .category {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.3rem;
                width: 100%;

                margin-top: 2.5rem;

                &:last-child {
                    margin-bottom: 15rem;
                }

                .title {
                    width: 90%;
                    text-align: left;

                    display: flex;
                    align-items: center;
                    gap: 0.2rem;

                    font-size: 20px;
                    font-weight: 700px;
                    color: var(--phone-text-primary);

                    svg {
                        color: var(--phone-text-secondary);
                        font-size: 20px;
                    }
                }

                section {
                    display: flex;
                    flex-direction: column;
                    width: 100%;
                    // gap: 0.5rem;

                    &.albums {
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        gap: 1.5rem 1rem;
                        width: 90%;

                        .album {
                            display: flex;
                            flex-direction: column;
                            gap: 0.5rem;

                            width: 100%;
                            height: 100%;

                            cursor: pointer;
                            transition: all 0.2s ease-in-out;

                            &:hover {
                                filter: brightness(0.5);
                            }

                            img {
                                height: 100%;
                                width: 100%;

                                object-fit: fill;

                                border-radius: 10px;
                            }

                            .title {
                                font-size: 18px;
                                font-weight: 500;
                                color: var(--phone-text-primary);
                            }
                        }
                    }

                    .song {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: space-between;

                        padding: 0.5rem 5%;
                        border-bottom: 1px solid var(--phone-color-border);

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            background-color: var(--phone-color-hover);
                        }

                        .song-info {
                            display: flex;
                            align-items: center;
                            gap: 0.5rem;

                            img {
                                height: 4rem;
                                width: 4rem;
                                border-radius: 5px;
                                object-fit: cover;
                            }

                            .text {
                                .title {
                                    font-size: 17px;
                                    font-weight: 500;
                                    color: var(--phone-text-primary);

                                    width: inherit;
                                }

                                .subtitle {
                                    font-size: 15px;
                                    font-weight: 400;
                                    color: var(--phone-text-secondary);
                                }
                            }
                        }

                        &[data-playing="true"] {
                            .text {
                                .title {
                                    color: var(--phone-color-pink);
                                }

                                .subtitle {
                                    color: var(--phone-color-pink);
                                    opacity: 0.75;
                                }
                            }

                            svg {
                                color: var(--phone-color-pink);
                            }
                        }

                        svg {
                            color: var(--phone-text-primary);
                            font-size: 20px;
                        }
                    }
                }
            }
        }

        .playlist-wrapper {
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 2rem;
            margin-top: 2rem;

            overflow-y: auto;
            overflow-x: hidden;
            max-height: 55rem;

            &::-webkit-scrollbar {
                display: none;
            }

            .playlist-details {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 1rem;

                width: 100%;
                height: 100%;

                img {
                    height: 17rem;
                    width: 17rem;

                    border-radius: 12px;

                    box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.25);
                }

                .playlist-cover {
                    height: 17rem;
                    width: 17rem;
                    border-radius: 5px;

                    box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.25);

                    &[data-grid="true"] {
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        grid-template-rows: repeat(2, 1fr);

                        img {
                            border-radius: 0;

                            &:nth-child(1) {
                                border-top-left-radius: 5px;
                            }

                            &:nth-child(2) {
                                border-top-right-radius: 5px;
                            }

                            &:nth-child(3) {
                                border-bottom-left-radius: 5px;
                            }

                            &:nth-child(4) {
                                border-bottom-right-radius: 5px;
                            }
                        }
                    }

                    img {
                        width: 100%;
                        height: 100%;
                        border-radius: 5px;
                        box-shadow: none;
                    }
                }

                .text {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 0.25rem;

                    .title {
                        font-size: 20px;
                        font-weight: 500;
                        color: var(--phone-text-primary);
                    }

                    .subtitle {
                        font-size: 18px;
                        font-weight: 400;
                        color: var(--phone-color-pink);
                    }
                }

                .buttons {
                    width: 90%;

                    display: flex;
                    align-items: center;
                    gap: 0.5rem;

                    .button {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        gap: 0.25rem;

                        width: 100%;
                        height: 3rem;
                        border-radius: 8px;

                        background-color: var(--phone-color-highlight2);
                        color: var(--phone-color-pink);
                        font-size: 16px;
                        font-weight: 500;

                        svg {
                            font-size: 25px;
                        }

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            filter: brightness(0.7);
                        }
                    }
                }
            }

            .playlist-tracks {
                display: flex;
                flex-direction: column;

                width: 100%;
                height: 100%;

                margin-bottom: 22rem;

                .track {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;

                    padding: 0.5rem 0;
                    border-top: 1px solid var(--phone-color-border);

                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        background-color: var(--phone-color-hover);
                    }

                    &[data-album="true"] {
                        padding: 1rem 0;
                    }

                    &[data-playing="true"] {
                        .track-info {
                            .text {
                                .title {
                                    color: var(--phone-color-pink);
                                }

                                .subtitle {
                                    color: var(--phone-color-pink);
                                    opacity: 0.75;
                                }
                            }
                        }

                        svg {
                            color: var(--phone-color-pink);
                        }
                    }

                    .track-info {
                        display: flex;
                        align-items: center;
                        gap: 1rem;

                        margin-left: 1.5rem;

                        .index {
                            font-size: 17px;
                            font-weight: 400;
                            color: var(--phone-text-secondary);
                        }

                        img {
                            width: 3.75rem;
                            height: 3.75rem;
                            border-radius: 5px;
                        }

                        .text {
                            display: flex;
                            flex-direction: column;

                            .title {
                                font-size: 17px;
                                font-weight: 400;
                                color: var(--phone-text-primary);
                            }

                            .subtitle {
                                font-size: 15px;
                                font-weight: 400;
                                color: var(--phone-text-secondary);
                            }
                        }
                    }

                    svg {
                        font-size: 20px;
                        color: var(--phone-text-primary);
                        margin-right: 1rem;
                    }
                }
            }
        }
    }

    .music-player {
        position: absolute;
        bottom: 0;
        z-index: 2;
        height: 100%;
        width: 100%;

        background-color: var(--phone-color-primary);

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;

        .music-player-header {
            display: flex;
            align-items: center;

            margin-top: 3.5rem;

            background-color: var(--phone-text-primary);
            height: 0.5rem;
            width: 4rem;
            border-radius: 5px;

            cursor: pointer;
        }

        .music-player-body {
            display: flex;
            flex-direction: column;
            gap: 1.5rem;

            width: 85%;

            img {
                height: 22rem;
                width: 22rem;
                border-radius: 12px;
                object-fit: cover;

                box-shadow: 0px 10px 26px 12px rgba(0, 0, 0, 0.25);
            }

            .song-info {
                display: flex;
                flex-direction: column;
                gap: 0.15rem;

                margin-left: 0.5rem;

                .title {
                    font-size: 22px;
                    color: var(--phone-text-primary);
                    font-weight: 500;
                }

                .subtitle {
                    font-size: 18px;
                    color: var(--phone-text-secondary);
                }
            }

            .duration {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.3rem;

                width: 100%;

                input {
                    width: 100%;

                    // style it
                    -webkit-appearance: none;
                    background: transparent;
                    outline: none;
                    -webkit-transition: 0.2s;
                    transition: opacity 0.2s;

                    &::-webkit-slider-runnable-track {
                        width: 100%;
                        height: 0.3rem;
                        cursor: pointer;

                        backdrop-filter: blur(15px);

                        border-radius: 5px;
                    }

                    &::-webkit-slider-thumb {
                        height: 1.5rem;
                        width: 1.5rem;
                        cursor: pointer;

                        -webkit-appearance: none;
                    }

                    &:focus {
                        outline: none;
                    }
                }

                .time {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    align-items: center;

                    width: 100%;

                    font-size: 13px;
                    font-weight: 500;
                    color: var(--phone-text-primary);

                    font-weight: 300;
                    opacity: 0.5;
                }
            }

            .controls {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                gap: 4rem;

                margin-top: 1rem;

                svg {
                    color: var(--phone-text-primary);

                    font-size: 40px;
                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        filter: brightness(0.7);
                    }
                }

                .play {
                    svg {
                        font-size: 55px;
                        stroke-width: 20;
                    }
                }
            }

            .volume {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 0.5rem;

                width: 100%;

                margin-top: 2.5rem;

                svg {
                    opacity: 0.4;
                    color: var(--phone-text-primary);
                    font-size: 25px;
                }

                input {
                    width: 100%;

                    // style it
                    -webkit-appearance: none;
                    background: transparent;
                    outline: none;
                    -webkit-transition: 0.2s;
                    transition: opacity 0.2s;

                    &::-webkit-slider-runnable-track {
                        width: 100%;
                        height: 0.25rem;
                        cursor: pointer;

                        backdrop-filter: blur(15px);

                        border-radius: 3px;
                    }

                    &::-webkit-slider-thumb {
                        height: 1.5rem;
                        width: 1.5rem;
                        cursor: pointer;

                        -webkit-appearance: none;
                    }

                    &:focus {
                        outline: none;
                    }
                }
            }

            .other {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                gap: 4rem;

                .liked {
                    color: red;
                }

                svg {
                    opacity: 0.6;
                    color: var(--phone-text-primary);

                    font-size: 40px;
                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        filter: brightness(0.7);
                    }
                }
            }
        }
    }

    .add-playlist-container {
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: 3;

        background-color: var(--phone-color-primary);

        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    .music-footer {
        position: absolute;
        bottom: 0;

        width: 100%;

        display: flex;
        flex-direction: column;
        gap: 0.75rem;

        background-color: var(--phone-color-primary);

        padding-bottom: 3rem;

        .music-playing {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 1rem 2rem;

            border-bottom: 1px solid var(--phone-color-border);
            background-color: var(--phone-color-opacity);
            backdrop-filter: blur(30px);

            cursor: pointer;

            .song-info {
                display: flex;
                align-items: center;
                gap: 0.75rem;

                img {
                    height: 3rem;
                    // opacity: 0.75;
                    filter: brightness(0.8);

                    border-radius: 5px;
                }
            }

            .text {
                display: flex;
                flex-direction: column;

                .title {
                    font-size: 18px;
                    color: var(--phone-text-primary);
                }

                .subtitle {
                    font-size: 16px;
                    color: var(--phone-text-secondary);
                }
            }

            .controls {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
            }

            svg {
                font-size: 35px;
                color: var(--phone-text-primary);

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    transform: scale(1.1);
                    filter: brightness(0.7);
                }
            }
        }

        .items {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 7.5rem;

            .item {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.2rem;

                color: var(--phone-text-primary);
                font-size: 16px;
                opacity: 0.6;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                svg {
                    font-size: 30px;
                }

                &:hover {
                    color: var(--phone-color-pink);
                    opacity: 1;
                }

                &[data-active="true"] {
                    color: var(--phone-color-pink);
                    opacity: 1;
                    font-weight: 500;
                }
            }
        }
    }
}