@import "../../../styles/Colors.scss";
@import "../../../styles/Animations.scss";

.darkchat-container {
    position: relative;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    height: 100%;
    padding-top: 3%;

    background-color: #000000;

    &::-webkit-scrollbar {
        display: none;
    }

    .animation-slide {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .darkchat-header {
        display: flex;
        width: 90%;
        align-items: center;
        justify-content: space-between;

        margin-top: 3rem;
        padding: 0.5rem 0;

        border-bottom: 1px solid rgba(200, 200, 200, 0.3);

        .title {
            font-size: 30px;
            font-weight: 500;
            color: #ffffff;
        }

        svg {
            font-size: 35px;
            color: var(--phone-color-orange);

            cursor: pointer;

            &:hover{
                filter: brightness(90%);
            }
        }

        &.chat {
            svg {
                font-size: 30px;
            }

            .title {
                font-size: 25px;
            }
        }
    }

    .channels-body {
        width: 100%;

        display: flex;
        flex-direction: column;

        .channel {
            width: 90%;
            color: #ffffff;

            display: flex;
            align-items: center;
            justify-content: space-between;

            padding: 0.75rem 1.5rem;
            border-bottom: 1px solid var(--phone-color-border);

            cursor: pointer;
            transition: all 0.1s ease-in-out;

            &:hover {
                background-color: rgba(255, 255, 255, 0.075);
            }

            .channel-info {
                display: flex;
                flex-direction: column;
                gap: 0.25rem;

                .channel-name {
                    font-size: 22px;
                    svg {
                        font-size: 15px;
                    }
                }

                .recent-message {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 0.3rem;

                    font-size: 15px;
                    color: var(--phone-color-orange);

                    .members {
                        display: flex;
                        align-items: center;
                        gap: 0.25rem;

                        font-weight: 700;

                        svg {
                            font-size: 22px;
                        }
                    }
                }
            }

            .right {
                display: flex;
                flex-wrap: row;
                align-items: center;
                gap: 0.3rem;

                font-size: 15px;
                font-weight: 600;
                color: var(--phone-text-secondary);

                svg {
                    font-size: 20px;
                }
            }
        }
    }
    .message-bottom {
        width: 95%;
        height: 17%;
        display: flex;
        flex-direction: column;
        align-items: center;
        // gap: 0.5rem;

        &.absolute {
            position: absolute;
            bottom: 0rem;

            padding-bottom: 1.2rem;
            height: 10%;

            .upper {
                background-color: var(--phone-color-highlight);
            }
        }

        .upper {
            width: 100%;
            height: 5rem;

            display: flex;
            flex-direction: row;
            align-items: center;

            justify-content: center;
            gap: 0.75rem;

            img {
                cursor: pointer;
                color: #999999;
                width: 25px;

                svg,
                path {
                    color: #999999;
                    fill: #999999;
                }
            }

            svg {
                font-size: 40px;
                color: #999999;

                cursor: pointer;
            }

            .input {
                width: 100%;

                border-radius: 30px;

                display: flex;
                align-items: center;

                border: 2px solid var(--phone-color-border);
                padding: 0.3rem;
            }


            input {
                width: 90%;

                border: none;

                background-color: transparent;

                color: white;
                font-family: "Roboto";
                font-size: 17px;

                padding: 0.4rem 0.5rem;

                &:active,
                &:focus {
                    outline: none;
                }
            }

            .send {
                display: flex;
                align-items: center;
                justify-content: center;

                background-color: var(--phone-color-orange);

                border-radius: 50%;

                height: 1.8rem;
                width: 1.8rem;

                cursor: pointer;

                svg {
                    font-size: 20px;
                    color: #ffffff;
                }
            }
        }

        .actions-wrapper {
            display: flex;

            width: 100%;
            height: 30%;
            padding: 0 0.5rem;


            justify-content: center;

            .actions {
                display: flex;
                flex-direction: row;
                align-items: flex-start;
                justify-content: center;
                gap: 1rem;

                margin: 0.5rem 0rem 0px;
            }

            .action {
                display: flex;
                align-items: center;
                justify-content: center;

                cursor: pointer;

                width: 3.25rem;
                height: 2.5rem;

                background-color: #1b1b1b;
                color: #ffffff;
                border-radius: 30px;

                font-size: 22px;
                font-weight: 500;

                &:hover{
                    filter: brightness(90%);
                }

                img {
                    width: 1.5rem;
                    height: 1.5rem;
                }

                svg {
                    font-size: 25px;
                }
            }
        }
    }

    .chat-wrapper {
        display: relative;

        display: flex;
        flex-direction: column-reverse;
        height: 70%;

        width: 87%;

        overflow-y: auto;
        overflow-x: hidden;

        scroll-behavior: smooth;

        &::-webkit-scrollbar {
            display: none;
        }

        .chat-body {
            width: 100%;

            display: flex;
            flex-direction: column;

            margin-top: 1rem;
            padding: 1rem 0;
            gap: 0.5rem;

            .message {
                font-size: 16px;
                font-weight: 300;
                width: auto;
                max-width: 70%;

                color: rgb(15, 20, 25);

                display: flex;
                flex-direction: column;
                gap: 0.5rem;

                .content {
                    padding: 0.75rem 1rem;
                    border-radius: 20px;
                    overflow-wrap: break-word;
                    font-size: 18px;

                    a {
                        color: var(--phone-color-blue);
                        cursor: pointer;
                    }
                }

                .attatchments {
                    img {
                        width: 100%;
                        border-radius: 15px;
                    }
                }

                .location {
                    border-radius: 20px;
                    width: 100%;
                    width: 16rem;

                    cursor: pointer;
                    display: flex;
                    flex-direction: column;

                    background-color: rgb(50, 50, 50);
                    transition: all;

                    .img {
                        height: 10rem;
                        border-radius: 20px;
                        background-size: cover;
                        background-position: center;
                    }
                }

                .date {
                    padding: 0 0.5rem;
                    font-size: 13px;
                    font-weight: 300;
                    color: #687684;
                }

                .user {
                    margin-top: 0.5rem;
                    padding: 0 0.5rem;
                    font-size: 15px;

                    font-weight: 500;
                    color: #687684;
                }

                &.self {
                    margin-left: auto;

                    .date {
                        text-align: right;
                        margin-left: auto;
                    }

                    .content {
                        border-bottom-right-radius: 0;

                        text-align: right;
                        background-color: var(--phone-color-orange);

                        color: #ffffff;
                        overflow-wrap: break-word;
                    }
                }

                &.other {
                    margin-right: auto;

                    .content {
                        border-bottom-left-radius: 0;

                        background-color: #a6a6a7;
                        color: $black-primary;

                        &:not(img) + .other {
                            background-color: transparent;
                            padding: 0;
                        }
                    }

                    .date {
                        margin-right: auto;
                    }
                }
            }
        }
    }

    .manage-title {
        width: 75%;
        font-size: 13px; //hmm
        color: #88888c;
        text-transform: uppercase;
        text-align: left;

        margin-bottom: -0.5rem;
    }

    .category-title {
        width: 75%;
        font-size: 13px;
        color: #88888c;
        text-transform: uppercase;
        text-align: left;
    }

    section {
        margin-bottom: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;

        background-color: rgb(15, 15, 15);

        width: 90%;
        border-radius: 10px;

        .settings-item {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            padding: 0.75rem 1rem;

            border-width: 1rem;

            border-bottom: 1px solid var(--phone-color-border);


            &:hover {
                filter: brightness(90%);
            }

            &:last-child {
                border: none;
            }

            input {
                font-size: 16px;
                text-align: right;
                border: none;
                background: transparent;
                outline: none;
                color: var(--phone-text-secondary);

                &:focus {
                    outline: none;
                }

                &::placeholder {
                    color: var(--phone-text-secondary);
                }
            }

            .setting {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;
                justify-content: left;

                .title {
                    font-size: 18px;
                    font-weight: 300;

                    color: white;

                    &.blue {
                        color: var(--phone-color-blue);
                        background: none;
                        font-weight: 400;
                    }

                    &.red {
                        color: var(--phone-color-red);
                        background: none;
                        font-weight: 400;
                    }

                    &.grey {
                        color: var(--phone-text-secondary);
                        background: none;
                        font-weight: 400;
                    }

                    margin: 0;
                }

                img {
                    width: 2rem;
                    height: 2rem;
                    border-radius: 7px;
                }

                svg:not(.checkmark, .remove) {
                    margin-left: -0.1rem;
                    margin-right: 0.25rem;

                    color: #ffffff;
                    font-size: 30px;
                    width: 1.1rem;
                    height: 1.1rem;

                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 0.5rem;
                    border-radius: 10px;
                }

                .remove {
                    padding: 0.2rem;
                    font-size: 30px;

                    width: 1.75rem;
                    height: 1.75rem;

                    color: var(--phone-color-red);
                    cursor: pointer;
                    margin-left: 0;
                }

                .hidden {
                    visibility: hidden;
                }

                .orange {
                    background-color: #ffa338;
                }

                .blue {
                    background-color: #348de9;
                }

                .darkblue {
                    background-color: #1a3fd4;
                }

                .lightblue {
                    background-color: #5aa7d7;
                }

                .green {
                    background-color: #57d26e;
                }

                .red {
                    background-color: #ff5252;
                }

                .pink {
                    background-color: #f5315e;
                }

                .purple {
                    background-color: #5958c0;
                }

                .grey {
                    background-color: #8e8e92;
                }
            }

            .setting-value {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;

                color: var(--phone-text-secondary);

                .value {
                    font-size: 18px;
                    font-weight: 300;
                }

                svg {
                    font-size: 22px;
                    cursor: pointer;
                }
            }

            svg {
                margin-left: 0.5rem;
                margin-right: 0.5rem;
                font-size: 28px;
                color: var(--phone-text-secondary);

                &.checkmark {
                    margin: 0;
                    font-size: 35px;
                    color: var(--phone-color-blue);

                    &.hidden {
                        visibility: hidden;
                    }
                }
            }

            .chevron {
                font-size: 20px;
                margin: 0;
                margin-right: -0.4rem;
            }
        }
    }

    input {
        color: var(--phone-color-input);

        ::placeholder {
            color: var(--phone-color-input);
        }
    }
}