@import "styles/Animations.scss";
@import "styles/Colors.scss";

[data-theme="light"] .popup {
    background-color: rgba(242, 242, 242, 0.8);

    .buttons {
        border-top: 1px solid #00000034;

        div {
            border-right: 1px solid #00000034;

            &:hover:not([data-disabled="true"]) {
                background-color: #00000034;
            }
        }
    }
}

[data-theme="dark"] .popup {
    background-color: rgba(30, 30, 30, 0.9);

    .buttons {
        div {
            &:hover:not([data-disabled="true"]) {
                background-color: #ffffff34;
            }
        }
    }
}

.popup-container {
    position: absolute;

    width: 100%;
    height: 100%;

    z-index: 999;

    border-radius: 40px;

    background-color: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(5px);

    display: flex;
    align-items: center;
    justify-content: center;
}

.popup {
    width: 18rem;

    border-radius: 15px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;

    padding-bottom: 0;

    .title {
        padding: 0 0.75rem;
        padding-top: 1.5rem;
        font-size: 20px;
        font-weight: 600;

        color: var(--phone-text-primary);
    }

    .description {
        font-size: 16px;
        font-weight: 400;
        text-align: center;
        padding: 0 1.5rem;
        margin-bottom: 0.75rem;

        color: var(--phone-text-primary);
    }

    .attachment {
        display: flex;
        align-items: center;
        justify-content: center;

        img,
        video {
            width: 100%;
            min-width: 100%;

            width: 18rem;

            height: 15rem;
            object-fit: cover;
        }
    }

    .profile-image {
        display: flex;

        width: 5rem;
        height: 5rem;
        border-radius: 50%;

        justify-content: center;
        align-items: center;
        font-size: 23px;
        font-weight: 400;

        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        background-color: var(--phone-text-secondary);

        color: $white-primary;

        margin-bottom: 1rem;
    }

    input {
        width: 80%;
        height: 2rem;

        border-radius: 7px;
        border: 1px solid #00000034;

        padding: 0 0.75rem;
        font-size: 16px;
        font-weight: 400;

        margin-bottom: 0.5rem;

        &:focus {
            outline: none;
        }

        background-color: #ffffff93;
    }

    .buttons {
        display: flex;
        flex-direction: row;
        height: 100%;

        width: 100%;

        border-top: 1px solid var(--phone-color-border);

        &[data-buttons="1"] {
            div {
                border-bottom-left-radius: 15px;
                border-bottom-right-radius: 15px;
            }
        }

        &[data-vertical="true"] {
            flex-direction: column;

            div {
                border-right: none;
                border-bottom: 1px solid var(--phone-color-border);

                &:nth-child(1) {
                    border-bottom-left-radius: 0;
                }

                &:last-child {
                    border-bottom: none;
                    border-bottom-left-radius: 15px;
                    border-bottom-right-radius: 15px;
                }
            }
        }

        div {
            flex: 1;
            height: 100%;

            padding: 0.75rem;
            padding-bottom: 1rem;

            font-size: 20px;
            font-weight: 500;
            text-align: center;

            border-right: 1px solid var(--phone-color-border);
            color: var(--phone-color-blue);

            &[data-bold="true"] {
                font-weight: 600;
            }

            &.red {
                color: var(--phone-color-red);
            }

            &.green {
                color: var(--phone-color-green);
            }

            &.yellow {
                color: var(--phone-color-yellow);
            }

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &[data-disabled="true"] {
                filter: opacity(0.5);
                cursor: not-allowed;
            }

            &:nth-child(1) {
                border-bottom-left-radius: 15px;
            }

            &:nth-child(2) {
                border-bottom-right-radius: 15px;
            }

            &:last-child {
                border-right: none;
            }
        }
    }
}
