@import "../../../styles/Colors.scss";
@import "../../../styles/Animations.scss";

.gif-selector {
    position: absolute;
    z-index: 999;

    bottom: 0;
    height: 94.5%;
    width: 100%;

    border-radius: 40px 40px 50px 50px;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    overflow: hidden;

    background-color: var(--phone-color-primary);
    animation: slideUp 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;

    .gif-selector-header {
        margin-top: 1rem;
        width: 90%;

        display: flex;
        flex-direction: column;
        padding: 0.5rem 0;
        gap: 1rem;

        .cancel {
            color: var(--phone-color-blue);
            font-size: 20px;
            font-weight: 400;

            cursor: pointer;
            transition: all 0.5s;

            text-align: left;
        }
    }

    .gif-results {
        width: 96%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0.2rem;

        overflow-y: auto;

        &::-webkit-scrollbar {
            display: none;
        }

        .gif-result {
            width: 100%;
            height: 100%;
            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:hover {
                filter: brightness(0.5);
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}
