@import "styles/Colors.scss";
@import "styles/Animations.scss";

.call-container-banner {
    position: absolute;
    z-index: 98;

    top: 3rem;
    left: 0;
    right: 0;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    background-color: rgba(19, 18, 18, 0.75);
    backdrop-filter: blur(25px);
    box-shadow: 0 10px 15px rgb(0 0 0 / 20%);

    animation: slideDown 1s cubic-bezier(0.19, 1, 0.22, 1);

    margin: auto;

    width: 95%;
    border-radius: 18px;

    height: 7rem;

    cursor: pointer;

    .caller {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 0.75rem;
        padding-left: 1.25rem;
        color: #ffffff;

        img {
            width: 3.5rem;
            height: 3.5rem;
            border-radius: 50%;
            background-color: var(--phone-color-primary);
            display: flex;
            align-items: center;
            justify-content: center;

            object-fit: cover;
        }

        .info {
            display: flex;
            flex-direction: column;

            .name {
                font-size: 24px;
                font-weight: 400;
            }

            .type {
                opacity: 0.8;
                font-size: 16px;
            }
        }
    }

    .call-buttons {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: right;
        gap: 1rem;
        margin-right: 1rem;

        .button {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 0.5rem;

            text-align: center;


            svg {
                font-size: 1.5rem;
                color: #ffffff;
                padding: 0.75rem;

                border-radius: 50%;

                box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
                transition: all 0.2s ease-in-out;
                cursor: pointer;

                &:hover {
                    filter: brightness(0.8);
                }
            }

            .text {
                color: #ffffff;
                opacity: 0.9;
                text-align: center;
            }

            .decline {
                transform: rotate(135deg);
                background-color: #fe3b30;
            }

            .accept {
                background-color: #34c759;
            }
        }
    }
}

.call-container {
    position: absolute;

    z-index: 96;

    background-color: white;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    height: 95%;
    width: 100%;
    padding-top: 15%;

    border-radius: 25px;

    overflow: hidden;

    .call-background {
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%;

        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        filter: blur(4px) brightness(0.4);

        border-radius: 25px;
        scale: 1.1;
    }

    .caller {
        margin-top: 3rem;

        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;

        z-index: 99;

        img {
            object-fit: cover;

            width: 4.5rem;
            height: 4.5rem;
            border-radius: 50%;
        }

        .info {
            color: #ffffff;
            text-align: center;

            .name {
                font-size: 35px;
            }

            .type {
                font-size: 18px;

                opacity: 0.8;
                text-align: center;
            }
        }
    }

    .buttons {
        z-index: 99;

        width: 100%;

        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        gap: 8rem;
        margin-bottom: 7rem;

        .button {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 0.5rem;

            i {
                color: #ffffff;
                font-size: 1.9rem;
                border-radius: 50%;

                padding: 1.5rem;

                box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
                transition: all 0.2s ease-in-out;
                cursor: pointer;

                &:hover {
                    filter: brightness(0.8);
                }
            }

            svg {
                color: #ffffff;
                font-size: 1.9rem;
                border-radius: 50%;

                padding: 1.5rem;

                box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
                transition: all 0.2s ease-in-out;
                cursor: pointer;

                &:hover {
                    filter: brightness(0.8);
                }

                &.bigger {
                    padding: 1.2rem;
                    font-size: 2.5rem;
                }
            }

            .text {
                color: #ffffff;
                opacity: 0.9;
                text-align: center;
            }

            .grey {
                background-color: #7c7c80;
            }

            .decline {
                transform: rotate(135deg);
                background-color: #fe3b30;
            }

            .accept {
                background-color: #34c759;
            }
        }
    }

    .options {
        width: 60%;

        margin: auto;

        justify-content: center;
        align-items: center;

        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 1.5rem;
        align-items: start;

        .option {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 0.5rem;

            text-align: center;

            .icon {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                color: #ffffff;

                transition: all 0.2s ease-in-out;
                cursor: pointer;

                height: 5.2rem;
                width: 5.2rem;

                border-radius: 50%;
                background-color: rgba(244, 244, 244, 0.4);
                box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
                opacity: 0.99;

                &:hover:not(.disabled) {
                    background-color: rgba(244, 244, 244, 0.5);
                }

                svg {
                    width: 100%;
                    height: 100%;
                    padding: 1.4rem;
                }
            }

            .text {
                color: #ffffff;
                opacity: 0.9;
                font-weight: 400;
            }

            &.disabled {
                opacity: 0.4;
                .icon {
                    cursor: not-allowed;
                }
            }

            &.active {
                .icon {
                    background-color: rgba(244, 244, 244, 1);
                    svg {
                        color: #000000;
                    }
                }
            }
        }
    }

    .feed {
        top: 0;
        position: absolute;
        height: 57rem;

        object-fit: cover;
        object-position: center;

        border-radius: 20px;

        filter: brightness(0.9);
    }

    .options-header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        width: 87%;

        left: 0;
        right: 0;

        top: 3.5rem;
        padding: 0.25rem;
        gap: 0.5rem;
        padding-top: 1rem;

        position: absolute;
        margin: auto;

        border-radius: 30px;
        background-color: rgba(0, 0, 0, 0.6);

        .call-header {
            width: 90%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            // gap: 1rem;

            .facetime-caller {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                gap: 0.5rem;

                .avatar {
                    width: 3rem;
                    height: 3rem;
                    border-radius: 50%;
                }

                .info {
                    color: #ffffff;
                    display: flex;
                    flex-direction: column;
                    gap: 0.2rem;

                    .name {
                        font-size: 20px;
                        opacity: 0.95;
                        font-weight: 400;
                    }

                    .status {
                        display: flex;
                        flex-direction: row;
                        align-items: center;

                        gap: 0.3rem;

                        opacity: 0.8;

                        font-size: 16px;
                        font-weight: 400;

                        svg {
                            font-size: 18px;
                        }
                    }
                }
            }

            .end-button {
                padding: 0.3rem 1.3rem;
                border-radius: 25px;
                background-color: #fe3b30;
                font-size: 16px;

                transition: 0.5s;
                cursor: pointer;

                color: #ffffff;

                &:hover {
                    filter: brightness(0.8);
                }
            }
        }

        .facetime-buttons {
            width: 90%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-bottom: 0.5rem;

            .button {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                height: 3.25rem;
                width: 3.25rem;

                border-radius: 50%;
                background-color: rgba(244, 244, 244, 0.2);
                box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);

                &.active {
                    background-color: rgba(244, 244, 244, 0.99);
                    color: #000000;
                }

                &.disabled {
                    opacity: 0.5;

                    &:hover {
                        cursor: not-allowed;
                        background-color: rgba(244, 244, 244, 0.13);
                        color: #ffffff;
                    }
                }

                &:hover {
                    background-color: rgba(244, 244, 244, 0.9);
                    color: #000000;
                }

                transition: all 0.5s ease-in-out;
                cursor: pointer;
                color: #ffffff;

                i,
                svg {
                    padding: 0.75rem;
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }

    .camera {
        position: absolute;

        top: 36rem;
        right: 0;

        display: flex;
        flex-direction: column;

        width: 140px;
        height: 200px;

        .camera-body {
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;

            border-radius: 20px;
            canvas {
                box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.4);
                height: 100%;
            }
        }

        .camera-buttons {
            z-index: 99;

            position: absolute;
            bottom: 0;
            width: 90%;

            display: flex;
            justify-content: flex-end;

            margin-bottom: 0.5rem;

            .button {
                display: flex;
                justify-content: center;
                align-items: center;

                border-radius: 50%;
                background-color: rgba(0, 0, 0, 0.5);
                box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
                padding: 0.8rem;

                cursor: pointer;

                svg {
                    font-size: 22px;
                    color: #ffffff;
                }
            }
        }
    }
}
