@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

.marketplace-container {
    position: relative;
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: var(--phone-color-primary);

    .marketplace-header {
        width: 100%;
        padding-top: 4rem;
        padding-bottom: 1rem;

        display: flex;
        flex-direction: column;
        align-items: center;

        background-color: #0e4892;

        .searchbox {
            width: 80%;
        }
    }

    .add {
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 1;

        position: absolute;
        bottom: 2rem;
        right: 2rem;

        height: 4.5rem;
        width: 4.5rem;
        border-radius: 50%;

        background-color: #0e4892;
        color: #ffffff;

        font-size: 30px;

        cursor: pointer;
        transition: all 0.2s ease-in-out;

        &:hover {
            transform: scale(1.1);
        }
    }

    .marketplace-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;

        width: 100%;
        height: 100%;
        position: relative;

        .posts {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;

            overflow-y: auto;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                display: none;
            }

            max-width: 100%;

            padding-bottom: 15rem;

            .post {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 1rem;

                width: 100%;

                position: relative;

                border-bottom: 1px solid var(--phone-color-border);

                padding-bottom: 2rem;
                margin-bottom: 2rem;

                &:last-child {
                    border-bottom: none;
                    margin-bottom: 0;
                    padding-bottom: 0;
                }

                .images {
                    display: flex;
                    align-items: center;

                    width: 100%;
                    height: 25rem;
                    overflow-x: auto;
                    overflow-y: hidden;

                    &::-webkit-scrollbar {
                        display: none;
                    }

                    div {
                        flex: 1;

                        min-width: 100%;
                        width: 100%;
                        height: 100%;

                        img,
                        video {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;

                            pointer-events: none;
                        }
                    }
                }

                .scroll-dots {
                    position: absolute;
                    margin: auto;
                    left: 0;
                    right: 0;

                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 0.25rem;

                    div {
                        background-color: #737373;
                        height: 0.6rem;
                        width: 0.6rem;
                        border-radius: 50%;

                        cursor: pointer;
                        z-index: 1;
                        pointer-events: all;

                        transition: all 0.1s ease-in-out;

                        &:hover {
                            background-color: #2f81edc1;
                        }

                        &.active {
                            background-color: #2f80ed;
                        }
                    }
                }

                .post-info {
                    width: 88%;

                    display: flex;
                    flex-direction: column;

                    .post-header {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;

                        .price {
                            color: var(--phone-color-green);

                            font-size: 21px;
                            font-weight: 700;
                        }

                        border-bottom: 1px solid var(--phone-color-border);
                        padding-bottom: 0.5rem;
                    }

                    .post-content {
                        display: flex;
                        flex-direction: column;
                        gap: 0.8rem;

                        margin-top: 0.5rem;

                        .title {
                            display: flex;
                            flex-direction: row;
                            align-items: center;
                            gap: 0.4rem;

                            font-size: 20px;
                            font-weight: 600;
                            color: var(--phone-text-primary);

                            .date {
                                opacity: 0.5;
                                font-size: 16px;
                                font-weight: 500;
                            }
                        }

                        .description {
                            color: var(--phone-text-primary);
                            opacity: 0.9;
                        }

                        .buttons {
                            display: flex;
                            flex-direction: column;
                            gap: 0.5rem;

                            .button {
                                display: flex;
                                align-items: center;
                                justify-content: center;

                                width: 100%;
                                height: 2.75rem;
                                border-radius: 6px;

                                background-color: #0e4892;
                                color: rgba(255, 255, 255, 0.9);

                                font-size: 17px;
                                font-weight: 500;

                                cursor: pointer;
                                transition: all 0.2s ease-in-out;

                                &.red {
                                    background-color: var(--phone-color-red);
                                }

                                &:hover {
                                    transform: scale(1.01);
                                    filter: brightness(0.8);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .new-post-container {
        position: absolute;
        bottom: 0;
        z-index: 1;

        height: 94%;
        width: 100%;

        background-color: var(--phone-color-primary);

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.5rem;

        animation: slideUp 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        .new-post-header {
            display: flex;
            align-items: center;
            justify-content: space-between;

            width: 100%;
            padding: 1rem 0;

            text-align: center;

            background-color: #0e4892;

            border-bottom: 1px solid var(--phone-color-border);

            .cancel {
                color: var(--phone-color-blue);
                font-size: 18px;
                font-weight: 400;

                cursor: pointer;
            }

            .title {
                color: var(--phone-text-primary);

                font-size: 18px;
                font-weight: 500;
            }

            div {
                flex: 1;
            }
        }

        .new-post-body {
            width: 100%;

            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;
            margin-right: 1rem;

            .item {
                display: flex;
                flex-direction: column;
                width: 80%;

                .title {
                    color: #ffffff;

                    font-size: 18px;
                    font-weight: 500;
                }

                .images {
                    width: 100%;
                    display: grid;
                    grid-template-columns: repeat(3, 1fr);
                    gap: 0.7rem;

                    .image {
                        width: 100%;
                        // height: 100%;
                        height: 6.5rem;

                        // background-color: rgba(241, 241, 241, 0.656);
                        background-color: var(--phone-color-input);
                        border: 3px dashed var(--phone-color-border);

                        border-radius: 6px;

                        display: flex;
                        align-items: center;
                        justify-content: center;

                        align-self: center;
                        justify-self: center;

                        font-size: 45px;
                        color: #8e8e93;

                        cursor: pointer;

                        background-size: cover;
                        background-position: center;
                    }
                }

                input,
                textarea {
                    width: 100%;

                    padding: 0.75rem;
                    border: none;

                    font-family: Roboto;
                    font-size: 16px;

                    background-color: var(--phone-color-input);
                    color: var(--phone-text-primary);

                    resize: none;
                    border-radius: 3px;

                    &::-webkit-inner-spin-button,
                    &::-webkit-outer-spin-button {
                        -webkit-appearance: none;
                        margin: 0;
                    }

                    &:focus {
                        outline: none;
                    }
                }
            }

            .button {
                margin-top: 4rem;

                padding: 0.6rem 2rem;
                border-radius: 7px;
                background-color: #0e4892;
                color: #ffffff;

                font-size: 18px;
                font-weight: 500;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    filter: brightness(0.8);
                }
            }
        }
    }
}
