@import "styles/Colors.scss";
@import "styles/Animations.scss";

:root {
    --share-background: rgb(240, 240, 240);
    --share-background-secondary: rgba(255, 255, 255, 0.8);
    --share-x: rgb(0, 40, 40);
}

[data-theme="dark"] {
    --share-background: rgb(35, 35, 35);
    --share-background-secondary: rgba(65, 65, 65, 0.8);
    --share-x: rgba(160, 160, 160, 1);
}

.share-component {
    height: 23rem;
    width: 100%;

    position: absolute;
    z-index: 99;

    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;

    background-color: var(--share-background);

    display: flex;
    flex-direction: column;
    align-items: center;

    border-top-left-radius: 12px;
    border-top-right-radius: 12px;

    animation: slideUp 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);

    .share-header {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        margin-top: 1rem;
        margin-right: 1.25rem;

        svg {
            font-size: 1.5rem;
            padding: 0.2rem;
            background-color: var(--share-background-secondary);
            color: var(--share-x);

            border-radius: 50%;

            cursor: pointer;
        }
    }

    .share-body {
        width: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;

        overflow-y: auto;
        overflow-x: hidden;
        &::-webkit-scrollbar {
            display: none;
        }

        padding-bottom: 3rem;
    }

    .airdrop {
        width: 90%;
        display: flex;
        flex-direction: row;
        gap: 1.2rem;

        .user {
            display: flex;
            flex-direction: column;
            align-items: center;
            // gap: 0.4rem;

            cursor: pointer;
            transition: all 0.2s ease-in-out;
            &:hover:not(.sent) {
                filter: brightness(0.8);
            }

            img {
                width: 4.2rem;
                height: 4.2rem;
                border-radius: 50%;

                border: 3px solid transparent;
                margin-bottom: 0.3rem;
            }

            .name {
                font-size: 15px;
                color: var(--phone-text-primary);
            }

            &.sent {
                img {
                    border: 3px solid var(--phone-color-blue);
                }

                color: var(--phone-color-blue);
                font-size: 14px;
            }
        }

        padding-bottom: 0.75rem;
        border-bottom: 1px solid var(--phone-color-border);
    }

    .app-items {
        width: 90%;
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1.5rem;

        margin-top: 0.75rem;

        padding-bottom: 1rem;
        border-bottom: 1px solid var(--phone-color-border);

        .item {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 0.5rem;

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:hover {
                filter: brightness(0.6);
            }

            img {
                width: 4rem;
                height: 4rem;
                border-radius: 14px;
            }

            .title {
                font-size: 15px;
                color: var(--phone-text-primary);
                opacity: 0.95;
            }
        }
    }

    .buttons {
        margin-top: 1rem;

        width: 90%;
        display: flex;
        flex-direction: column;
        background-color: var(--share-background-secondary);
        border-radius: 10px;

        .item {
            display: flex;
            flex-direction: row;
            align-items: center;

            justify-content: space-between;
            padding: 1rem 1rem;

            border-bottom: 1px solid var(--phone-color-border);

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:hover {
                background-color: var(--phone-color-hover);
            }

            &:last-child {
                border-bottom: none;
            }

            .title {
                font-size: 18px;
                color: var(--phone-text-primary);
                opacity: 0.9;
                font-weight: 400;
            }

            svg {
                font-size: 25px;
                color: var(--phone-text-primary);
                opacity: 0.9;
            }
        }
    }
}
