@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

.messages-container {
    position: relative;
    height: 100%;
    padding-top: 3%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background-color: var(--phone-color-primary);

    .animation-slide {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .add-member-container {
        position: absolute;
        bottom: 1.75rem;
        z-index: 99;

        display: flex;
        flex-direction: column;
        align-items: center;

        height: 90%;
        width: 100%;

        background-color: var(--phone-color-highlight);
        animation: slideUp 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        transition: slideDown 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        .add-member-header {
            width: 87%;

            display: flex;
            flex-direction: column;
            gap: 1rem;

            align-items: center;

            background-color: var(--phone-color-highlight2);

            padding: 1rem 1.2rem;
            border-top-left-radius: 15px;
            border-top-right-radius: 15px;

            border-bottom: 1px solid var(--phone-color-border);

            .searchbox {
                width: 90%;
            }

            .top {
                width: 100%;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                span {
                    flex: 1;
                }

                div {
                    flex: 1;
                    text-align: center;

                    &.button {
                        color: var(--phone-color-blue);
                        text-align: right;
                        font-size: 18px;

                        cursor: pointer;
                    }

                    &.title {
                        font-size: 20px;
                        font-weight: 500;
                        color: var(--phone-text-primary);
                    }
                }
            }
        }

        .contacts {
            margin-top: 0.5rem;
            width: 87%;

            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 0.2rem;

            overflow-y: auto;
            padding-bottom: 2rem;

            .contact {
                width: 100%;
            }

            &::-webkit-scrollbar {
                display: flex;
            }
        }
    }

    .new-message-container {
        position: absolute;
        z-index: 99;
        bottom: 0;

        display: flex;
        flex-direction: column;
        align-items: center;

        height: 94%;
        width: 100%;

        background-color: var(--phone-color-highlight);

        animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        .new-message-header {
            width: 94%;

            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            background-color: var(--phone-color-highlight2);

            padding: 1rem 1.2rem;
            border-top-left-radius: 15px;
            border-top-right-radius: 15px;

            border-bottom: 1px solid var(--phone-color-border);

            span {
                flex: 1;
            }

            div {
                flex: 1;
                text-align: center;

                &.button {
                    color: var(--phone-color-blue);
                    text-align: right;
                    font-size: 18px;

                    cursor: pointer;
                }

                &.title {
                    font-size: 18px;
                    font-weight: 500;
                    color: var(--phone-text-primary);
                }
            }
        }

        .new-message-body {
            display: flex;
            flex-direction: column;
            align-items: center;

            width: 88%;
            height: 91%;

            .new-message-search {
                width: 100%;

                padding: 0.75rem 0;
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;

                // height: 1.5rem;
                min-height: 1.5rem;
                flex-wrap: wrap;

                border-bottom: 1px solid var(--phone-color-border);

                .to {
                    font-size: 15px;
                    color: var(--phone-text-secondary);
                }

                .contacts {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    flex-wrap: wrap;

                    gap: 0.3rem;

                    .contact {
                        background-color: var(--phone-color-highlight2);
                        font-size: 16px;
                        padding: 0.2rem 0.4rem;
                        border-radius: 7px;

                        white-space: nowrap;

                        text-align: center;

                        &.green {
                            color: var(--phone-color-green);
                        }

                        &.blue {
                            color: var(--phone-color-blue);
                        }
                    }
                }

                input {
                    // height: 100%;

                    border: none;

                    background: transparent;

                    font-size: 15px;
                    color: var(--phone-text-primary);

                    min-width: 7rem;

                    &:focus {
                        outline: none;
                    }
                }
            }

            .search-results {
                display: flex;
                flex-direction: column;
                align-items: center;

                width: 100%;
                margin-top: 0.3rem;

                overflow-y: auto;
                &::-webkit-scrollbar {
                    display: none;
                }

                .contact {
                    width: 100%;

                    display: flex;
                    align-items: center;

                    gap: 0.4rem;
                    padding: 0.4rem 0;

                    border-bottom: 1px solid var(--phone-color-border);

                    cursor: pointer;

                    img {
                        width: 2.75rem;
                        height: 2.75rem;

                        border-radius: 50%;
                        object-fit: cover;
                    }

                    .user {
                        display: flex;
                        flex-direction: column;

                        .name {
                            font-weight: 400;
                            font-size: 16px;
                            color: var(--phone-text-primary);
                        }

                        .number {
                            font-size: 14px;
                            color: var(--phone-text-secondary);
                        }
                    }

                    svg {
                        margin-left: auto;

                        font-size: 27px;
                        color: var(--phone-color-blue);
                    }
                }
            }
        }
    }

    .info-panel {
        position: absolute;
        z-index: 1;
        bottom: 0rem;

        display: flex;
        flex-direction: column;
        align-items: center;

        background-color: var(--phone-color-primary);

        animation: slideUp 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        height: 94%;
        width: 100%;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;

        .info-panel-header {
            width: 90%;
            padding: 1rem 0.2rem;

            text-align: right;

            color: var(--phone-color-blue);
            font-size: 18px;

            .done {
                cursor: pointer;
            }
        }

        .info-panel-body {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;

            overflow-y: auto;
            overflow-x: hidden;

            max-height: 100%;
            padding-bottom: 2rem;

            &::-webkit-scrollbar {
                display: none;
            }

            .info-panel-top {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.5rem;

                .group-avatar {
                    margin-right: 50px;

                    img {
                        width: 70px;
                        height: 70px;

                        border-radius: 50%;
                        object-fit: cover;
                        margin-right: -50px;
                    }
                }

                svg {
                    color: var(--phone-text-secondary);
                    font-size: 150px;

                    cursor: pointer;
                    padding: 0;
                    margin: 0;

                    &.big {
                        font-size: 200px;
                    }
                }

                .avatar {
                    width: 9rem;
                    height: 9rem;
                    border-radius: 50%;
                }

                .profile-image {
                    display: flex;

                    width: 4rem;
                    height: 4rem;
                    border-radius: 50%;

                    justify-content: center;
                    align-items: center;
                    font-size: 23px;
                    font-weight: 400;

                    background-position: center;
                    background-size: cover;
                    background-repeat: no-repeat;

                    // background-color: #bbbbbd;
                    background-color: var(--phone-text-secondary);

                    color: $white-primary;

                    &.big {
                        width: 5rem;
                        height: 5rem;
                        font-size: 22px;
                    }

                    &.bigger {
                        width: 9rem;
                        height: 9rem;
                        font-size: 50px;
                    }

                    &.custom {
                        background-color: transparent;
                    }
                }

                .members {
                    font-size: 16px;
                    font-weight: 400;

                    color: var(--phone-text-primary);
                }

                .name {
                    font-size: 27px;
                    font-weight: 400;
                    color: var(--phone-text-primary);
                }
            }

            .items {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
            }

            .info-section {
                width: 89%;
                margin-top: 1.5rem;

                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.2rem;
                border-radius: 7.5px;
                background-color: var(--phone-color-highlight);

                .item {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 0.4rem;

                    width: 90%;

                    padding: 0.75rem;
                    border-top: 1px solid var(--phone-color-border);

                    font-size: 17px;
                    color: var(--phone-text-primary);
                    font-weight: 400;

                    cursor: pointer;

                    img {
                        width: 35px;
                        height: 35px;

                        border-radius: 50%;
                        object-fit: cover;
                    }

                    svg {
                        font-size: 25px;
                    }

                    .remove {
                        color: var(--phone-color-red);
                    }

                    .info {
                        margin-left: auto;
                        color: var(--phone-color-blue);
                    }

                    .name {
                        font-weight: 400;
                        font-size: 16px;
                    }

                    &.red {
                        color: var(--phone-color-red);
                    }

                    &.blue {
                        color: var(--phone-color-blue);
                    }

                    .title {
                        font-size: 14px;
                        color: var(--phone-text-primary);
                    }

                    .value {
                        font-size: 18px;
                        color: var(--phone-color-blue);
                    }

                    &:first-child {
                        border-top: none;
                    }
                }
            }
        }
    }

    .messages-header {
        width: 90%;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0.5rem;

        margin-top: 2rem;

        .title {
            color: var(--phone-text-primary);
            font-size: 30px;
            font-weight: 600;
            font-style: bold;
        }

        svg {
            font-size: 30px;

            color: var(--phone-color-blue);

            cursor: pointer;
            &:hover {
                filter: brightness(0.7);
            }
        }
    }

    .searchbox {
        width: 83%;
        z-index: 1;
    }

    .users-list {
        display: flex;
        flex-direction: column;
        align-items: center;

        margin-top: 1rem;

        height: 44rem;
        max-height: 44rem;
        width: 100%;

        overflow: auto;

        &::-webkit-scrollbar {
            display: none;
        }

        .user {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            width: 100%;

            gap: 0.5rem;

            cursor: pointer;

            padding: 0.25rem 1rem;

            &:hover{
                filter: brightness(90%);
            }

            .avatar {
                width: 3.1rem;
                height: 3.1rem;
                border-radius: 50%;
                background-color: #ffffff;
                margin-right: 0.3rem;

                margin-top: 0.25rem;

                object-fit: cover;

                &.group {
                    background-color: #999999;
                    border: none;

                    height: auto;
                    height: 3.1rem;
                    width: 3.1rem;
                    position: relative;

                    div {
                        position: absolute;

                        display: flex;
                        align-items: center;
                        justify-content: center;

                        background-color: #c1c7d0;

                        border-radius: 50%;
                        color: #ffffff;

                        background-size: cover;
                        background-position: center;
                        background-repeat: no-repeat;

                        &.unknown {
                            background-image: url("../../../../../public/assets/img/no-pfp.png");
                        }

                        &:nth-child(1) {
                            top: 0.4rem;
                            left: 0.4rem;

                            height: 1.3rem;
                            width: 1.3rem;
                            font-size: 12px;
                        }

                        &:nth-child(2) {
                            bottom: 0.6rem;
                            right: 0.4rem;

                            height: 1.1rem;
                            width: 1.1rem;
                            font-size: 9px;
                        }

                        &:nth-child(3) {
                            top: 0.4rem;
                            right: 0.45rem;

                            height: 0.8rem;
                            width: 0.8rem;
                            font-size: 8px;
                        }

                        &:nth-child(4) {
                            bottom: 0.35rem;
                            left: 0.55rem;

                            height: 0.9rem;
                            width: 0.9rem;
                            font-size: 7px;
                        }
                    }
                }
            }

            .read {
                margin-left: 0.5rem;
                padding: 0.45rem;

                margin-top: 1.75rem;

                &.unread {
                    background-color: var(--phone-color-blue);
                    border-radius: 50%;
                }
            }

            .user-body {
                display: flex;
                flex-direction: column;
                width: 77%;
                gap: 0;

                border-bottom: 1px solid var(--phone-color-border);

                .user-header {
                    width: 95%;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;

                    .name {
                        color: var(--phone-text-primary);
                        font-size: 18px;
                        font-weight: 500;

                        margin: 0;

                        max-width: 70%;
                    }

                    .right {
                        display: flex;
                        flex-direction: row;
                        align-items: center;

                        gap: 0.7rem;

                        .time {
                            color: var(--phone-text-secondary);
                            font-weight: 400;
                            font-size: 14px;
                        }

                        svg {
                            font-size: 22px;
                            color: var(--phone-text-secondary);
                            opacity: 0.6;
                        }
                    }
                }

                .content {
                    display: flex;
                    flex-direction: column;

                    width: 100%;
                    height: 2rem;

                    color: var(--phone-text-secondary);
                    font-weight: 400;
                    font-size: 16px;

                    margin-bottom: 0.5rem;

                    overflow-wrap: break-word;
                }
            }
        }
    }

    .message-header {
        width: 88%;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0.3rem 0;

        margin-top: 2rem;

        border-bottom: 1px solid var(--phone-color-border);

        .back {
            display: flex;
            align-items: center;
            justify-content: center;

            color: var(--phone-color-blue);
            cursor: pointer;

            svg {
                font-size: 35px;
            }

            span {
                display: flex;
                align-items: center;
                justify-content: center;

                font-size: 14px;
                font-weight: 400;

                background-color: var(--phone-color-blue);
                border-radius: 15px;
                height: 1.5rem;
                width: 2rem;

                color: $white4;

                margin-left: -0.5rem;
            }
        }

        .call {
            color: var(--phone-color-blue);
            font-size: 35px;

            cursor: pointer;

            &.hidden {
                visibility: hidden;
            }
        }

        .facetime {
            color: var(--phone-color-blue);
            font-size: 35px;

            cursor: pointer;

            &.hidden {
                visibility: hidden;
            }
        }

        .user {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 0.5rem;

            margin-top: 1rem;
            margin-right: 1.5rem;

            cursor: pointer;

            .avatar {
                width: 50px;
                height: 50px;
                border-radius: 50%;
                object-fit: cover;
            }

            .name {
                font-weight: 400;
                font-size: 16px;

                color: var(--phone-text-primary);
            }
        }
    }

    .message-container {
        display: relative;

        display: flex;
        flex-direction: column-reverse;
        height: 68%;

        width: 87%;

        overflow-y: auto;
        overflow-x: hidden;

        scroll-behavior: smooth;

        transition: all 0.2s ease-in-out;

        &::-webkit-scrollbar {
            display: none;
        }

        .message-body {
            width: 100%;

            display: flex;
            flex-direction: column;

            margin-top: 1rem;
            padding: 1rem 0;
            gap: 0.5rem;

            .message {
                font-size: 16px;
                font-weight: 300;
                width: auto;
                max-width: 70%;

                color: rgb(15, 20, 25);

                display: flex;
                flex-direction: column;
                gap: 0.5rem;

                .content-wrapper {
                    display: flex;
                    align-items: center;
                    gap: 0.25rem;

                    svg {
                        font-size: 35px;
                        color: var(--phone-color-red);
                    }
                }

                .content {
                    padding: 0.75rem 1rem;
                    border-radius: 20px;
                    overflow-wrap: break-word;
                    font-size: 18px;
                    max-width: 100%;

                    user-select: text;

                    a {
                        color: var(--phone-color-blue);
                        cursor: pointer;
                    }
                }

                .location {
                    border-radius: 20px;
                    width: 100%;
                    width: 16rem;

                    cursor: pointer;
                    display: flex;
                    flex-direction: column;

                    background-color: rgb(50, 50, 50);
                    transition: all;

                    .img {
                        height: 10rem;
                        border-radius: 20px;

                        filter: blur(2px);

                        background-size: cover;
                        background-position: center;
                    }

                    .sender {
                        padding: 0.75rem;

                        color: #ffffff;
                        opacity: 0.8;

                        font-size: 16px;
                        font-weight: 500;
                    }
                }

                .payment {
                    padding: 2.5rem 1.5rem;
                    border-radius: 20px;

                    background-color: rgb(20, 20, 20);
                    color: #fafafa;

                    width: 100%;

                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .sent {
                        font-size: 70px;
                        font-weight: 600;

                        padding: 0.5rem 1rem;
                    }

                    .request {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        gap: 1.5rem;
                        width: 14rem;


                        &.self {
                            padding: 1.5rem 0;
                        }

                        .title {
                            font-weight: 400;
                            font-size: 25px;
                            text-align: center;
                        }

                        .button {
                            background-color: #ffffff;
                            color: #000000;
                            font-size: 18px;
                            font-weight: 500;

                            border-radius: 25px;
                            padding: 0.6rem 1.5rem;

                            cursor: pointer;
                            transition: all 0.2s ease-in-out;

                            &:hover {
                                filter: brightness(0.8);
                            }
                        }
                    }
                }

               .voice-message {
                    width: 100%;
                    height: 100%;

                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 1rem;

                    border-radius: 25px;

                    padding: 0 0.25rem;
                    padding-left: 1rem;

                    svg {
                        font-size: 16px;
                        color: var(--phone-color-blue);
                        padding: 0.3rem;
                        background-color: #ffffff;
                        border-radius: 50%;

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;
                    }

                    .wave {
                        height: 60%;
                        width: 50%;

                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }

                    .duration {
                        font-size: 14px;
                        font-weight: 400;
                        color: #ffffff;
                    }

                    &.self {
                        border-bottom-right-radius: 0;
                        text-align: left;

                        background-color: var(--phone-color-blue);
                        color: #fafafa;

                        img {
                            filter: invert(1);
                        }
                    }

                    &.other {
                        border-bottom-left-radius: 0;
                        text-align: right;

                        background-color: var(--phone-color-highlight3);

                        color: #fafafa;

                        img {
                            filter: invert(62%) sepia(65%) saturate(6689%) hue-rotate(180deg) brightness(100%) contrast(100%);
                        }

                        svg {
                            color: var(--phone-color-highlight3);
                            background-color: var(--phone-color-blue);
                        }
                    }
                }

                .attatchments {
                    img,
                    video {
                        width: 100%;
                        border-radius: 15px;
                        cursor: pointer;
                    }
                }

                .date {
                    padding: 0 0.5rem;
                    font-size: 13px;
                    font-weight: 300;
                    color: #687684;
                }

                .status {
                    padding: 0 0.5rem;
                    font-size: 15px;
                    font-weight: 600;
                    color: var(--phone-color-red);
                }

                .user {
                    margin-top: 0.5rem;
                    padding: 0 0.5rem;
                    font-size: 14px;

                    font-weight: 300;
                    color: #687684;
                }

                &.self {
                    align-items: flex-end;
                    margin-left: auto;

                    cursor: pointer;

                    .date {
                        text-align: right;
                        margin-left: auto;
                    }

                    .payment {
                        border-bottom-right-radius: 0;
                        text-align: right;
                    }

                    .content {
                        border-bottom-right-radius: 0;
                        text-align: left;

                        background-color: var(--phone-color-blue);
                        color: #fafafa;

                        .number {
                            color: #fafafa;
                            font-weight: 400;
                        }
                    }
                }

                &.other {
                    margin-right: auto;

                    .payment {
                        border-bottom-left-radius: 0;
                    }

                    .content {
                        border-bottom-left-radius: 0;

                        background-color: var(--phone-color-highlight3);
                        color: var(--phone-text-primary);

                        &:not(img) + .other {
                            background-color: transparent;
                            padding: 0;
                        }
                    }

                    .date {
                        margin-right: auto;
                    }
                }
            }
        }
    }

    .attachments {
        position: absolute;
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 0.5rem;

        bottom: 10rem;
        left: 2rem;

        .attachment {
            position: relative;

            height: 5rem;
            width: 5rem;

            img,
            video {
                width: 100%;
                height: 100%;

                object-fit: cover;
                border-radius: 10px;
            }

            svg {
                position: absolute;

                top: 0.25rem;
                right: 0.25rem;

                height: 1.3rem;
                width: 1.3rem;
                padding: 0.1rem;
                border-radius: 50%;

                display: flex;
                align-items: center;
                justify-content: center;

                background-color: rgba(0, 0, 0, 0.8);
                color: #ffffff;

                cursor: pointer;
                font-size: 13px;
            }
        }
    }

    .message-bottom {
        width: 95%;
        height: 17%;
        display: flex;
        flex-direction: column;
        align-items: center;
        // gap: 0.5rem;

        &.absolute {
            position: absolute;
            bottom: 0rem;

            padding-bottom: 1.2rem;
            height: 10%;

            .upper {
                background-color: var(--phone-color-highlight);
            }
        }

        .upper {
            width: 90%;
            height: 5rem;

            display: flex;
            flex-direction: row;
            align-items: center;

            justify-content: center;
            gap: 0.75rem;

            background-color: var(--phone-color-primary);

            img {
                cursor: pointer;
                color: #999999;
                width: 25px;

                svg,
                path {
                    color: #999999;
                    fill: #999999;
                }
            }

            svg {
                font-size: 40px;
                color: #999999;

                cursor: pointer;
            }

            .input {
                width: 100%;

                border-radius: 30px;

                display: flex;
                align-items: center;

                border: 2px solid var(--phone-color-border);
                padding: 0.3rem;
            }

            .audio-message {
                width: 90%;
                height: 2rem;

                display: flex;
                flex-direction: row;
                align-items: center;

                padding: 0 0.25rem;

                svg {
                    color: #797979;

                    font-size: 25px;
                    padding: 0.3rem;
                    border-radius: 50%;

                    cursor: pointer;
                }

                .audio-waves {
                    width: 80%;
                    height: 80%;

                    img {
                        // add filter over the image to make it grey
                        filter: invert(51%) sepia(1%) saturate(0%) hue-rotate(294deg) brightness(92%) contrast(88%);
                        width: 100%;
                        height: 100%;
                    }
                }
            }


            input {
                width: 90%;

                border: none;

                background-color: transparent;

                color: var(--phone-text-primary);
                font-family: "Roboto";
                font-size: 17px;

                padding: 0.4rem 0.5rem;

                &:active,
                &:focus {
                    outline: none;
                }
            }

            .send {
                display: flex;
                align-items: center;
                justify-content: center;

                background-color: var(--phone-color-blue);

                border-radius: 50%;

                height: 1.8rem;
                width: 1.8rem;

                cursor: pointer;

                svg {
                    font-size: 20px;
                    color: #ffffff;
                }
            }
        }

        .actions-wrapper {
            display: flex;

            width: 100%;
            height: 100%;
            padding: 0 0.5rem;
            justify-content: center;

            background-color: var(--phone-color-primary);

            .actions {
                display: flex;
                flex-direction: row;
                align-items: flex-start;
                justify-content: center;
                gap: 1rem;

                margin: 0.5rem 0rem 0px;
            }

            .action {
                display: flex;
                align-items: center;
                justify-content: center;

                cursor: pointer;

                width: 3.25rem;
                height: 2.5rem;

                background-color: #e9e9e9;
                border-radius: 30px;

                font-size: 22px;
                font-weight: 500;

                &:hover{
                    filter: brightness(90%);
                }

                &.dark {
                    background-color: #1b1b1b;
                    color: #ffffff;

                    .action{
                        color: white;
                    }

                    svg {
                        fill: white;
                        stroke: white;
                    }
                }

                &.blue {
                    background-color: var(--phone-color-blue);
                    color: #ffffff;
                }

                img {
                    width: 1.5rem;
                    height: 1.5rem;
                }

                svg {
                    font-size: 25px;
                }
            }
        }

        .audioMessage-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 2rem;

            background-color: var(--phone-color-primary);
            width: 100%;
            height: 100%;

            padding: 0 0.75rem;
            padding-top: 1.5rem;
            padding-bottom: 3.3rem;

            transition: all 0.2s ease-in-out;

            .audioMessage-wrapper {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
            }

            .audioMessage-button {
                height: 6rem;
                width: 6rem;
                border-radius: 50%;

                display: flex;
                align-items: center;
                justify-content: center;

                font-size: 40px;

                color: var(--phone-color-red);
                background-color: rgba(160, 32, 25, 0.5);
                border: 1.75rem solid rgb(41, 9, 6);

                cursor: pointer;
                transition: all 0.3s ease-in-out;

                &[data-recording="true"] {
                    background-color: var(--phone-color-blue);
                    border: 1.75rem solid #002243;

                    color: #ffffff;
                }

                &:hover {
                    filter: brightness(0.7);
                }
            }
        }

        .payment-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 2rem;

            background-color: rgb(0, 0, 0);
            width: 100%;
            height: 100%;

            padding: 0 0.75rem;
            padding-top: 1.5rem;
            padding-bottom: 3.3rem;

            transition: all 0.2s ease-in-out;

            .payment-wrapper {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 2rem;

                .button {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    height: 3.5rem;
                    width: 3.5rem;

                    color: #fafafa;
                    font-size: 50px;
                    font-weight: 300;

                    border-radius: 50%;
                    background-color: rgb(0, 0, 0);

                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        background-color: rgb(30, 30, 30);
                    }
                }

                .amount {
                    input {
                        color: #fafafa;

                        background: transparent;
                        border: none;
                        width: 6rem;
                        text-align: center;

                        font-weight: 500;
                        font-size: 45px;

                        &:focus,
                        &:active {
                            outline: none;
                        }

                        &::-webkit-outer-spin-button,
                        &::-webkit-inner-spin-button {
                            -webkit-appearance: none;
                            margin: 0;
                        }
                    }
                }
            }

            .payment-buttons {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 1rem;

                .button {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    height: 2rem;
                    width: 9rem;

                    color: #fafafa;
                    font-size: 22px;
                    font-weight: 400;

                    padding: 0.7rem;

                    border-radius: 15px;
                    background-color: rgb(20, 20, 20);

                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        background-color: rgb(30, 30, 30);
                    }
                }
            }
        }
    }

    .contact {
        display: flex;
        align-items: center;

        gap: 0.4rem;
        padding: 0.4rem 0;

        border-bottom: 1px solid var(--phone-color-border);

        cursor: pointer;

        img {
            width: 2.75rem;
            height: 2.75rem;

            border-radius: 50%;
            object-fit: cover;
        }

        .user {
            display: flex;
            flex-direction: column;

            .name {
                font-weight: 400;
                font-size: 16px;
                color: var(--phone-text-primary);
            }

            .number {
                font-size: 14px;
                color: var(--phone-text-secondary);
            }
        }

        svg {
            margin-left: auto;

            font-size: 27px;
            color: var(--phone-color-blue);
        }
    }

    .group-avatar {
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
        margin-right: 35px;

        img {
            width: 45px;
            height: 45px;

            border-radius: 50%;
            object-fit: cover;

            margin-right: -35px;
            border: 3px solid var(--phone-color-primary);

            &:last-child {
                margin-left: 0;
            }
        }
    }
}
