.animation {
    width: 100%;
    height: 100%;

    &.slideUp {
        animation: slideUp 1s cubic-bezier(0.19, 1, 0.22, 1);
    }

    &.zoomIn {
        animation: zoomIn 0.7s cubic-bezier(0.19, 1, 0.22, 1);
    }

    &.zoomOut {
        animation: zoomOut 0.7s cubic-bezier(0.19, 1, 0.22, 1);
    }
}

@keyframes zoomIn {
    0% {
        transform: scale(0.5);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes zoomOut {
    0% {
        transform: scale(1.5);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes slideDown {
    0% {
        transform: translateY(-20%);
    }

    100% {
        transform: translateY(0%);
    }
}

@keyframes slideUp {
    0% {
        transform: translateY(40%);
    }

    100% {
        transform: translateY(0%);
    }
}

@keyframes slideRight {
    0% {
        transform: translateX(-10%);
    }

    100% {
        transform: translateX(0%);
    }
}

@keyframes slideLeft {
    0% {
        transform: translateX(10%);
    }

    100% {
        transform: translateX(0%);
    }
}

@keyframes appJiggle {
    0% {
        transform: rotate(-1deg);
        animation-timing-function: ease-in;
    }

    50% {
        transform: rotate(1.5deg);
        animation-timing-function: ease-out;
    }
}

@keyframes appJiggle2 {
    0% {
        transform: rotate(1deg);
        animation-timing-function: ease-in;
    }

    50% {
        transform: rotate(-1.5deg);
        animation-timing-function: ease-out;
    }
}

@keyframes widgetJiggle {
    0% {
        transform: rotate(-0.5deg);
        animation-timing-function: ease-in;
    }

    50% {
        transform: rotate(0.5deg);
        animation-timing-function: ease-out;
    }
}

@keyframes widgetJiggle2 {
    0% {
        transform: rotate(0.5deg);
        animation-timing-function: ease-in;
    }

    50% {
        transform: rotate(-0.5deg);
        animation-timing-function: ease-out;
    }
}
