@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

.mail-container {
    position: relative;
    height: 100%;
    padding-top: 3%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background-color: var(--phone-color-primary);

    .slide {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .mail-form {
        width: 70%;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1.5rem;

        .item {
            display: flex;
            flex-direction: column;
            gap: 0.2rem;
            width: 100%;

            .title {
                font-size: 18px;
                color: var(--phone-text-primary);
                font-weight: 500;
            }

            .error {
                font-size: 16px;
                color: var(--phone-color-red);
            }

            .input {
                display: flex;
                flex-direction: row;

                border: 2px solid var(--phone-color-border);
                border-radius: 5px;

                width: 100%;

                input {
                    background: transparent;
                    border: none;

                    padding: 0.5rem;
                    width: 100%;

                    color: var(--phone-text-primary);
                    font-size: 16px;

                    &:focus {
                        outline: none;
                    }
                }

                .suffix {
                    padding: 0.5rem;
                    background-color: var(--phone-color-highlight3);
                    color: var(--phone-text-primary);

                    font-size: 16px;
                    font-weight: 500;
                }
            }
        }

        .button {
            display: flex;
            flex-direction: row;
            justify-content: center;

            padding: 0.5rem 1.25rem;
            border-radius: 7px;

            background-color: var(--phone-color-highlight3);

            color: var(--phone-text-primary);
            font-size: 18px;
            font-weight: 500;

            cursor: pointer;
            transition: 0.2s all ease-in-out;

            &:hover {
                filter: brightness(0.8);
            }
        }

        .footer {
            position: absolute;
            bottom: 3.5rem;

            font-size: 17px;
            color: var(--phone-text-primary);

            span {
                color: var(--phone-color-blue);
                font-weight: 500;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    filter: brightness(0.8);
                }
            }
        }
    }

    .mail-header {
        width: 90%;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0.5rem 0;

        margin-top: 2rem;

        .title {
            color: var(--phone-text-primary);
            font-size: 30px;
            font-weight: 600;
            font-style: bold;
        }

        svg {
            font-size: 30px;

            color: var(--phone-color-blue);

            cursor: pointer;
            &:hover {
                filter: brightness(0.7);
            }
        }

        .left {
            display: flex;
            flex-direction: row;
            align-items: center;
            // gap: 0.2rem;

            font-size: 19px;
            color: var(--phone-color-blue);

            cursor: pointer;
        }
    }

    .searchbox {
        width: 83%;
        z-index: 1;
    }

    .mail-footer {
        position: absolute;
        bottom: 0;

        background-color: var(--phone-color-opacity);
        backdrop-filter: blur(15px);

        width: 100%;

        padding: 1rem 0;
        padding-bottom: 3rem;

        display: flex;
        justify-content: center;

        .footer-wrapper {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            width: 85%;

            .text {
                display: flex;
                flex-direction: column;
                align-items: center;

                .title {
                    font-size: 16px;
                    font-weight: 500;
                    color: var(--phone-text-primary);
                }

                .count {
                    font-size: 15px;
                    font-weight: 400;
                    color: var(--phone-text-secondary);
                }
            }

            svg {
                font-size: 33px;
                color: var(--phone-color-blue);

                cursor: pointer;
            }
        }
    }

    .mail-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        user-select: text;

        height: 50rem;
        background-color: var(--phone-color-highlight);
        padding-bottom: 10rem;

        overflow-y: auto;
        overflow-x: hidden;

        width: 100%;

        &::-webkit-scrollbar {
            display: none;
        }

        .item-header {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;

            border-top: 1px solid var(--phone-color-border);

            width: 88%;
            padding-top: 1rem;
            padding-bottom: 0.75rem;

            .user {
                display: flex;
                flex-direction: column;
                gap: 0.2rem;

                .name {
                    font-size: 20px;
                    font-weight: 500;
                    color: var(--phone-text-primary);
                }

                .to {
                    font-size: 16px;
                    font-weight: 400;
                    color: var(--phone-text-secondary);

                    span {
                        font-weight: 500;
                        color: var(--phone-text-primary);
                    }
                }
            }

            .date {
                font-size: 16px;
                font-weight: 400;
                color: var(--phone-text-secondary);
            }
        }

        .item {
            width: 88%;
            padding: 0.75rem 0;
            border-top: 1px solid var(--phone-color-border);

            color: var(--phone-text-primary);

            &.subject {
                font-size: 20px;
                font-weight: 500;
            }

            &.message {
                height: auto;

                textarea {
                    width: 100%;

                    font-size: 16px;
                    font-weight: 400;
                    color: var(--phone-text-primary);

                    background: transparent;
                    font-family: inherit;
                    border: none;

                    resize: none;
                    overflow: hidden;

                    &:focus {
                        outline: none;
                    }

                    &::-webkit-scrollbar {
                        display: none;
                    }
                }
            }

            &.attachments {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                gap: 0.5rem;

                .attachment,
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;

                    border-radius: 8px;

                    cursor: pointer;
                }
            }

            &.actions {
                padding: 1.5rem 0;

                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                gap: 1.5rem;
                width: 100%;

                .action {
                    padding: 0.75rem 1.5rem;
                    border-radius: 8px;

                    background-color: var(--phone-color-highlight3);

                    font-size: 18px;

                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        filter: brightness(0.9);
                    }
                }
            }
        }
    }

    .mail-list {
        display: flex;
        flex-direction: column;
        max-height: 45rem;

        width: 100%;
        margin-top: 1.5rem;

        overflow-y: auto;
        overflow-x: hidden;

        &::-webkit-scrollbar {
            display: none;
        }

        .mail {
            display: flex;
            flex-direction: column;
            width: 90%;

            padding: 0.75rem 5%;
            padding-bottom: 2rem;

            border-top: 1px solid var(--phone-color-border);

            cursor: pointer;
            transition: 0.2s;
            &:hover {
                background-color: var(--phone-color-hover);
            }

            &:last-child {
                border-bottom: 1px solid var(--phone-color-border);
                margin-bottom: 5rem;
            }

            .item-header {
                display: flex;
                align-items: center;
                justify-content: space-between;

                width: 100%;

                .user {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 0.75rem;

                    .symbol {
                        width: 0.75rem;
                        height: 0.75rem;
                        border-radius: 50%;

                        background: transparent;

                        &[data-read="false"] {
                            background: var(--phone-color-blue);
                        }
                    }

                    .sender {
                        color: var(--phone-text-primary);
                        font-size: 20px;
                        font-weight: 600;
                        line-height: 22px;

                        display: flex;
                        align-items: center;
                        gap: 0.3rem;

                        span {
                            font-size: 16px;
                            font-weight: 400;
                            color: var(--phone-text-secondary);
                        }
                    }
                }

                .date {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 0.5rem;

                    color: var(--phone-text-secondary);
                    font-size: 16px;
                    font-weight: 400;

                    svg {
                        font-size: 22px;
                    }
                }
            }

            .item-body {
                width: 90%;
                display: flex;
                flex-direction: column;
                gap: 0.25rem;

                margin-left: 1.5rem;

                .subject {
                    font-size: 18px;
                    font-weight: 500;
                    color: var(--phone-text-primary);
                    overflow-wrap: break-word;
                }

                .message {
                    font-size: 16px;
                    color: var(--phone-text-secondary);
                    overflow-wrap: break-word;
                }
            }
        }
    }

    .new-mail-container {
        position: absolute;
        bottom: 0;
        z-index: 2;

        height: 97%;
        width: 100%;

        background-color: var(--phone-color-primary);

        display: flex;
        flex-direction: column;
        align-items: center;

        .mail-header {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            width: 90%;

            padding-top: 1.5rem;
            padding: 1rem 5%;

            border-radius: 15px 15px 0 0;

            background-color: var(--phone-color-highlight2);

            .title {
                font-size: 20px;
                font-weight: 500;
                color: var(--phone-text-primary);
                text-align: center;
            }

            .cancel,
            .send {
                font-size: 18px;
                font-weight: 400;
                color: var(--phone-color-blue);

                cursor: pointer;

                &[data-disabled="true"] {
                    color: var(--phone-text-secondary);
                    cursor: not-allowed;
                }
            }
        }

        .mail-options {
            width: 90%;
            height: 80%;

            display: flex;
            flex-direction: column;

            .option {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.25rem;

                padding: 0.75rem 0;
                width: 100%;

                border-top: 1px solid var(--phone-color-border);

                user-select: text;

                .title {
                    font-size: 18px;
                    color: var(--phone-text-secondary);
                }

                &.textarea {
                    flex-direction: column;
                    align-items: inherit;

                    position: relative;
                    max-height: 37rem;

                    .attachments {
                        width: 100%;
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        gap: 0.5rem;

                        .attachment {
                            position: relative;

                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;

                                border-radius: 8px;

                                cursor: pointer;

                                filter: brightness(0.8);
                            }

                            svg {
                                position: absolute;

                                top: 0.5rem;
                                right: 0.5rem;

                                height: 1.5rem;
                                width: 1.5rem;
                                padding: 0.1rem;
                                border-radius: 50%;

                                display: flex;
                                align-items: center;
                                justify-content: center;

                                background-color: rgba(0, 0, 0, 0.8);
                                color: #ffffff;

                                cursor: pointer;
                                font-size: 13px;
                            }
                        }
                    }
                }

                input {
                    width: 70%;

                    font-size: 18px;

                    border: none;
                    background: transparent;

                    color: var(--phone-text-primary);

                    &.blue {
                        color: var(--phone-color-blue);
                    }

                    &:focus {
                        outline: none;
                    }

                    &:disabled {
                        color: var(--phone-text-secondary);
                    }
                }

                textarea {
                    width: 100%;
                    // height: 15rem;

                    background-color: pink;

                    font-size: 18px;
                    color: var(--phone-text-primary);
                    font-family: "Roboto", sans-serif;

                    resize: none;
                    border: none;
                    background: transparent;

                    &:focus {
                        outline: none;
                    }

                    &:disabled {
                        color: var(--phone-text-secondary);
                    }

                    &::-webkit-scrollbar {
                        display: none;
                    }
                }
            }
        }
    }
}
