@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

.settings-container {
    position: relative;
    overflow-y: scroll;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    height: 100%;

    background-color: var(--phone-color-primary);

    &::-webkit-scrollbar {
        display: none;
    }

    .animation-slide {
        width: 100%;
        height: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;

        gap: 1rem;

        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .title {
        margin-top: 4rem;

        text-align: center;
        font-size: 22px;
        font-weight: 600;
        font-style: bold;
        white-space: nowrap;
        color: var(--phone-text-primary);
    }

    .top {
        margin-top: 4rem;

        width: 90%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        div {
            flex: 1 1 0px;
        }

        .back {
            display: flex;
            flex-direction: row;
            align-items: center;
            cursor: pointer;

            color: var(--phone-color-blue);
            font-size: 17px;
            gap: 0.4rem;

            &:hover {
                filter: brightness(90%);
            }

            svg {
                font-size: 22px;
                margin-right: -0.45rem;
            }
        }

        .title {
            margin: 0;
            color: var(--phone-text-primary);
        }

        .value {
            color: var(--phone-color-blue);
            font-size: 17px;

            text-align: right;
            cursor: pointer;

            &:hover {
                filter: brightness(90%);
            }
        }
    }

    .searchbox {
        width: 80%;
    }

    .profile {
        margin-bottom: 1rem;
        background-color: var(--phone-color-highlight);

        width: 85%;
        display: flex;
        flex-direction: row;
        align-items: center;

        padding: 0.75rem 0.75rem;
        border-radius: 10px;
        gap: 0.75rem;
        cursor: pointer;

        .profile-image {
            display: flex;

            width: 4rem;
            height: 4rem;
            border-radius: 50%;

            justify-content: center;
            align-items: center;
            font-size: 24px;
            font-weight: 400;

            background-color: #bbbbbd;
        }

        .profile-info {
            display: flex;
            flex-direction: column;
            color: var(--phone-text-primary);

            .name {
                font-size: 24px;
                font-weight: 400;
            }

            .info {
                font-size: 13px;
                font-weight: 400;
            }
        }

        svg {
            margin-left: auto;
            margin-right: 0.5rem;
            font-size: 25px;

            color: var(--phone-text-primary);
        }
    }

    .category-title {
        width: 75%;
        font-size: 13px; //hmm
        color: #88888c;
        text-transform: uppercase;
        text-align: left;

        margin-bottom: -0.5rem;
    }

    section {
        margin-bottom: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;

        background-color: var(--phone-color-highlight);

        width: 90%;
        border-radius: 10px;

        .settings-item {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            padding: 0.75rem 1rem;

            border-width: 1rem;

            border-bottom: 1px solid var(--phone-color-border);

            &:hover {
                filter: brightness(90%);
            }

            &:last-child {
                border: none;
            }

            input {
                font-size: 19px;
                text-align: right;
                border: none;
                background: transparent;
                outline: none;
                color: var(--phone-text-secondary);

                &:focus {
                    outline: none;
                }

                &::placeholder {
                    color: var(--phone-text-secondary);
                }
            }

            .wallpapers {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 1rem;

                position: relative;
                width: 100%;

                margin: 0 1rem;

                .preview {
                    width: 50%;
                    border-radius: 15px;
                    height: 18rem;

                    background-position: center;
                    background-size: cover;
                    background-repeat: no-repeat;

                    .lockscreen-container {
                        width: 100%;

                        display: flex;
                        flex-direction: column;
                        align-items: center;

                        margin-top: 1rem;

                        color: #ffffff;

                        .time {
                            text-align: center;
                            font-size: 28px;
                            height: auto;
                        }

                        .date {
                            width: 80%;
                            font-size: 9px;
                            text-align: center;
                        }

                        svg {
                            color: #ffffff;
                            font-size: 15px;
                            margin-bottom: 0.4rem;
                        }
                    }

                    .homescreen-container {
                        height: 100%;
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        position: relative;

                        .homescreen-apps {
                            display: grid;
                            grid-template-columns: repeat(4, 1fr);
                            grid-template-rows: repeat(4, 1fr);
                            gap: 0.3rem 0rem;
                            margin-top: 1.8rem;

                            height: auto;
                        }

                        .upper {
                            display: flex;
                            width: 92%;
                            position: relative;
                            margin-top: 0.2rem;

                            .header {
                                font-size: 8px;
                                display: flex;
                                align-items: center;

                                width: 100%;
                                padding: 0;

                                .right {
                                    display: flex;
                                    align-items: center;
                                    margin-right: 0.5rem;
                                    .top {
                                        margin-top: unset;

                                        .service {
                                            div {
                                                &:nth-child(1) {
                                                    height: 0.2rem;
                                                }

                                                &:nth-child(2) {
                                                    height: 0.3rem;
                                                }

                                                &:nth-child(3) {
                                                    height: 0.4rem;
                                                }

                                                &:nth-child(4) {
                                                    height: 0.5rem;
                                                }
                                            }
                                        }

                                        .battery-container {
                                            gap: 0.25rem;

                                            .battery {
                                                width: 1rem;
                                                height: 0.4rem;
                                                border: 1px solid rgba(255, 255, 255, 0.5);
                                            }
                                        }
                                    }

                                    svg {
                                        margin: 0;
                                        font-size: 12px;
                                    }
                                }
                            }
                        }

                        .favourite-apps {
                            position: absolute;
                            bottom: 0.5rem;

                            display: flex;
                            flex-direction: row;
                            justify-content: center;
                            gap: 0.5rem;

                            border-radius: 10px;
                            padding: 0.4rem;
                            background-color: $opacity-color-light;
                            width: 80%;
                        }

                        .homescreen-app {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            gap: 0.2rem;

                            img {
                                height: 1.4rem;
                                width: 1.4rem;
                                border-radius: 5px;
                            }

                            .app-name {
                                font-size: 6px;
                                font-weight: 200;
                                color: #ffffff;
                            }
                        }
                    }
                }
            }

            .setting {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;
                justify-content: left;

                .title {
                    font-size: 18px;
                    font-weight: 300;

                    color: var(--phone-text-primary);

                    &.blue {
                        color: var(--phone-color-blue);
                        background: none;
                        font-weight: 400;
                    }

                    &.red {
                        color: var(--phone-color-red);
                        background: none;
                        font-weight: 400;
                    }

                    &.grey {
                        color: var(--phone-text-secondary);
                        background: none;
                        font-weight: 400;
                    }

                    margin: 0;
                }

                img {
                    width: 2rem;
                    height: 2rem;
                    border-radius: 7px;
                }

                svg:not(.checkmark, .remove) {
                    margin-left: -0.1rem;
                    margin-right: 0.25rem;

                    color: #ffffff;
                    font-size: 30px;
                    width: 1.1rem;
                    height: 1.1rem;

                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 0.5rem;
                    border-radius: 10px;
                }

                .remove {
                    padding: 0.2rem;
                    font-size: 30px;

                    width: 1.75rem;
                    height: 1.75rem;

                    color: var(--phone-color-red);
                    cursor: pointer;
                    margin-left: 0;
                }

                .hidden {
                    visibility: hidden;
                }

                .orange {
                    background-color: #ffa338;
                }

                .blue {
                    background-color: #348de9;
                }

                .darkblue {
                    background-color: #1a3fd4;
                }

                .lightblue {
                    background-color: #5aa7d7;
                }

                .green {
                    background-color: #57d26e;
                }

                .red {
                    background-color: #ff5252;
                }

                .pink {
                    background-color: #f5315e;
                }

                .purple {
                    background-color: #5958c0;
                }

                .grey {
                    background-color: #8e8e92;
                }
            }

            .setting-value {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;

                color: var(--phone-text-secondary);

                .value {
                    font-size: 18px;
                    font-weight: 300;
                }

                svg {
                    font-size: 22px;
                    cursor: pointer;
                }
            }

            svg {
                margin-left: 0.5rem;
                margin-right: 0.5rem;
                font-size: 28px;
                color: var(--phone-text-secondary);

                &.checkmark {
                    margin: 0;
                    font-size: 35px;
                    color: var(--phone-color-blue);

                    &.hidden {
                        visibility: hidden;
                    }
                }
            }

            .chevron {
                font-size: 20px;
                margin: 0;
                margin-right: -0.4rem;
            }
        }
    }

    .wallpapers-container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 0.25rem;

        margin-top: 1rem;
        padding-bottom: 5rem;

        .wallpaper {
            width: 7.8rem;
            height: 18rem;

            cursor: pointer;

            transition: all 0.2s ease;
            border: 3px solid transparent;

            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;

            &.add {
                display: flex;
                align-items: center;
                justify-content: center;

                background-color: var(--phone-highlight-opacity55);

                svg {
                    font-size: 50px;
                    color: var(--phone-text-primary);
                    opacity: 0.8;
                }
            }

            &:hover:not(.add),
            &.active,
            &:focus {
                opacity: 0.75;

                border: 3px solid var(--phone-color-blue);
                border-radius: 2px;
            }
        }
    }

    .appearance-container {
        width: 100%;
        display: flex;
        flex-direction: column;

        .appearance-switches {
            display: flex;

            flex-direction: row;
            align-items: center;
            justify-content: center;
            gap: 4rem;

            .mode {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                gap: 0.5rem;
                cursor: pointer;

                &:hover {
                    filter: brightness(95%);
                }

                img {
                    width: 5rem;
                }

                .mode-text {
                    font-size: 18px;
                    font-weight: 400;
                    color: var(--phone-text-primary);
                }
            }
        }
    }

    .passcode-container {
        height: 100%;
        width: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;

        .pin {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 80%;
            gap: 1rem;

            margin-top: 15rem;

            font-size: 18px;

            color: var(--phone-text-primary);
        }

        .numbers {
            position: absolute;
            bottom: 0;
            width: 90%;

            padding: 2rem;
            padding-top: 1rem;

            border-radius: 30px;

            background-color: var(--phone-color-highlight3);
        }
    }

    input {
        color: var(--phone-color-input);

        ::placeholder {
            color: var(--phone-color-input);
        }
    }
}
