@import "../../../../styles/Colors.scss";

.camera-container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: #000000;

    .camera-header {
        margin-top: 10%;

        display: flex;
        align-items: center;
        justify-content: space-between;

        width: 80%;

        height: 7.5%;

        div {
            flex: 1;
        }

        svg {
            color: #ffffff;
            font-size: 20px;

            border: 2px solid #ffffff;
            border-radius: 50%;
            padding: 0.1rem;

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:hover {
                filter: brightness(0.5);
            }
        }

        .timer {
            color: #ffffff;
            font-size: 22px;
            text-align: center;
        }

        span {
            flex: 1;
        }
    }

    .camera-body {
        width: 90%;
        height: 65%;

        display: flex;
        align-items: center;
        justify-content: center;

        overflow: hidden;
        position: relative;

        .loading {
            position: absolute;
            height: 100%;
            width: 100%;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 1rem;

            .uploading {
                font-size: 18px;
                font-weight: 500;
                color: #ffffff;

                text-align: center
            }

            backdrop-filter: brightness(0.8) blur(5px);
        }

        canvas {
            max-height: 100%;
        }

        &.rotate {
            canvas {
                transform: rotate(90deg);
            }
        }

        &.selfie {
            canvas {
                transform: translate(15%, 0);
            }
        }
    }

    .camera-bottom {
        width: 90%;
        display: flex;
        flex-direction: column;
        align-items: center;

        margin-top: 1rem;

        overflow: hidden;

        .camera-types {
            display: flex;

            align-items: center;
            gap: 1.25rem;

            transition: all 0.2s ease-in-out;

            margin-left: 3rem;

            div {
                text-transform: uppercase;
                cursor: pointer;

                color: #fafafa;
                font-weight: 400;
                font-size: 17px;

                &.active {
                    color: #e8c438;
                    font-weight: 500;
                }
            }
        }

        .camera-buttons {
            margin-top: 1rem;

            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 5rem;

            .image-gallery {
                width: 3.5rem;
                height: 3.5rem;
                border-radius: 4px;

                cursor: pointer;

                background-position: center;
                background-size: cover;

                background-color: rgb(15, 15, 15);
            }

            .camera-button {
                .camera-button-container {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    width: 4.5rem;
                    height: 4.5rem;
                    border-radius: 50%;

                    border: 4px solid white;

                    .camera-button-inner {
                        height: 91.5%;
                        width: 91.5%;

                        border-radius: 50%;

                        background-color: white;

                        cursor: pointer;

                        transition: all 0.2s ease-in-out;

                        &.Video {
                            background-color: var(--phone-color-red);
                        }

                        &.Recording {
                            border-radius: 10%;
                            height: 50%;
                            width: 50%;
                        }

                        &:hover:not(.Recording) {
                            height: 87%;
                            width: 87%;
                            opacity: 0.9;
                        }
                    }
                }
            }

            .flip-camera {
                display: flex;
                align-items: center;
                justify-content: center;

                height: 3.3rem;
                width: 3.3rem;

                background-color: rgb(15, 15, 15);
                color: #d8d8d7;
                font-size: 15px;

                border-radius: 50%;

                cursor: pointer;

                svg {
                    font-size: 30px;
                }
            }
        }
    }
}
