@import "../../../../styles/Colors.scss";

.weather-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding-top: 15%;

    background-size: cover;
    background-repeat: no-repeat;

    .weather-info {
        margin-top: 3rem;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        color: $white-primary;

        text-shadow: 1px 1px 7px rgba(0, 0, 0, 0.25);

        .location {
            font-size: 33px;
            font-weight: 400;
        }

        .temperature {
            width: 70%;

            font-size: 90px;
            font-weight: 300;
            word-wrap: normal;
            text-shadow: 1px 1px 7px rgba(0, 0, 0, 0.25);
        }

        .weather-type {
            font-size: 20px;
            font-weight: 400;
        }
    }

    .hourly-forecast {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 80%;

        border-radius: 20px;

        margin-top: 2.5rem;

        .info-header {
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 0.3rem;

            margin: 1rem;

            color: #ffffff84;

            font-size: 13px;
            letter-spacing: 0.5px;
            font-weight: 400;
        }

        .content {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            margin: 0 1rem;
            margin-bottom: 1rem;

            .item {
                display: flex;
                flex-direction: column;

                align-items: center;
                justify-content: center;
                gap: 0.8rem;

                color: #ffffff;

                img {
                    width: 26px;
                    height: 28px;
                }

                .time {
                    font-size: 15px;
                    font-weight: 400;

                    span {
                        font-size: 12px;
                    }
                }

                .temp {
                    font-size: 18px;
                    font-weight: 400;
                }
            }
        }
    }

    .details {
        margin-top: 1rem;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;

        .item {
            position: relative;
            display: flex;
            flex-direction: column;

            border-radius: 15px;

            height: 8rem;
            width: 8rem;

            padding: 1rem;

            .item-header {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.3rem;

                color: #ffffff84;
                text-transform: uppercase;

                font-size: 13px;
                letter-spacing: 0.5px;
                font-weight: 400;

                svg {
                    color: #ffffff84;
                    font-size: 15px;
                }
            }

            .item-content {
                color: #ffffff;
                font-size: 30px;
                margin-top: 0.5rem;

                span {
                    font-size: 15px;
                }
            }

            img {
                position: absolute;

                left: 0;
                right: 0;
                top: 20px;
                bottom: 0;

                margin: auto;

                width: 85px;
                height: 85px;
            }

            .wind-data {
                position: absolute;
                left: 41%;
                top: 45%;

                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0;
                margin: 0;
                margin-bottom: 0;

                color: #ffffff;
                font-size: 16px;

                span {
                    font-size: 12px;
                    font-weight: 300;
                    margin: 0;
                }
            }

            .item-subcontent {
                color: #ffffff;
                font-size: 14px;
            }

            .item-footer {
                width: 80%;
                margin-top: auto;

                font-size: 13px;
                color: $white4;

                span {
                    font-size: 7px;
                }
            }
        }
    }
}
