@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

.wallet-app-container {
    display: flex;
    flex-direction: column;

    height: 100%;
    overflow-y: auto;

    &::-webkit-scrollbar {
        display: none;
    }

    background-color: var(--phone-color-primary);

    .overview {
        display: flex;
        flex-direction: column;

        padding: 4rem 2rem;

        .header-title {
            color: var(--phone-text-primary);

            font-size: 30px;
            font-weight: 600;
            font-style: bold;
        }

        .top {
            width: 90%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            .back {
                display: flex;
                flex-direction: row;
                align-items: center;
                cursor: pointer;

                color: var(--phone-color-blue);
                font-size: 17px;
                gap: 0.4rem;

                i {
                    font-size: 22px;
                }
            }

            .title {
                text-align: center;
                font-size: 22px;
                font-weight: 600;
                font-style: bold;
                white-space: nowrap;
                margin-left: 1rem;
            }

            div {
                flex: 1;
            }
        }

        .wrapper {
            display: flex;
            flex-direction: column;
            gap: 1rem;

            &.center {
                justify-content: center;
                align-items: center;
                gap: 2rem;
                height: 85vh;
            }

            .card {
                margin-top: 1rem;
                height: 14rem;

                background-image: url("../../../../../public/assets/img/card.png");
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
            }

            section {
                display: flex;
                flex-direction: column;

                background-color: var(--phone-color-highlight);

                border-radius: 10px;
                padding: 1rem;

                &.input {
                    padding: 1.5rem;
                }

                .item {
                    display: flex;
                    flex-direction: column;
                    gap: 0.4rem;

                    &.space {
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        align-items: center;

                        width: 100%;
                    }

                    .title {
                        font-size: 17px;
                        font-weight: 500;

                        &.big {
                            font-size: 25px;
                        }
                    }

                    .button {
                        background-color: var(--phone-text-primary);
                        color: var(--phone-color-primary);
                        padding: 0.5rem 1.25rem;
                        border-radius: 20px;

                        font-size: 16px;

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            opacity: 0.8;
                        }
                    }

                    .balance {
                        width: auto;
                        display: flex;
                        flex-direction: column;
                        gap: 0.5rem;

                        color: var(--phone-text-primary);
                    }

                    .input {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 1rem;

                        color: var(--phone-color-highlight);

                        // background-color: rgb(226, 226, 226);
                        padding: 0.5rem 0.75rem;
                        border-radius: 5px;

                        input {
                            border: none;
                            background: transparent;
                            font-size: 15px;

                            &:active,
                            &:focus {
                                outline: none;
                            }
                        }
                    }

                    .value {
                        font-size: 25px;
                        font-weight: 700;
                    }

                    .date {
                        font-size: 15px;
                        font-weight: 400;
                    }

                    .link {
                        cursor: pointer;
                        transition: all 0.2s ease-in-out;
                        &:hover {
                            transform: scale(1.02);
                        }

                        font-size: 18px;
                        font-weight: 500;
                        color: var(--phone-color-blue);
                    }

                    &.multi {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        width: 100%;
                        padding: 1rem 0;

                        border-top: 2px solid var(--phone-color-border);

                        &:first-child {
                            border: none;
                            padding-top: 0;
                        }

                        &:last-child {
                            padding-bottom: 0;
                        }

                        img {
                            width: 3rem;
                            height: 3rem;
                            border-radius: 5px;
                        }

                        .image {
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            width: 3.5rem;
                            height: 3.1rem;
                            border-radius: 50%;

                            background-color: #bbbbbd;
                            color: $white-primary;

                            font-size: 20px;
                            font-weight: 400;
                        }

                        .info {
                            display: flex;
                            flex-direction: column;
                            gap: 0.25rem;
                            width: 100%;

                            .transaction-header {
                                display: flex;
                                flex-direction: row;
                                align-items: center;
                                justify-content: space-between;

                                .title {
                                    font-size: 17px;
                                    font-weight: 600;

                                    color: var(--phone-text-primary);
                                }

                                .right {
                                    display: flex;
                                    flex-direction: row;
                                    align-items: center;
                                    gap: 0.5rem;

                                    .amount {
                                        font-size: 18px;
                                        font-weight: 500;

                                        &.green {
                                            color: var(--phone-color-green);
                                        }

                                        &.red {
                                            color: var(--phone-color-red);
                                        }
                                    }

                                    i {
                                        font-size: 18px;
                                    }
                                }
                            }

                            .date {
                                color: var(--phone-text-secondary);
                                font-size: 15px;
                                font-weight: 400;
                            }
                        }

                        cursor: pointer;
                    }
                }
            }

            .subtitle {
                display: flex;
                align-items: center;
                justify-content: space-between;

                color: var(--phone-text-primary);

                .title {
                    font-size: 22px;
                    font-weight: 700;
                }

                .link {
                    font-size: 15px;
                    font-weight: 500;
                    color: var(--phone-color-blue);

                    cursor: pointer;
                }
            }
        }
    }

    .send-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10rem;

        height: 94%;
        width: 100%;

        position: absolute;
        z-index: 1;
        top: 3rem;

        background-color: var(--phone-color-highlight2);
        animation: slideUp 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;

        .send-header {
            margin-top: 1.5rem;
            width: 88%;

            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            div {
                font-size: 18px;
                color: var(--phone-color-blue);

                cursor: pointer;

                &.disabled {
                    color: var(--phone-text-secondary);
                    cursor: not-allowed;
                }
            }
        }

        .send-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 90%;

            .success {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.5rem;

                .title {
                    color: var(--phone-text-primary);
                    font-size: 20px;
                    font-weight: 400;
                }

                .subtitle {
                    color: var(--phone-text-secondary);
                    font-size: 15px;
                    font-weight: 400;
                }

                svg {
                    font-size: 80px;
                    color: var(--phone-color-blue);
                }
            }

            .amount,
            .user {
                display: flex;
                flex-direction: row;
                justify-content: center;
                gap: 0.25rem;

                width: 100%;

                .symbol {
                    font-size: 35px;
                    font-weight: 700;
                    text-align: center;

                    color: var(--phone-text-primary);
                }

                input {
                    border: none;
                    background: transparent;
                    color: var(--phone-text-primary);

                    font-size: 65px;
                    text-align: center;

                    width: 10rem;

                    &.number_input {
                        width: 20rem;
                        font-size: 45px;
                    }

                    &::-webkit-outer-spin-button,
                    &::-webkit-inner-spin-button {
                        -webkit-appearance: none;
                    }

                    &:active,
                    &:focus {
                        outline: none;
                    }
                }
            }

            .description {
                margin-top: 1rem;

                font-size: 16px;
                color: var(--phone-text-secondary);

                text-align: center;
            }

            .error {
                margin-top: 1rem;
                font-size: 16px;

                color: var(--phone-color-red);
            }
        }

        .numpad-wrapper {
            width: 90%;

            position: absolute;
            bottom: 3rem;
        }
    }
}
