@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

@font-face {
    font-family: "Tinder";
    src: url("../../../../../public/assets/fonts/Tinder.woff2");
}

@keyframes scaleUp {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}

.tinder-container {
    height: 100%;
    width: 100%;

    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    font-family: Tinder;
    background-color: #ffffff;

    .slide {
        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .loading {
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        background-color: #f1f1f1;
    }

    .home-wrapper {
        height: 100%;
        width: 90%;
        padding: 0 5%;

        display: flex;
        flex-direction: column;
        align-items: center;

        .message-header {
            width: 94%;
            margin-top: 3rem;
            padding: 1rem 0;

            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 1.5rem;

            border-bottom: 1px solid var(--phone-color-border);

            svg {
                color: var(--tinder-color-mix);
                font-size: 27px;

                cursor: pointer;
            }

            .user {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;
                cursor: pointer;

                .profile-picture {
                    width: 2.5rem;
                    height: 2.5rem;

                    border-radius: 50%;
                    object-fit: cover;
                }

                .name {
                    font-size: 20px;
                    font-weight: 600;
                }
            }
        }

        .message-container {
            display: relative;

            display: flex;
            flex-direction: column-reverse;
            height: 95%;

            width: 100%;

            overflow-y: auto;
            overflow-x: hidden;

            scroll-behavior: smooth;

            &::-webkit-scrollbar {
                display: none;
            }
        }

        .message-body {
            width: 100%;

            display: flex;
            flex-direction: column;

            margin-top: 1rem;
            padding: 1rem 0;
            gap: 0.5rem;

            .message {
                font-size: 16px;
                font-weight: 300;
                width: auto;
                max-width: 70%;

                color: rgb(15, 20, 25);

                display: flex;
                flex-direction: column;
                gap: 0.5rem;

                .content {
                    a {
                        color: var(--phone-color-blue);
                        cursor: pointer;
                    }
                }

                .attatchments {
                    img,
                    video {
                        width: 100%;
                        border-radius: 15px;
                    }
                }

                .profile-picture {
                    img {
                        width: 2.5rem;
                        height: 2.5rem;

                        border-radius: 50%;

                        object-fit: cover;
                        object-position: center;
                    }
                }

                .date {
                    padding: 0 0.5rem;
                    font-size: 13px;
                    font-weight: 300;
                    color: #687684;
                }

                &.self {
                    align-items: flex-end;
                    margin-left: auto;

                    .date {
                        text-align: right;
                        margin-left: auto;
                    }

                    .content {
                        padding: 0.75rem 1rem;
                        border-radius: 15px;

                        background-color: #00b3ff;
                        color: $white3;

                        overflow-wrap: break-word;
                        font-size: 18px;
                    }
                }

                &.other {
                    margin-right: auto;

                    .message-with-pfp {
                        display: flex;
                        flex-direction: row;
                        align-items: flex-end;
                        gap: 0.6rem;
                    }

                    .message-content {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;

                        .content {
                            padding: 0.75rem 1rem;
                            border-radius: 18px;

                            background-color: #e5e5e5;
                            color: $black-primary;

                            overflow-wrap: break-word;

                            &:not(img) + .other {
                                background-color: transparent;
                                padding: 0;
                            }
                        }

                        .date {
                            margin-left: 3rem;
                            margin-right: auto;
                        }
                    }
                }
            }
        }

        .attachments {
            position: absolute;
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 0.5rem;

            bottom: 5.5rem;
            left: 2rem;

            .attachment {
                position: relative;

                height: 5rem;
                width: 5rem;

                img,
                video {
                    width: 100%;
                    height: 100%;

                    object-fit: cover;
                    border-radius: 10px;
                }

                svg {
                    position: absolute;

                    top: 0.25rem;
                    right: 0.25rem;

                    height: 1.3rem;
                    width: 1.3rem;
                    padding: 0.1rem;
                    border-radius: 50%;

                    display: flex;
                    align-items: center;
                    justify-content: center;

                    background-color: rgba(0, 0, 0, 0.8);
                    color: #ffffff;

                    cursor: pointer;
                    font-size: 13px;
                }
            }
        }

        .message-bottom {
            width: 95%;
            padding-bottom: 2rem;

            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 0.5rem;

            .image {
                padding: 0.4rem 0.5rem;
                height: 1.8rem;
                width: 1.8rem;
                border-radius: 50%;
                background-color: #00b3ff;
                color: #ffffff;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    filter: brightness(0.8);
                }
            }

            .input {
                display: flex;
                flex-direction: row;
                align-items: center;
                height: 100%;
                border-radius: 20px;

                padding: 0 0.5rem;

                background-color: #e5e5e5;
                width: 100%;

                input {
                    border: none;
                    background: transparent;
                    width: 88%;

                    font-family: "Tinder";
                    font-size: 16px;

                    &:focus {
                        outline: none;
                    }
                }

                svg {
                    color: #838383;
                    font-size: 24px;

                    cursor: pointer;

                    &.active {
                        color: #00b3ff;
                    }
                }
            }
        }

        .searchbox {
            margin-top: 1rem;
        }

        .message-list-body {
            margin-top: 1rem;
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 1.5rem;

            overflow-y: scroll;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                display: none;
            }

            padding-bottom: 2rem;

            .title {
                color: var(--tinder-color-mix);
                text-transform: uppercase;
                font-size: 18px;
                font-weight: 600;
                letter-spacing: 0.75px;

                span {
                    margin-left: 0.25rem;
                    border-radius: 15px;
                    padding: 0.15rem 0.5rem;

                    font-weight: 400;
                    color: #ffffff;
                    background-color: var(--tinder-color-mix);
                }
            }

            .profile-picture {
                width: 4.75rem;
                height: 4.75rem;
                border-radius: 50%;
                object-fit: cover;
            }

            .recent-messages {
                display: flex;
                flex-direction: column;
                gap: 1rem;

                .messages {
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;

                    .message {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 0.75rem;

                        cursor: pointer;

                        .user {
                            display: flex;
                            flex-direction: column;
                            gap: 0.2rem;

                            .name {
                                font-size: 20px;
                                font-weight: 600;
                            }

                            .message {
                                color: #3b3b3b;
                                font-size: 19px;
                            }
                        }
                    }
                }
            }

            .new-matches {
                display: flex;
                flex-direction: column;
                gap: 1rem;
                margin-top: 0.5rem;

                padding-bottom: 1rem;
                border-bottom: 1px solid var(--phone-color-border);

                .matches {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 0.5rem;

                    width: 100%;

                    overflow-x: auto;
                    overflow-y: hidden;

                    padding-bottom: 1rem;

                    &::-webkit-scrollbar {
                        height: 5px;
                    }

                    &::-webkit-scrollbar-track {
                        background: #f1f1f1;
                    }

                    &::-webkit-scrollbar-thumb {
                        background-color: var(--tinder-color-mix);
                    }

                    &::-webkit-scrollbar-thumb:hover {
                        filter: brightness(0.8);
                    }

                    .new-match {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        gap: 0.25rem;

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;
                        &:hover {
                            filter: brightness(0.8);
                        }

                        .name {
                            font-size: 18px;
                            font-weight: 500;
                        }
                    }
                }
            }
        }

        .match {
            position: absolute;
            z-index: 99;

            height: 102%;
            width: 100%;

            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;

            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;

            animation: scaleUp 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);

            .match-image {
                margin-top: 17rem;
                width: 80%;
            }

            .likes-too {
                color: #ffffff;
                font-size: 21px;
                font-style: italic;
            }

            .input {
                display: flex;
                flex-direction: row;
                align-items: center;

                width: 75%;
                height: 2.5rem;

                background-color: #ffffff;
                border-radius: 10px;
                padding: 0.5rem 1rem;

                input {
                    background: transparent;
                    border: none;
                    height: 100%;

                    flex-grow: 1;

                    font-family: "Tinder";
                    font-size: 17px;
                    color: #000000;

                    &:focus {
                        outline: none;
                    }
                }

                .send {
                    text-align: right;
                    color: var(--phone-color-blue);
                    font-size: 19px;
                    text-transform: uppercase;

                    cursor: pointer;
                }
            }

            .keep-swiping {
                margin-top: 4rem;

                font-size: 16px;
                color: #ffffff;
                font-weight: 300;
                letter-spacing: 1px;
                text-transform: uppercase;

                cursor: pointer;
            }
        }

        .profile-body {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;

            overflow-y: auto;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                display: none;
            }

            padding-bottom: 5rem;

            .user {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.75rem;

                margin-top: 2rem;

                .profile-picture {
                    width: 10rem;
                    height: 10rem;
                    border-radius: 50%;
                    object-fit: cover;
                }

                .name {
                    color: #000000;
                    font-size: 26px;
                    font-weight: 600;
                }
            }

            .inputs {
                margin-top: 2rem;

                display: flex;
                flex-direction: column;
                align-items: center;

                gap: 1.25rem;
                width: 100%;

                .input {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    gap: 0.25rem;
                    width: 100%;

                    &.center {
                        align-items: center;
                    }

                    .label {
                        width: 100%;
                        font-size: 20px;
                        color: #000000;
                    }

                    input {
                        width: 100%;
                        border: none;
                        height: 3rem;

                        padding: 0.5rem;

                        font-family: Tinder;
                        color: #202020;
                        font-size: 18px;

                        &:focus {
                            outline: none;
                        }
                    }

                    textarea {
                        width: 100%;
                        border: none;
                        height: 5rem;

                        padding: 0.5rem;

                        font-family: Tinder;
                        color: #202020;
                        font-size: 18px;

                        resize: none;

                        &:focus {
                            outline: none;
                        }
                    }

                    select {
                        width: 100%;
                        height: 3rem;
                        padding: 0.5rem;

                        border: none;
                        border-radius: 10px;

                        font-family: Tinder;
                        color: #202020;
                        font-size: 18px;

                        &:focus {
                            outline: none;
                        }

                        &:focus::-ms-value {
                            background: transparent;
                            color: inherit;
                            outline-style: dotted;
                            outline-width: thin;
                        }

                        option {
                            font-family: Tinder;
                            color: #202020;
                            font-size: 13px;
                        }
                    }
                }

                .photo-grid {
                    display: grid;
                    grid-template-columns: repeat(3, 1fr);
                    grid-gap: 0.5rem;

                    margin-top: 1rem;
                    width: 80%;

                    .photo {
                        height: 9rem;
                        width: 100%;
                        position: relative;

                        background-color: #eaedf2;
                        border-radius: 7px;

                        background-position: center;
                        background-size: cover;
                        background-repeat: no-repeat;

                        border: 1px dashed #a2a2a2;

                        .photo-button {
                            position: absolute;
                            bottom: -0.25rem;
                            right: -0.25rem;

                            width: 1.7rem;
                            height: 1.7rem;

                            border-radius: 50%;

                            display: flex;
                            align-items: center;
                            justify-content: center;

                            svg {
                                font-size: 22px;
                            }

                            &.add {
                                background: linear-gradient(45deg, var(--tinder-color-pink), var(--tinder-color-orange));
                                svg {
                                    color: #ffffff;
                                }
                            }

                            &.remove {
                                background: #ffffff;
                                svg {
                                    color: var(--tinder-color-pink);
                                }
                            }
                        }
                    }
                }
            }

            .button {
                width: 90%;
                padding: 1rem 0;
                border-radius: 25px;

                margin-top: 1.5rem;

                display: flex;
                align-items: center;
                justify-content: center;

                text-transform: uppercase;
                font-size: 18px;
                font-weight: 800;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    transform: scale(0.97);
                }

                &.gradient {
                    background: linear-gradient(45deg, var(--tinder-color-pink), var(--tinder-color-orange));
                    color: #ffffff;
                }
            }
        }

        .home-header {
            margin-top: 4rem;
            width: 96%;
            display: flex;
            justify-content: space-between;
            align-items: center;

            img {
                width: 1.5rem;
                height: 1.5rem;

                cursor: pointer;

                &.logo {
                    width: 2.5rem;
                    height: 2.5rem;
                }
            }
        }

        .home-body {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 2rem;
            margin-top: 1.5rem;

            .cards {
                width: 24rem;
                height: 35rem;

                .swipe {
                    position: absolute;
                }

                .card {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: flex-end;

                    gap: 1rem;

                    background-position: center;
                    background-size: cover;
                    background-repeat: no-repeat;

                    background-color: #ffffff;

                    width: 24rem;
                    height: 35rem;
                    border-radius: 12px;
                    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

                    &.no-more {
                        position: absolute;
                        align-items: center;
                        justify-content: center;

                        div {
                            width: 80%;
                            font-size: 20px;
                            text-align: center;

                            overflow-wrap: break-word;
                        }
                    }

                    .image-count {
                        position: absolute;
                        z-index: 1;
                        top: 0.5rem;
                        left: 0;
                        right: 0;
                        margin: 0 auto;

                        height: 0.5rem;
                        width: 90%;

                        display: flex;
                        align-items: center;
                        gap: 0.25rem;

                        .image {
                            width: 100%;
                            background-color: #dad9d9;
                            opacity: 0.6;
                            height: 0.2rem;

                            border-radius: 5px;
                            cursor: pointer;

                            &.active {
                                opacity: 1;
                            }
                        }
                    }

                    .card-content {
                        margin: 1rem;

                        display: flex;
                        flex-direction: column;
                        gap: 0.3rem;
                        width: 90%;

                        .name {
                            font-size: 38px;
                            font-weight: 600;
                            color: #ffffff;

                            span {
                                font-weight: 400;
                                font-size: 28px;
                                opacity: 0.8;
                            }
                        }

                        .description {
                            color: #ffffff;
                            opacity: 0.9;
                            font-size: 20px;

                            overflow-wrap: break-word;
                        }
                    }
                }
            }

            .actions {
                // width: 30%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 1.5rem;

                .button {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.16);

                    height: 4.5rem;
                    width: 4.5rem;
                    border-radius: 50%;

                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        transform: scale(1.1);
                    }

                    img {
                        width: 1.8rem;
                        height: 1.8rem;
                    }

                    &.small {
                        height: 4rem;
                        width: 4rem;

                        img {
                            width: 1.6rem;
                            height: 1.6rem;
                        }
                    }

                    &.disabled {
                        cursor: not-allowed;
                    }
                }
            }
        }
    }

    .login-container {
        height: 100%;
        width: 80%;
        padding: 0 10%;
        // background: linear-gradient(45deg, #fd267a, #ff6036);

        display: flex;
        flex-direction: column;
        align-items: center;

        .logo {
            margin-top: 20rem;
            color: #ffffff;
            width: 13rem;
        }

        .create-account {
            height: 100%;
            width: 90%;
            margin-top: 5rem;
            display: flex;
            flex-direction: column;

            position: relative;

            .chevron {
                position: absolute;
                top: -2.5rem;
                left: -2rem;
                font-size: 32px;
                color: #a2a2a2;

                cursor: pointer;
            }

            .title {
                // width: 70%;
                font-size: 35px;
                font-weight: 600;
                cursor: pointer;
            }

            .requirement {
                font-size: 15px;
                text-align: center;

                display: flex;
                align-items: center;
                gap: 0.3rem;

                margin-top: 1rem;

                &:last-child {
                    margin-top: 0;
                }

                svg {
                    font-size: 25px;

                    &.red {
                        color: var(--phone-color-red);
                    }

                    &.green {
                        color: var(--phone-color-green);
                    }
                }
            }

            .form-body {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                // height: 100%;

                gap: 2rem;

                margin-top: 10rem;

                input {
                    width: 100%;
                    height: 2rem;

                    font-size: 20px;
                    font-family: Tinder;

                    border: none;
                    border-bottom: 1px solid #00000048;

                    &:focus {
                        outline: none;
                        border-bottom: 2px solid var(--tinder-color-orange);
                    }

                    &.username {
                        text-transform: lowercase;
                    }
                }

                .dob {
                    display: flex;
                    flex-direction: column;
                    gap: 0.75rem;

                    .inputs {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 0.2rem;

                        font-size: 25px;

                        input {
                            border: none;
                            text-align: center;
                            letter-spacing: 0.3rem;

                            width: 6rem;

                            &::-webkit-inner-spin-button {
                                display: none;
                            }
                        }
                    }
                }
            }

            .photo-grid {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-gap: 0.5rem;

                margin-top: 7rem;

                .photo {
                    height: 9rem;
                    width: 100%;
                    position: relative;

                    background-color: #eaedf2;
                    border-radius: 7px;

                    background-position: center;
                    background-size: cover;
                    background-repeat: no-repeat;

                    border: 1px dashed #a2a2a2;

                    .button {
                        position: absolute;
                        bottom: -0.25rem;
                        right: -0.25rem;

                        width: 1.7rem;
                        height: 1.7rem;

                        border-radius: 50%;

                        svg {
                            font-size: 22px;
                        }

                        &.add {
                            background: linear-gradient(45deg, var(--tinder-color-pink), var(--tinder-color-orange));
                            svg {
                                color: #ffffff;
                            }
                        }

                        &.remove {
                            background: #ffffff;
                            svg {
                                color: var(--tinder-color-pink);
                            }
                        }
                    }
                }
            }

            textarea {
                margin-top: 5rem;

                height: 15rem;
                border: none;
                border-radius: 10px;
                background-color: #eaedf2;

                padding: 0.5rem;

                font-family: "Tinder";
                font-size: 18px;

                resize: none;

                &:focus {
                    outline: none;
                }
            }

            .showme {
                height: 100%;

                margin-top: 10rem;

                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 1rem;
            }
        }

        .buttons {
            margin-top: 10rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;

            .disclamer {
                color: rgba(255, 255, 255, 0.9);
                font-size: 15px;
                text-align: center;

                margin-bottom: 1rem;

                span {
                    color: #ffffff;
                    font-weight: 500;
                    text-decoration: underline;
                }
            }
        }

        .button {
            width: 100%;
            height: 3rem;
            border-radius: 25px;

            display: flex;
            align-items: center;
            justify-content: center;

            text-transform: uppercase;
            font-size: 18px;
            font-weight: 800;

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:hover {
                transform: scale(0.97);
            }

            &.border {
                color: #a2a2a2;
                border: 2px solid #a2a2a2;

                &:hover {
                    color: var(--tinder-color-pink);
                    border: 2px solid var(--tinder-color-pink);
                }

                &.active {
                    color: var(--tinder-color-pink);
                    border: 2px solid var(--tinder-color-pink);
                }
            }

            &.create {
                background-color: #ffffff;
                color: #000000;
            }

            &.signIn {
                background-color: transparent;
                color: #ffffff;
                border: 1px solid #ffffff;
            }

            &.gradient {
                background: linear-gradient(45deg, var(--tinder-color-pink), var(--tinder-color-orange));
                color: #ffffff;

                position: absolute;
                bottom: 2rem;
            }
        }
    }
}
