@import "styles/Colors.scss";

[data-theme="light"] {
    .numpad {
        div {
            background-color: #ffffff;
        }
    }
}

[data-theme="dark"] {
    .numpad {
        div {
            background-color: #636366;
        }
    }
}

.numpad {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5rem;

    div {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 0.5rem;

        padding: 0.2rem 1rem;
        // background-color: #ffffff;
        color: var(--phone-text-primary);

        font-size: 26px;
        font-family: "Roboto";
        font-weight: 400;

        border-radius: 5px;
        transition: all 0.1s ease-in-out;

        cursor: pointer;

        &:hover {
            filter: brightness(0.8);
        }

        span {
            font-size: 12px;
            font-weight: 400;
            letter-spacing: 3px;
        }

        &.zero {
            padding: 0.75rem;
        }
    }

    span {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        i {
            cursor: pointer;
            font-size: 25px;
            color: var(--phone-text-primary);
            transition: all 0.1s ease-in-out;

            &:hover {
                color: var(--phone-text-secondary);
            }
        }
    }
}
