@import "../../../../styles/Colors.scss";

.notes-container {
    position: relative;
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: var(--phone-color-primary);

    gap: 1rem;

    &.slide-right {
        animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    }

    &.slide-left {
        animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    }

    .title {
        font-size: 30px;
        font-weight: 600;
        font-style: bold;

        margin-right: auto;
        margin-left: 3rem;

        margin-top: 4.5rem;

        color: var(--phone-text-primary);
    }

    &::-webkit-scrollbar {
        display: none;
    }

    .searchbox {
        width: 80%;
    }

    .notes-body {
        max-height: 55rem;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        padding-bottom: 7rem;
    }

    .notes-list {
        width: 75%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        padding: 0rem 1.5rem;
        border-radius: 0.75rem;

        background-color: var(--phone-color-highlight);

        .note-item {
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 0.3rem;

            cursor: pointer;
            padding: 0.5rem 0;

            border-bottom: 1px solid var(--phone-color-border);

            &:first-child {
                padding-top: 1rem;
            }

            &:last-child {
                border-bottom: none;
                padding-bottom: 1rem;
            }

            .note-title {
                font-size: 18px;
                font-weight: 500;
                font-style: bold;

                color: var(--phone-text-primary);
            }

            .note-details {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;

                color: var(--phone-text-secondary);
                font-weight: 400;
                font-size: 15px;
            }
        }
    }

    .notes-bottom {
        position: absolute;
        bottom: 0;
        z-index: 2;

        background-color: var(--phone-color-primary);

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        padding: 1rem 1.5rem;
        padding-bottom: 2.5rem;

        width: 90%;

        .amount {
            color: var(--phone-text-primary);
            font-weight: 400;
            font-size: 15px;
        }

        svg {
            color: var(--phone-color-yellow);
            cursor: pointer;

            font-size: 28px;
        }
    }

    .top {
        width: 80%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        padding-top: 4rem;

        .back {
            display: flex;
            flex-direction: row;
            align-items: center;
            cursor: pointer;

            color: var(--phone-color-yellow);
            font-size: 17px;
            gap: 0.2rem;
        }

        .title {
            text-align: center;
            width: 60%;

            font-size: 19px;
            font-weight: 700;
            margin: 0;

            background-color: transparent;
            border: none;

            color: var(--phone-text-primary);

            &:active,
            &:focus {
                outline: none;
            }
        }

        div {
            flex: 1 1 0px;
        }
    }

    .bottom {
        width: 90%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        padding: 1rem 1.5rem;
        color: var(--phone-text-primary);

        .button {
            cursor: pointer;
            font-size: 25px;
        }
    }

    textarea {
        width: 80%;
        height: 100%;
        display: flex;

        border: none;
        font-size: 16px;
        font-family: "Roboto";

        background-color: transparent;
        resize: none;

        color: var(--phone-text-primary);

        &:active,
        &:focus {
            outline: none;
        }
    }
}
