@import "../../../../styles/Colors.scss";

.garage-container {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
    height: 100%;

    background-position: center;
    background-size: cover;
    position: relative;

    .blur-overlay {
        position: absolute;
        top: 0;
        left: 0;

        width: 100%;
        height: 100%;

        backdrop-filter: blur(20px) brightness(0.75);
    }

    .garage-header {
        width: 85%;
        z-index: 1;

        margin-top: 4rem;

        text-align: left;

        display: flex;
        align-items: center;
        justify-content: space-between;

        font-size: 28px;
        font-weight: 500;
        color: #fafafa;

        .close {
            cursor: pointer;
            font-size: 30px;

            color: #fafafa;
        }

        .title {
            display: flex;
            align-items: center;
            gap: 0.5rem;
        }
    }

    .garage-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        z-index: 1;

        width: 85%;
        height: 100%;
        overflow-y: auto;

        margin-top: 2rem;

        &::-webkit-scrollbar {
            display: none;
        }

        .subtitle {
            font-size: 18px;
            color: #ffffff;
            font-weight: 500;
            opacity: 0.9;

            margin: 0.5rem 0;

            display: flex;
            align-items: center;
            gap: 0.1rem;

            width: 100%;
            text-align: left;

            svg {
                font-size: 22px;
                opacity: 0.5;
            }
        }

        section {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;

            width: 100%;
        }

        .options-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 2rem;

            width: 100%;
        }

        .grid-wrapper {
            width: 100%;
        }

        .grid {
            width: 100%;
            height: 100%;

            display: grid;

            grid-template-columns: repeat(2, 1fr);
            gap: 0rem 1rem;
        }

        .item {
            width: 100%;
            height: 5rem;

            border-radius: 20px;

            background-color: var(--phone-highlight-opacity45);
            backdrop-filter: blur(30px);

            display: flex;
            align-items: center;

            cursor: pointer;
            transition: all 0.3s ease-in-out;

            &.big {
                display: flex;
                align-items: flex-end;
                height: 11rem;

                .icon {
                    position: absolute;
                    top: 0.75rem;
                    left: 0.75rem;
                    margin: 0;
                }
            }

            &:hover {
                filter: brightness(1.7);
            }

            .icon {
                margin-left: 0.75rem;
                width: 3rem;
                height: 3rem;
                border-radius: 50%;

                background-color: var(--phone-highlight-opacity45);

                display: flex;
                justify-content: center;
                align-items: center;

                opacity: 1;

                color: #ffffff;

                &.blue {
                    color: #66e4e5;
                }

                &.yellow {
                    color: var(--phone-color-yellow);
                }

                svg {
                    font-size: 28px;
                }
            }

            .info {
                display: flex;
                flex-direction: column;
                padding: 0.75rem;

                .title {
                    color: #fafafa;
                    opacity: 0.9;
                    font-size: 18px;
                    font-weight: 500;

                    margin: 0;
                }

                .value {
                    color: #ffffff;
                    opacity: 0.4;

                    font-size: 16px;
                    font-weight: 400;
                }
            }
        }
    }
}
