[data-theme="light"] {
    .searchbox {
        background-color: rgba(118, 118, 128, 0.12);

        input {
            color: rgba(60, 60, 67, 0.6);

            &::placeholder {
                color: rgba(60, 60, 67, 0.6);
            }
        }
    }
}

[data-theme="dark"] {
    .searchbox {
        background-color: rgba(118, 118, 128, 0.12);

        input {
            color: rgba(250, 250, 250, 0.6);

            &::placeholder {
                color: rgba(250, 250, 250, 0.6);
            }
        }
    }
}

.searchbox {
    width: 93%;
    z-index: 1;

    display: flex;
    align-items: center;
    gap: 0.25rem;

    padding: 0.25rem 1rem;
    border-radius: 7px;

    svg {
        width: 20px;
        opacity: 0.7;
    }

    input {
        width: 92%;
        border: none;

        font-family: "Roboto";
        font-size: 18px;
        font-weight: 400;

        background-color: transparent;

        &:focus,
        &:active {
            outline: none;
        }

        &::placeholder {
            // color: rgba(60, 60, 67, 0.6);
            font-size: 18px;
        }
    }
}
