.homescreen {
    height: 100%; //THIS WAS AT 96% BEFORE INCASE IT FUCKS SOMETHING UP
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    overflow: hidden;

    background-size: cover;

    .apps {
        padding-top: 4.5rem;

        display: flex;
        flex-direction: row;
        align-items: flex-start;
        gap: 0;

        height: 100%;
        width: 100%;

        overflow-y: auto;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    animation: zoomIn 0.7s cubic-bezier(0.19, 1, 0.22, 1);
}

.grid-container {
    display: flex;
    align-items: center;

    .grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 0.75rem 0rem;

        width: 22.5rem;
        padding: 0 1.5rem;
    }
}

.bottom {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;

    margin-bottom: 1rem;

    .circles {
        display: flex;
        flex-direction: row;
        gap: 0.75rem;
        cursor: pointer;

        i {
            color: rgba(245, 245, 245, 0.75);
            font-size: 13px;
        }

        .active {
            color: #ffffff;
        }
    }
}

.favourites {
    display: flex;
    flex-direction: row;
    justify-content: center;

    gap: 1rem;

    width: 85%;
    border-radius: 30px;

    padding: 0.75rem 1rem;

    backdrop-filter: blur(15px);

    background-color: var(--phone-color-opacity);
}
