@import "../../../../styles/Colors.scss";

.tiktok-container {
    height: 100%;
    width: 100%;

    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    .tiktok-wrapper {
        height: 93%;
        width: 100%;

        .tiktok-feed {
            height: 100%;

            .videos {
                width: 100%;
                height: 100%;

                display: flex;
                flex-direction: column;

                .video {
                    position: relative;
                    height: 100%;
                    width: 100%;

                    video {
                        position: absolute;
                        z-index: 1;

                        top: 0;

                        width: 100%;
                        height: 100%;
                        object-position: center;
                        object-fit: cover;
                        filter: brightness(0.8);
                    }

                    .video-info {
                        position: absolute;
                        bottom: 1rem;
                        left: 1rem;
                        z-index: 1;

                        display: flex;
                        flex-direction: column;
                        gap: 0.4rem;

                        color: #ffffff;

                        width: 60%;

                        .user {
                            font-size: 16px;
                            font-weight: 500;

                            margin-bottom: 0.4rem;

                            span {
                                font-size: 15px;
                                font-weight: 400;
                                color: #cccccc;
                            }
                        }

                        .caption {
                            font-size: 15px;
                            max-width: 100%;
                        }

                        .song {
                            display: flex;
                            align-items: center;
                            gap: 0.3rem;

                            font-weight: 300;

                            svg {
                                font-size: 18px;
                            }
                        }
                    }

                    .video-actions {
                        position: absolute;
                        bottom: 1rem;
                        right: 0.5rem;
                        z-index: 1;

                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        gap: 1rem;

                        .profile {
                            width: 3.25rem;
                            height: 3.25rem;
                            border-radius: 50%;

                            border: 2px solid #ffffff;
                            margin-bottom: 0.5rem;
                        }

                        .action {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            gap: 0.25rem;

                            font-size: 13px;
                            color: #ffffff;

                            cursor: pointer;
                            transition: all 0.2s ease-in-out;

                            svg {
                                font-size: 35px;
                            }

                            &:hover {
                                filter: brightness(0.7);
                            }
                        }

                        .song {
                            margin-top: 0.5rem;

                            display: flex;
                            align-items: center;
                            justify-content: center;

                            width: 3.5rem;
                            height: 3.5rem;
                            border-radius: 50%;

                            background: linear-gradient(140deg, rgba(0, 0, 0, 1) 0%, rgba(30, 30, 0, 30) 100%);

                            img {
                                height: 60%;
                                width: 60%;
                                object-fit: cover;
                                border-radius: 50%;
                            }
                        }
                    }
                }
            }
        }
    }

    .tiktok-footer {
        height: 10%;
        width: 100%;
        padding-bottom: 1.5rem;

        margin-top: auto;
        background-color: #000000;

        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 2.3rem;

        .item {
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;

            cursor: pointer;

            font-size: 12px;
            color: var(--phone-text-primary);
            font-weight: 300;

            &[data-active="true"] {
                font-weight: 500;

                svg {
                }
            }

            img {
                margin-bottom: 0.5rem;
            }

            svg {
                font-size: 35px;
                fill: var(--phone-text-primary);
            }
        }
    }
}
