.emoji-picker {
    position: absolute;
    bottom: 7rem;
    left: 1rem;
    z-index: 5;

    .epr-body {
        &::-webkit-scrollbar {
            display: none;
        }
    }
}

[data-theme="dark"] {
    .EmojiPickerReact {
        background-color: rgb(10, 10, 10);
    }

    .epr-emoji-category-label {
        background-color: rgba(20, 20, 20, 0.902);
    }
}
