@import "styles/Colors.scss";
@import "styles/Animations.scss";

.gallery-container {
    position: absolute;

    bottom: 0;
    height: 94%;
    width: 100%;

    z-index: 90;

    border-top-left-radius: 30px;
    border-top-right-radius: 30px;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: var(--phone-color-primary);
    animation: slideUp 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;

    .gallery-header {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        width: 90%;

        padding: 1.5rem;

        .cancel {
            color: var(--phone-color-blue);
            font-size: 20px;
            font-weight: 400;

            cursor: pointer;

            &:hover{
                filter: brightness(0.90);
            }
        }
    }

    .no-images {
        color: var(--phone-text-secondary);
        font-size: 18px;
        font-weight: 400;

        position: absolute;
        z-index: 1;

        text-align: center;
        justify-self: center;

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .gallery-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        width: 96%;

        .searchbox {
            width: 90%;
        }
    }

    .gallery-content {
        display: grid;
        grid-template-columns: repeat(3, 1fr);

        max-height: 47rem;

        width: 100%;

        overflow-y: scroll;
        overflow-x: hidden;

        &::-webkit-scrollbar {
            display: none;
        }

        gap: 0.15rem;

        .gallery-item {
            width: 8rem;
            height: 8rem;

            cursor: pointer;
            transition: all 0.2s ease-in-out;
            position: relative;

            &:hover {
                filter: brightness(0.6);
            }

            video,
            img {
                object-position: center;
                object-fit: cover;

                height: 100%;
                width: 100%;

                filter: brightness(0.9);
            }

            .video-duration {
                position: absolute;
                right: 0;
                bottom: 0;
                margin: 0.5rem;

                font-size: 16px;
                color: #ffffff;
                font-weight: 500;
            }
        }
    }
}
