@import "styles/Colors.scss";

.check {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 1.25rem;
    height: 1.25rem;

    border-radius: 50%;

    border: 2px solid var(--phone-color-grey);

    &.checked {
        background-color: var(--phone-color-blue);

        i {
            font-size: 14px;
            font-weight: 400;

            color: var(--phone-color-primary);
        }
    }
}
