.companies-container {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    background-color: var(--phone-color-primary);

    .animation-slide {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .companies-header {
        width: 100%;

        margin-left: 3rem;
        margin-top: 4.5rem;

        font-size: 30px;
        font-weight: 600;

        .title {
            color: var(--phone-text-primary);
        }
    }

    .companies-body {
        height: 100%;
        width: 100%;

        .companies-list {
            height: 100%;
            width: 100%;

            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1rem;
            overflow-y: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            .item {
                background-color: var(--phone-color-highlight2);

                width: 85%;

                display: flex;
                align-items: center;
                justify-content: space-between;

                padding: 1rem;
                border-radius: 0 0 10px;
                transition: all 0.2s ease-in-out;

                &[data-open="true"] {
                    background-color: rgba(200, 200, 200, 0.2);
                    border-left: 4px solid var(--phone-color-green);
                    // background: linear-gradient(130deg, rgba(0, 255, 38, 0.25) 0%, rgba(4, 181, 31, 0.25) 100%);
                }

                &[data-open="false"] {
                    background-color: var(--phone-color-highlight2);
                    border-left: 4px solid var(--phone-color-red);
                    cursor: not-allowed;
                    // background: linear-gradient(130deg, rgba(255, 0, 0, 0.25) 0%, rgba(181, 4, 4, 0.25) 100%);
                }

                .info {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;

                    img {
                        width: 50px;
                        height: 50px;
                    }

                    .company-details {
                        display: flex;
                        flex-direction: column;
                        gap: 0.25rem;

                        .title {
                            font-size: 17px;
                            color: var(--phone-text-primary);
                        }

                        .location {
                            display: flex;
                            align-items: center;
                            gap: 0.25rem;

                            color: var(--phone-text-secondary);
                            font-size: 15px;

                            svg {
                                font-size: 20px;
                            }
                        }
                    }
                }

                .actions {
                    display: flex;
                    align-items: center;
                    gap: 1rem;

                    font-size: 18px;
                    font-weight: 500;
                    color: var(--phone-color-red);

                    svg {
                        color: var(--phone-text-secondary);

                        font-size: 25px;
                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            filter: brightness(0.6);
                        }
                    }
                }
            }
        }

        .company-messages-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1.5rem;

            width: 100%;
            height: 100%;

            max-height: 100%;
            overflow-y: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            section {
                width: 100%;

                .title {
                    font-size: 18px;
                    font-weight: 500;
                    color: var(--phone-text-primary);
                    margin-bottom: 1rem;

                    margin-left: 6%;
                }

                .content {
                    display: flex;
                    flex-direction: column;

                    .item {
                        width: 90%;
                        height: 100%;

                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 0.5rem;

                        padding: 0.5rem 5%;
                        // padding-left: 5%;

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            background-color: var(--phone-color-hover);
                        }

                        img {
                            width: 3.1rem;
                            height: 3.1rem;
                            border-radius: 50%;
                            object-fit: cover;
                        }

                        .info {
                            display: flex;
                            flex-direction: column;

                            width: 85%;

                            border-bottom: 1px solid var(--phone-color-border);

                            .info-header {
                                width: 100%;
                                display: flex;
                                align-items: center;
                                justify-content: space-between;

                                .user {
                                    max-width: 70%;

                                    font-size: 18px;
                                    font-weight: 500;
                                    color: var(--phone-text-primary);
                                }

                                .right {
                                    display: flex;
                                    align-items: center;
                                    gap: 0.7rem;

                                    color: var(--phone-text-secondary);

                                    .time {
                                        font-size: 14px;
                                    }

                                    svg {
                                        font-size: 22px;
                                        opacity: 0.6;
                                    }
                                }
                            }

                            .message {
                                width: 70%;
                                height: 2rem;
                                color: var(--phone-text-secondary);
                                font-size: 16px;

                                margin-bottom: 0.5rem;
                                overflow-wrap: break-word;
                            }
                        }
                    }
                }
            }
        }

        .chat-header {
            width: 88%;

            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            padding: 0.3rem 0;

            margin-top: 2rem;

            border-bottom: 1px solid var(--phone-color-border);

            .back {
                display: flex;
                align-items: center;
                justify-content: center;

                color: var(--phone-color-blue);
                cursor: pointer;

                svg {
                    font-size: 35px;
                }

                span {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    font-size: 14px;
                    font-weight: 400;

                    background-color: var(--phone-color-blue);
                    border-radius: 15px;
                    height: 1.5rem;
                    width: 2rem;

                    color: #fafafa;

                    margin-left: -0.5rem;
                }
            }

            svg {
                color: var(--phone-color-blue);
                font-size: 25px;

                cursor: pointer;

                &.hidden {
                    visibility: hidden;
                }
            }

            .user {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                gap: 0.5rem;

                margin-top: 1rem;
                margin-right: 1.5rem;

                .avatar {
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                }

                .name {
                    font-weight: 400;
                    font-size: 16px;

                    color: var(--phone-text-primary);
                }
            }
        }

        .chat-container {
            display: relative;

            display: flex;
            flex-direction: column-reverse;
            height: 72%;

            width: 87%;

            overflow-y: auto;
            overflow-x: hidden;

            scroll-behavior: smooth;

            transition: all 0.2s ease-in-out;

            &::-webkit-scrollbar {
                display: none;
            }

            .chat-body {
                width: 100%;

                display: flex;
                flex-direction: column;

                margin-top: 1rem;
                padding: 1rem 0;
                gap: 0.5rem;

                .message {
                    font-size: 16px;
                    font-weight: 300;
                    width: auto;
                    max-width: 70%;

                    color: rgb(15, 20, 25);

                    display: flex;
                    flex-direction: column;
                    gap: 0.5rem;

                    .content-wrapper {
                        display: flex;
                        align-items: center;
                        gap: 0.25rem;

                        svg {
                            font-size: 35px;
                            color: var(--phone-color-red);
                        }
                    }

                    .content {
                        padding: 0.75rem 1rem;
                        border-radius: 20px;
                        overflow-wrap: break-word;
                        font-size: 18px;
                        max-width: 100%;

                        user-select: text;

                        a {
                            color: var(--phone-color-blue);
                            cursor: pointer;
                        }
                    }

                    .location {
                        border-radius: 20px;
                        width: 100%;
                        width: 16rem;

                        cursor: pointer;
                        display: flex;
                        flex-direction: column;

                        background-color: rgb(50, 50, 50);
                        transition: all;

                        .img {
                            height: 10rem;
                            border-radius: 20px;

                            filter: blur(2px);

                            background-size: cover;
                            background-position: center;
                        }

                        .sender {
                            padding: 0.75rem;

                            color: #ffffff;
                            opacity: 0.8;

                            font-size: 16px;
                            font-weight: 500;
                        }
                    }

                    .attatchments {
                        img,
                        video {
                            width: 100%;
                            border-radius: 15px;
                            cursor: pointer;
                        }
                    }

                    .date {
                        padding: 0 0.5rem;
                        font-size: 13px;
                        font-weight: 300;
                        color: #687684;
                    }

                    .status {
                        padding: 0 0.5rem;
                        font-size: 15px;
                        font-weight: 600;
                        color: var(--phone-color-red);
                    }

                    .user {
                        margin-top: 0.5rem;
                        padding: 0 0.5rem;
                        font-size: 14px;

                        font-weight: 300;
                        color: #687684;
                    }

                    &.self {
                        align-items: flex-end;
                        margin-left: auto;

                        cursor: pointer;

                        .date {
                            text-align: right;
                            margin-left: auto;
                        }

                        .content {
                            border-bottom-right-radius: 0;
                            text-align: left;

                            background-color: var(--phone-color-blue);
                            color: #fafafa;

                            .number {
                                color: #fafafa;
                                font-weight: 400;
                            }
                        }
                    }

                    &.other {
                        margin-right: auto;

                        .content {
                            border-bottom-left-radius: 0;

                            background-color: var(--phone-color-highlight3);
                            color: var(--phone-text-primary);

                            &:not(img) + .other {
                                background-color: transparent;
                                padding: 0;
                            }
                        }

                        .date {
                            margin-right: auto;
                        }
                    }
                }
            }
        }

        .chat-bottom {
            width: 90%;
            height: 5rem;

            display: flex;
            flex-direction: row;
            align-items: center;

            justify-content: center;
            gap: 0.75rem;

            background-color: var(--phone-color-primary);

            img {
                cursor: pointer;
                color: #999999;
                width: 25px;

                svg,
                path {
                    color: #999999;
                    fill: #999999;
                }
            }

            svg {
                font-size: 40px;
                color: #999999;

                cursor: pointer;
            }

            .input {
                width: 100%;

                border-radius: 30px;

                display: flex;
                align-items: center;

                border: 2px solid var(--phone-color-border);
                padding: 0.3rem;
            }

            input {
                width: 90%;

                border: none;

                background-color: transparent;

                color: var(--phone-text-primary);
                font-family: "Roboto";
                font-size: 17px;

                padding: 0.4rem 0.5rem;

                &:active,
                &:focus {
                    outline: none;
                }
            }

            .send {
                display: flex;
                align-items: center;
                justify-content: center;

                background-color: var(--phone-color-blue);

                border-radius: 50%;

                height: 1.8rem;
                width: 1.8rem;

                cursor: pointer;

                svg {
                    font-size: 20px;
                    color: #ffffff;
                }
            }
        }

        .actions-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 1.5rem;
            overflow-y: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            width: 100%;
            height: 100%;

            section {
                width: 89%;

                .title {
                    font-size: 18px;
                    font-weight: 500;
                    color: var(--phone-text-primary);
                    margin-bottom: 0.5rem;
                }

                .content {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    background-color: var(--phone-color-highlight2);
                    border-radius: 10px;

                    .row {
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: space-evenly;
                    }

                    &[data-clickable="true"] {
                        .item {
                            cursor: pointer;
                            transition: all 0.2s ease-in-out;

                            &:hover {
                                background-color: var(--phone-color-hover);

                                border-radius: 10px;
                            }
                        }
                    }

                    .item {
                        width: 90%;
                        height: 100%;

                        display: flex;
                        flex-direction: column;
                        gap: 0.2rem;

                        padding: 0.5rem 1rem;
                        // margin: 0 0.5rem;
                        border-bottom: 1px solid var(--phone-color-border);

                        &.single {
                            padding: 1rem;
                            cursor: pointer;

                            .value {
                                font-size: 18px;
                            }
                        }

                        .title {
                            font-size: 16px;
                            color: var(--phone-text-secondary);
                            font-weight: inherit;
                        }

                        &[data-col="2"] {
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            border-bottom: none;
                            border-right: 1px solid var(--phone-color-border);
                            padding-bottom: 1rem;

                            cursor: pointer;
                            transition: all 0.2s ease-in-out;

                            &:hover {
                                background-color: var(--phone-color-hover);

                                &:nth-child(1) {
                                    border-bottom-left-radius: 10px;
                                }

                                &:nth-child(2) {
                                    border-bottom-right-radius: 10px;
                                }
                            }

                            &:last-child {
                                border-right: none;
                            }
                        }

                        .value {
                            font-size: 18px;
                            color: var(--phone-text-primary);
                        }

                        &:last-child {
                            border-bottom: none;
                        }
                    }
                }
            }
        }
    }

    .companies-footer {
        position: absolute;
        bottom: 0rem;

        width: 100%;

        display: flex;
        justify-content: center;
        gap: 5rem;

        border-top: 1px solid var(--phone-color-border);
        background-color: var(--phone-color-opacity);
        backdrop-filter: blur(30px);

        padding-bottom: 3rem;

        .item {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 0.15rem;

            margin-top: 1rem;

            color: var(--phone-text-secondary);
            font-size: 14px;

            cursor: pointer;
            transition: all 0.2s ease-in-out;

            svg {
                font-size: 30px;
                color: var(--phone-text-secondary);
            }

            &:hover,
            &[data-active="true"] {
                color: var(--phone-color-blue);
                svg {
                    color: var(--phone-color-blue);
                }
            }
        }
    }
}
