@import "../../../../styles/Colors.scss";

.calculator-container {
    display: flex;

    align-items: center;
    justify-content: center;

    width: 100%;
    height: 100%;

    background-color: #000000;

    .calculator-wrapper {
        display: flex;
        flex-direction: column;

        gap: 1rem;

        height: 95%;
        width: 85%;
        margin: 0 auto;
        position: relative;
    }

    .result-container {
        text-align: right;

        margin-top: auto;

        color: #f6f6f6;
        font-size: 5rem;
        font-weight: 300;
    }

    .calculator-buttons {
        display: grid;
        grid-template-columns: repeat(4, 1fr);

        gap: 0.8rem;

        margin-bottom: 2rem;

        .button {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            background-color: #313131;

            border-radius: 50%;
            height: 5.25rem;
            width: 5.25rem;

            font-size: 40px;
            color: $white4;
            font-weight: 400;

            cursor: pointer;

            transition: 0.5s;

            &:hover {
                filter: brightness(1.6);
            }
        }

        .orange {
            background-color: #f6992e;

            &:hover {
                background-color: #ecb474;

                filter: none;
            }
        }

        .grey {
            background-color: #a0a0a0;
            color: $black-primary;
        }

        .enabled {
            color: #f6992e;
            background-color: $white-primary;
        }

        .fill {
            grid-column: 1 / -3;
            border-radius: 50px;

            width: 100%;
        }
    }
}
