.twitter-container {
    height: 100%;
    width: 100%;

    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    font-family: "Twitter";

    background-color: var(--twitter-primary);

    .slide {
        height: 100%;
        width: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;

        &.left {
            animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .new-tweet {
        display: flex;
        align-items: center;
        justify-content: center;

        position: absolute;
        bottom: 6rem;
        right: 1.5rem;

        height: 3.9rem;
        width: 3.9rem;
        border-radius: 50%;
        background-color: var(--twitter-blue);

        box-shadow: 0 0.3rem 0.4rem rgba(0, 0, 0, 0.15);

        cursor: pointer;

        img {
            width: 40%;
        }

        svg {
            height: 2rem;
            width: 2rem;
            path {
                fill: #ffffff;
            }
        }
    }

    .twitter-header {
        margin-top: 4rem;

        width: 100%;

        .twitter-header-bottom {
            display: flex;
            align-items: center;
            justify-content: center;

            .options {
                width: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 2rem;

                .option {
                    text-align: center;

                    padding: 0.5rem 2rem;
                    margin-top: -0.5rem;

                    font-size: 16px;
                    transition: all 0.2s ease-in-out;

                    color: var(--twitter-secondary-text);

                    &:hover {
                        background-color: var(--twitter-hover);
                        font-weight: 600;
                    }

                    flex: 1;

                    border-bottom: 3px solid transparent;

                    &.active {
                        color: var(--twitter-primary-text);

                        font-weight: 600;
                        border-bottom: 3px solid var(--twitter-blue);
                    }
                }
            }
        }

        .twitter-header-top {
            display: flex;
            flex-direction: row;
            align-items: center;

            justify-content: space-between;

            padding-bottom: 0.5rem;

            .profile-picture {
                align-self: left;

                object-fit: cover;
                object-position: center;

                border-radius: 50%;

                height: 40px;
                width: 40px;

                margin: 0 1.5rem;

                cursor: pointer;
            }

            .logo {
                display: flex;
                align-items: center;
                justify-content: center;

                img {
                    width: 30px;
                }
            }

            .title {
                text-align: center;
                font-size: 17px;
                font-weight: bold;
            }

            .search-container {
                width: 140%;

                input {
                    padding: 0.5rem 1.5rem;

                    border-radius: 20px;

                    font-size: 14px;

                    border: none;

                    &::placeholder,
                    &::-webkit-input-placeholder {
                        text-align: center;
                    }

                    &:active,
                    &:focus {
                        outline: none;
                    }
                }
            }

            div {
                flex: 1 1 0px;
            }
        }
    }

    .tweets-container {
        position: relative;
        max-height: 47.3rem;
        width: 88%;

        overflow: auto;

        &::-webkit-scrollbar {
            display: none;
        }

        animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    }

    .footer {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 4.5rem;

        width: 100%;

        margin-top: auto;

        padding-top: 1rem;
        padding-bottom: 2.8rem;

        z-index: 1;
        background-color: var(--twitter-primary);

        svg {
            height: 1.75rem;
            width: 1.75rem;
            cursor: pointer;

            transition: all 0.2s ease-in-out;

            path {
                fill: var(--twitter-secondary);
            }
        }
    }

    .tweets {
        width: 85%;
        position: relative;

        display: flex;
        flex-direction: column;
        align-items: center;

        max-height: 100%;
        overflow: auto;

        padding: 1rem 2rem;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    .profile {
        position: relative;
        width: 100%;
        height: 90%;

        display: flex;
        flex-direction: column;
        position: relative;

        animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        .profile-header {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;

            img,
            div {
                border-top-left-radius: 25px;
                border-top-right-radius: 25px;

                width: 100%;
                height: 8rem;

                object-fit: cover;
                object-position: center;
            }

            .go-back {
                display: flex;
                align-items: center;
                justify-content: center;

                position: absolute;
                left: 1.5rem;
                top: 3rem;

                cursor: pointer;

                background-color: #0f0f0f;
                height: 2rem;
                width: 2rem;

                border-radius: 50%;
                font-size: 14px;
                color: #ffffff;
            }
        }

        .profile-details {
            padding: 0 2.5rem;

            .top {
                display: flex;
                justify-content: right;

                .profile-picture {
                    position: absolute;

                    top: 6rem;
                    left: 2rem;

                    width: 72px;
                    height: 72px;

                    img {
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                        object-fit: cover;
                        object-position: center;

                        border: 3px solid var(--twitter-primary);
                    }
                }

                .action-buttons {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    gap: 0.5rem;

                    margin-left: auto;

                    .button {
                        height: 2.25rem;
                        width: 2.25rem;
                        margin-top: 1rem;

                        border-radius: 50%;
                        cursor: pointer;
                        transition: all 0.2s ease-in-out;

                        border: 1px solid rgb(139, 152, 165);

                        display: flex;
                        align-items: center;
                        justify-content: center;

                        svg {
                            width: 22px;
                            color: var(--instagram-primary-text);
                            fill: var(--instagram-primary-text);
                        }

                        &:hover {
                            background-color: var(--twitter-hover);
                        }
                    }
                }

                .edit,
                .follow,
                .following {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    padding: 0.5rem 1rem;
                    margin-top: 1rem;

                    border-radius: 20px;

                    cursor: pointer;

                    border: 1px solid var(--twitter-border);

                    &:hover {
                        background-color: var(--twitter-hover);
                    }
                }

                .edit,
                .following {
                    border: 1px solid var(--twitter-border-secondary);

                    color: var(--twitter-primary-text);
                }
            }

            .profile-bottom {
                display: flex;
                flex-direction: column;
                gap: 0.7rem;

                // margin-top: 1rem;

                .name-area {
                    display: flex;
                    flex-direction: column;

                    .name {
                        display: flex;
                        flex-direction: row;
                        align-items: center;

                        gap: 0.2rem;

                        font-weight: bold;
                        font-size: 22px;

                        color: var(--twitter-primary-text);

                        i {
                            font-size: 18px;
                        }
                    }

                    .username {
                        display: flex;
                        align-items: center;

                        gap: 0.3rem;

                        font-size: 17px;
                        font-weight: medium;
                        // color: rgb(139, 152, 165);
                        color: var(--twitter-secondary-text);

                        .follows {
                            padding: 0.3rem;
                            font-size: 13px;
                            border-radius: 7px;
                        }
                    }
                }

                .bio {
                    font-size: 17px;
                    font-weight: medium;

                    margin-top: 0.2rem;

                    color: var(--twitter-primary-text);
                }

                .joindate {
                    display: flex;
                    gap: 0.3rem;
                    align-items: center;

                    font-size: 16px;
                    font-weight: medium;
                    color: var(--twitter-alt-text);
                }

                .stats {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 0.5rem;

                    div {
                        color: var(--twitter-alt-text);
                        font-size: 16px;

                        cursor: pointer;
                        transition: all 0.2s ease-in-out;
                        &:hover {
                            filter: brightness(0.8);
                        }

                        span {
                            color: var(--twitter-primary-text);
                            font-size: 17px;
                            font-weight: bold;
                        }
                    }
                }

                .categories {
                    margin-top: 0.5rem;

                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    color: var(--twitter-alt-text);

                    div {
                        text-align: center;
                        padding: 0.5rem;

                        font-size: 16px;
                        transition: all 0.2s ease-in-out;

                        &:hover {
                            font-weight: 600;
                        }

                        flex: 1;

                        border-bottom: 3px solid transparent;

                        &:hover {
                            background-color: var(--twitter-hover);
                        }

                        &.active {
                            font-weight: 600;
                            border-bottom: 3px solid var(--twitter-blue);
                            color: var(--twitter-primary-text);
                        }
                    }
                }
            }
        }

        .profile-tweets-container {
            max-height: 24rem;
        }
    }

    .new-tweet-container {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        position: relative;

        height: 93%;

        padding: 0 1.5rem;
        padding-top: 4rem;

        animation: slideUp 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        .new-tweet-header {
            display: flex;
            flex-direction: row;

            align-items: center;
            justify-content: space-between;

            .cancel-text {
                color: $twitter-blue;
                font-size: 18px;

                cursor: pointer;
            }

            .tweet-button {
                color: rgb(247, 249, 249);
                background-color: $twitter-blue;
                padding: 0.6rem 1rem;
                border-radius: 20px;
                font-size: 17px;

                cursor: pointer;
            }
        }

        .message-container {
            display: flex;
            flex-direction: column;
            gap: 2rem;
            height: 40rem;

            overflow-y: auto;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                display: none;
            }

            .message-top {
                display: flex;
                flex-direction: row;
                gap: 1rem;

                .profile-picture {
                    img {
                        height: 40px;
                        width: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                        object-position: center;
                    }
                }

                textarea {
                    width: 100%;
                    height: auto;

                    box-sizing: border-box;
                    // border: 1px solid $dark-border-color;

                    resize: none;
                    border: none;

                    padding: 0.5rem;

                    font-family: "Twitter";
                    font-size: 18px;

                    &:active,
                    &:focus {
                        outline: none;
                    }
                }
            }

            .tweet-attatchments {
                display: flex;
                flex-direction: row;
                align-items: flex-start;

                gap: 0.5rem;

                max-height: 20rem;

                overflow: scroll;
                &::-webkit-scrollbar {
                    display: none;
                }

                .tweet-attatchment {
                    position: relative;
                    min-height: 15rem;
                    max-height: 20rem;
                    max-width: 15rem;
                    // width: 15rem;

                    img,
                    video {
                        object-fit: contain;
                        object-position: center;

                        width: 100%;
                        height: 100%;
                        max-height: 20rem;

                        border-radius: 10px;
                    }

                    i {
                        position: absolute;
                        top: 0.5rem;
                        right: 0.5rem;

                        height: 1.75rem;
                        width: 1.75rem;
                        border-radius: 50%;

                        display: flex;
                        align-items: center;
                        justify-content: center;

                        background-color: rgba(0, 0, 0, 0.8);
                        color: #ffffff;

                        cursor: pointer;
                        font-size: 14px;
                    }
                }
            }

            .buttons {
                position: absolute;
                bottom: 0;

                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 1rem;

                .button {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    height: 5rem;
                    width: 5rem;

                    margin: 2rem 0rem;

                    border-radius: 20px;
                    border: 2px solid $light-border-color;

                    color: $twitter-blue;
                    font-size: 32px;
                    font-weight: 200;

                    cursor: pointer;
                    transition: all 0.2s ease-in-out;
                    &:hover {
                        font-size: 33px;
                    }
                }
            }
        }
    }

    .viewtweet-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
        margin-top: 4rem;

        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;

        &::-webkit-scrollbar {
            display: none;
        }

        &.replying {
            margin-top: 0;
        }

        animation: slideLeft 0.5s cubic-bezier(0.19, 1, 0.22, 1);

        .viewtweet-header {
            width: 100%;

            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            .viewtweet-title {
                font-size: 19px;
                font-weight: bold;
                // color: rgb(15, 20, 25);
                text-align: center;
            }

            i {
                margin-left: 2rem;
                color: $twitter-blue;
                font-size: 22px;
                font-weight: 400;

                cursor: pointer;
            }

            div {
                width: 100%;
                flex: 1 1 auto;
            }
        }

        .viewtweet-body {
            display: flex;
            flex-direction: column;

