            }

            &.self {
                align-items: flex-end;
                margin-left: auto;

                .date {
                    text-align: right;
                    margin-left: auto;
                }

                .content {
                    padding: 0.5rem 1rem;
                    border-radius: 15px;

                    text-align: right;

                    background-color: rgb(29, 155, 240);
                    color: $white3;

                    border-bottom-right-radius: 0;

                    word-break: break-all;
                }
            }

            &.other {
                margin-right: auto;

                .tweet-with-pfp {
                    display: flex;
                    flex-direction: row;
                    align-items: flex-end;
                    gap: 0.6rem;
                }

                .tweet-content {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;

                    .content {
                        padding: 0.5rem 1rem;
                        border-radius: 15px;

                        background-color: var(--twitter-background-highlight);
                        color: var(--twitter-primary-text);

                        border-bottom-left-radius: 0;
                        word-break: break-all;

                        &:not(img) + .other {
                            background-color: transparent;
                            padding: 0;
                        }
                    }

                    .date {
                        margin-left: 3rem;
                        margin-right: auto;
                    }
                }
            }
        }
    }

    .attachments {
        position: absolute;
        bottom: 12rem;
        left: 2rem;

        display: flex;
        align-items: center;
        gap: 0.5rem;

        .attachment {
            position: relative;
            width: 75px;
            height: 75px;

            img,
            video {
                width: 100%;
                height: 100%;
                border-radius: 20px;
            }

            i {
                position: absolute;
                top: 0.4em;
                right: 0.4rem;

                height: 1.5rem;
                width: 1.5rem;
                border-radius: 50%;

                display: flex;
                align-items: center;
                justify-content: center;

                background-color: rgba(0, 0, 0, 0.8);
                color: #ffffff;

                cursor: pointer;
                font-size: 14px;
            }
        }
    }

    .message-bottom {
        width: 78%;
        height: 4rem;

        display: flex;
        flex-direction: row;
        align-items: flex-end;

        justify-content: center;
        gap: 1rem;

        position: relative;

        background-color: var(--twitter-primary);

        padding: 0 3rem;
        border-radius: 15px;

        // border top

        svg {
            cursor: pointer;

            width: 3.25rem;
            height: 3.25rem;

            path {
                fill: $twitter-blue;
            }
        }

        textarea {
            width: 100%;
            height: 1.75rem;

            resize: none;

            overflow: auto;

            &::-webkit-scrollbar {
                display: none;
            }

            border-radius: 15px;
            font-size: 15px;
            padding: 0.75rem;

            background-color: var(--phone-color-highlight);
            color: var(--twitter-primary-text);

            border: none;
            font-family: "Roboto";

            &:active,
            &:focus {
                outline: none;
            }

            &::placeholder {
                font-size: 15px;
            }
        }
    }
}

.tweet {
    width: 100%;

    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    cursor: pointer;
    position: relative;

    padding: 0.6rem 0;

    &:first-child {
        padding-top: 1rem;
    }

    .retweeted {
        display: flex;
        align-items: center;
        gap: 0.3rem;

        margin-left: 1.6rem;

        // color: #687684;
        font-weight: 400;
        font-size: 15px;
        font-weight: 500;
    }

    .tweet-body {
        display: flex;
        flex-direction: row;
        gap: 0.7rem;

        .profile-picture {
            img {
                height: 40px;
                width: 40px;
                border-radius: 50%;
                object-fit: cover;
                object-position: center;
            }
        }

        .tweet-content {
            display: flex;
            flex-direction: column;
            gap: 0.25rem;

            width: 80%;

            .tweet-header {
                display: flex;
                align-items: center;
                gap: 0.3rem;

                .name {
                    font-size: 18px;
                    font-weight: bold;

                    // color: #141619;
                }

                .username,
                .time {
                    // color: #687684;
                    font-weight: 500;
                    font-size: 15px;
                }

                .verified {
                    // color: $twitter-blue;
                    font-size: 20px;
                }
            }

            .replying-to {
                font-size: 14px;
                cursor: pointer;

                span {
                    font-size: 15px;

                    color: $twitter-blue;
                }
            }

            .text {
                max-width: 100%;
                font-size: 17px;
                overflow-wrap: break-word;
            }

            .attatchments {
                display: flex;
                flex-direction: row;
                align-items: flex-start;

                gap: 0.5rem;

                margin-top: 1rem;

                overflow: scroll;
                &::-webkit-scrollbar {
                    display: none;
                }

                .attatchment {
                    position: relative;

                    img,
                    video {
                        object-fit: contain;
                        object-position: center;

                        width: 100%;
                        height: 100%;
                        max-height: 25rem;

                        border-radius: 5px;
                    }
                }
            }

            .actions {
                width: 100%;

                display: flex;
                align-items: center;
                gap: 1.75rem;

                margin-top: 0.25rem;

                .action {
                    display: flex;
                    align-items: center;

                    font-size: 15px;
                    // color: #687684;

                    i {
                        padding: 0.4rem;
                        font-size: 20px;
                    }
                }

                .like {
                    &:hover {
                        color: rgb(249, 24, 128);
                        i {
                            border-radius: 50%;
                            background-color: rgba(249, 24, 128, 0.05);
                        }
                    }

                    &.active {
                        color: rgb(249, 24, 128);

                        i {
                            font-weight: 600;
                        }
                    }
                }

                .delete {
                    &:hover {
                        color: rgb(249, 24, 24);
                        i {
                            border-radius: 50%;
                            background-color: rgba(249, 24, 128, 0.05);
                        }
                    }
                }

                .retweet {
                    &:hover {
                        color: rgb(0, 186, 124);

                        i {
                            border-radius: 50%;
                            background-color: rgba(0, 186, 124, 0.1);
                        }
                    }

                    &.active {
                        color: rgb(0, 186, 124);

                        i {
                            font-weight: 600;
                        }
                    }
                }

                .comment {
                    &:hover {
                        color: $twitter-blue;

                        i {
                            border-radius: 50%;
                            background-color: rgba(29, 155, 240, 0.1);
                        }
                    }

                    &.active {
                        color: $twitter-blue;

                        i {
                            font-weight: 700;
                        }
                    }
                }
            }
        }
    }
}

.mention,
.hashtag {
    color: $twitter-blue;
    font-weight: bold;
    transition: all 0.2s ease-in-out;

    cursor: pointer;

    &:hover {
        text-decoration: underline;

        transform: scale(1.01);
    }
}

.rainbow-text {
    background-image: linear-gradient(to left, violet, indigo, blue, green, yellow, orange, red);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    font-weight: bold;
    transition: all 0.2s ease-in-out;

    filter: brightness(0.8);

    cursor: pointer;

    &:hover {
        text-decoration: underline;

        transform: scale(1.01);
    }
}
