@import "../../../../styles/Colors.scss";
@import "../../../../styles/Animations.scss";

@keyframes heart-animation {
    0% {
        display: block;
        transform: scale(0.5);
    }
    10% {
        transform: scale(1);
    }
    30% {
        transform: scale(1.1);
    }
    50% {
        transform: scale(1);
    }
    90% {
        transform: scale(1);
    }
    100% {
        display: none;
        transform: scale(0.5);
    }
}

.instagram-container {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;

    height: 100%;

    width: 100%;

    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: var(--instagram-primary);

    .slide {
        &.right {
            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }

        &.up {
            animation: slideUp 0.5s cubic-bezier(0.19, 1, 0.22, 1);
        }
    }

    .loading {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .instagram-footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 80%;
        padding: 0.75rem 2.5rem;
        padding-bottom: 3rem;

        position: absolute;
        bottom: 0;
        z-index: 2;

        border-top: 1px solid var(--instagram-border);
        background-color: var(--instagram-primary);

        border-bottom-left-radius: 30px;
        border-bottom-right-radius: 30px;

        img,
        svg {
            width: 25px;
            height: 25px;

            cursor: pointer;
            transition: all 0.1s ease-in-out;

            &:hover {
                transform: scale(1.05);
            }

            color: var(--instagram-primary-text);

            &.active {
                color: var(--instagram-primary-text);
                fill: var(--instagram-primary-text) !important;
            }

            &.profile {
                width: 28px;
                height: 28px;
                border-radius: 50%;
                object-fit: cover;
            }
        }
    }

    .instagram-wrapper {
        width: 100%;
        height: 100%;

        .instagram-header {
            margin-top: 3.5rem;

            width: 90%;
            display: flex;
            align-items: center;
            gap: 0.3rem;
            padding: 0 1.5rem;
            padding-bottom: 0.5rem;

            &.feed-header {
                width: 90%;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                svg {
                    width: 1.75rem;
                    height: 1.75rem;

                    cursor: pointer;
                    transition: all 0.1s ease-in-out;

                    color: var(--instagram-primary-text);
                }
            }

            .name {
                font-size: 25px;
                font-weight: 700;

                color: var(--instagram-primary-text);

                display: flex;
                align-items: center;
                gap: 0.25rem;

                span {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    img {
                        width: 22px;
                        height: 22px;
                    }
                }
            }

            .notifications {
                display: flex;
                align-items: center;
                justify-content: center;

                height: 1.3rem;
                width: 1.3rem;
                border-radius: 50%;
                background-color: #eb4956;

                font-size: 13px;
                font-weight: 400;
                color: #fff;
            }
        }

        .feed {
            width: 100%;
            display: flex;
            flex-direction: column;

            padding-bottom: 5rem;
            overflow-y: auto;
            overflow-x: hidden;
            max-height: 80%;

            &::-webkit-scrollbar {
                display: none;
            }

            animation: slideRight 0.5s cubic-bezier(0.19, 1, 0.22, 1);

            .stories-wrapper {
                padding: 0.25rem 0;
            }

            .stories {
                display: flex;
                align-items: center;
                gap: 1.5rem;

                overflow-x: auto;
                overflow-y: hidden;
                scroll-behavior: smooth;
                max-width: 100%;

                padding: 0.5rem 1rem;

                &::-webkit-scrollbar {
                    display: none;
                }

                div {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 0.5rem;

                    &.self {
                        .profile-picture {
                            display: block;
                            position: relative;

                            .plus {
                                position: absolute;

                                bottom: -5px;
                                right: -5px;

                                display: flex;
                                align-items: center;
                                justify-content: center;

                                border: 4px solid var(--instagram-primary);

                                height: 1.5rem;
                                width: 1.5rem;
                                border-radius: 50%;

                                color: #fff;
                                font-size: 14px;

                                background-color: rgb(0, 149, 246);
                            }
                        }
                    }

                    .profile-picture {
                        width: 3.75rem;
                        height: 3.75rem;
                        border-radius: 50%;

                        background-repeat: no-repeat;
                        background-position: center;
                        background-size: cover;

                        display: flex;
                        align-items: center;
                        justify-content: center;
                        // box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);

                        .circle {
                            width: 115%;
                            height: 115%;
                            background-position: center;
                            background-size: cover;

                            &.live {
                                width: 150%;
                                height: 150%;
                                margin-top: 7px;
                            }
                        }
                    }

                    .name {
                        font-size: 14px;
                        font-weight: 500;
                        // color: #000;
                        color: var(--instagram-primary-text);
                    }

                    cursor: pointer;
                    transition: all 0.1s ease-in-out;
                    &:hover {
                        transform: scale(1.025);
                    }
                }
            }
        }

        .no-posts {
            color: var(--instagram-primary-text);

            text-align: center;
            margin-top: 10rem;
            font-size: 16px;
            font-weight: 500;
        }

        .post {
            border-top: 1px solid #45454525;
            padding: 1rem 0;
            position: relative;

            .post-header {
                display: flex;
                align-items: center;
                gap: 0.5rem;

                padding-left: 0.75rem;
                padding-bottom: 0.75rem;

                background-color: var(--instagram-primary);

                cursor: pointer;

                .profile-picture {
                    width: 2.25rem;
                    height: 2.25rem;
                    border-radius: 50%;
                    box-shadow: 0px 0px 0px 1px #45454525;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }

                .name {
                    display: flex;
                    align-items: center;
                    gap: 0.2rem;

                    font-size: 17px;
                    font-weight: 500;
                    color: var(--instagram-primary-text);
                }
            }

            .post-body {
                .image-container {
                    display: flex;
                    align-items: center;

                    width: 100%;
                    height: 25rem;
                    overflow-x: auto;
                    overflow-y: hidden;

                    &::-webkit-scrollbar {
                        display: none;
                    }

                    div {
                        flex: 1;

                        min-width: 100%;
                        width: 100%;
                        height: 100%;

                        pointer-events: all;
                        cursor: pointer;
                        -webkit-user-drag: auto;

                        img,
                        video {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;

                            pointer-events: none;
                        }
                    }
                }

                .popup-heart {
                    position: absolute;

                    top: 13rem;
                    left: 0;
                    right: 0;
                    margin-left: auto;
                    margin-right: auto;

                    width: 6rem;
                    height: 6rem;

                    animation-name: heart-animation;
                    animation-duration: 1s;

                    &.show {
                        display: block;
                    }

                    &.hide {
                        display: none;
                    }

                    svg {
                        opacity: 0.95;
                        width: 100%;
                        height: 100%;
                    }
                }

                .actions {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 1rem 0.75rem;
                    gap: 1rem;

                    position: relative;

                    cursor: pointer;

                    .like-comment {
                        display: flex;
                        align-items: center;
                        gap: 0.75rem;

                        svg {
                            color: var(--instagram-primary-text);
                            font-size: 24px;

                            cursor: pointer;
                            pointer-events: all;
                            z-index: 1;
                        }
                    }

                    .scroll-dots {
                        position: absolute;
                        margin: auto;
                        left: 0;
                        right: 0;

                        display: flex;
                        align-items: center;
                        justify-content: center;
                        gap: 0.25rem;

                        div {
                            background-color: #737373;
                            height: 0.6rem;
                            width: 0.6rem;
                            border-radius: 50%;

                            cursor: pointer;
                            z-index: 1;
                            pointer-events: all;

                            transition: all 0.1s ease-in-out;

                            &:hover {
                                background-color: #2f81edc1;
                            }

                            &.active {
                                background-color: #2f80ed;
                            }
                        }
                    }
                }

                .liked-by {
                    padding: 0 0.75rem;
                    // color: #262626;
                    color: var(--instagram-primary-text);
                    font-size: 15px;
                    font-weight: 400;

                    margin-bottom: 0.1rem;

                    cursor: pointer;
                }

                .caption {
                    padding: 0 0.75rem;

                    color: var(--instagram-primary-text);
                    font-size: 16px;

                    font-weight: 400;
                    width: 90%;

                    overflow-wrap: break-word;

                    .user {
                        font-weight: 500;
                        cursor: pointer;
                        margin-right: 0.3rem;
                    }
                }

                .comments {
                    padding: 0.5rem 0.75rem;
                    // color: #737373;
                    color: var(--instagram-secondary-text);
                    font-size: 15px;

                    cursor: pointer;
                }

                .date {
                    padding: 0 0.75rem;
                    // color: #737373;
                    color: var(--instagram-secondary-text);
                    font-size: 14px;
                }
            }
        }

        .profile {
            .edit-profile-header {
                margin-top: 4rem;

                width: 88%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 0.5rem;

                padding: 0 1.5rem;

                color: var(--instagram-primary-text);

                .cancel {
                    cursor: pointer;
                    font-size: 18px;
                    font-weight: 400;
                }

                .title {
                    font-size: 20px;
                    font-weight: 600;
                }

                .save {
                    cursor: pointer;
                    font-size: 18px;
                    font-weight: 500;
                    color: var(--instagram-blue);
                }
            }

            .edit-profile-body {
                display: flex;
                flex-direction: column;
                align-items: center;
                margin-top: 0.3rem;

                padding: 1rem 2rem;

                .profile-picture {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 0.5rem;

                    cursor: pointer;

                    img {
                        object-fit: cover;
                        width: 6rem;
                        height: 6rem;
                        border-radius: 50%;
                    }

                    span {
                        color: var(--instagram-blue);
                        font-size: 16px;
                        font-weight: 500;
                    }
                }

                .profile-items {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    margin-top: 2rem;

                    gap: 1rem;

                    width: 100%;

                    color: var(--instagram-primary-text);
                    font-weight: 500;
                    font-size: 18px;

                    .item {
                        border-top: 1px solid var(--instagram-border);
                        padding: 0.5rem 0;

                        &:last-child {
                            border-bottom: 1px solid var(--instagram-border);
                        }

                        width: 100%;

                        display: flex;
                        flex-direction: row;
                        align-items: center;

                        input,
                        textarea {
                            margin-left: auto;
                            width: 17rem;

                            height: 2rem;

                            resize: none;

                            font-size: 18px;
                            font-weight: 500;

                            border: none;

                            font-family: inherit;

                            background-color: transparent;
                            color: var(--instagram-primary-text);

                            &::placeholder {
                                font-size: 18px;
                            }

                            &:active,
                            &:focus {
                                outline: none;
                            }
                        }
                    }
                }

                .sign-out {
                    margin-top: 22rem;

                    color: var(--instagram-blue);
                    font-size: 18px;
                    text-align: center;

                    cursor: pointer;
                }
            }

            .profile-header {
                margin-top: 4rem;

                width: 88%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 0.5rem;

                padding: 0 1.5rem;

                color: var(--instagram-primary-text);

                i {
                    cursor: pointer;
                    transition: all 0.1s ease-in-out;

                    font-size: 18px;
                    font-weight: 600;

                    flex: 1;
                }

                div {
                    font-size: 18px;
                    font-weight: 600;
                    text-align: center;
                    // flex: 1;

                    display: flex;
                    align-items: center;
                    gap: 0.2rem;
                }

                span {
                    flex: 1;
                }
            }

            .profile-body {
                display: flex;
                flex-direction: column;
                margin-top: 0.3rem;

                .profile-wrapper {
                    padding: 1rem 1rem;
                    padding-bottom: 2rem;

                    .top {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;

                        .live {
                            margin-top: 10px;
                            height: 135%;
                            width: 140%;
                        }

                        .stats {
                            display: flex;
                            align-items: center;
                            gap: 1rem;
                            margin-right: 1rem;

                            div {
                                display: flex;
                                flex-direction: column;
                                align-items: center;
                                // color: #262626;
                                color: var(--instagram-primary-text);

                                cursor: pointer;

                                .value {
                                    font-size: 18px;
                                    font-weight: 500;
                                }

                                .label {
                                    font-size: 16px;
                                    font-weight: 400;
                                }
                            }
                        }

                        padding-bottom: 0.75rem;
                    }

                    .name {
                        font-size: 18px;
                        font-weight: 600;
                        color: var(--instagram-primary-text);
                    }

                    .description {
                        font-size: 16px;
                        font-weight: 400;
                        color: var(--instagram-primary-text);
                        padding-bottom: 0.75rem;

                        max-width: 60%;
                        overflow-wrap: break-word;
                    }

                    .buttons {
                        display: flex;
                        align-items: center;
                        gap: 0.25rem;
                    }

                    .button {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        gap: 0.5rem;

                        width: 100%;

                        padding: 0.4rem 0.7rem;
                        border-radius: 7px;

                        cursor: pointer;
                        transition: all 0.1s ease-in-out;
                        &:hover {
                            filter: brightness(0.9);
                        }

                        font-size: 16px;
                        font-weight: 600;

                        &.follow {
                            color: #fff;
                            background-color: rgb(0, 149, 246);
                        }

                        &.following,
                        &.edit {
                            background-color: #dadada;
                            color: #000000;
                        }
                    }
                }

                .posts {
                    display: grid;

                    grid-template-columns: repeat(3, 1fr);
                    gap: 0.15rem;

                    border-top: 1px solid #45454525;

                    padding: 0 0.6rem;
                    padding-top: 0.2rem;

                    overflow-y: auto;
                    overflow-x: hidden;
                    max-height: 29rem;

                    &::-webkit-scrollbar {
                        display: none;
                    }

                    div {
                        width: 100%;
                        height: 8rem;
                        max-height: 8rem;

                        cursor: pointer;
                        transition: all 0.1s ease-in-out;
                        &:hover {
                            filter: brightness(0.6);
                        }

                        img,
                        video {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                }

                .profile-picture {
                    width: 6rem;
                    height: 6rem;
                    border-radius: 50%;

                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: cover;

                    display: flex;
                    align-items: center;
                    justify-content: center;
                    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);

                    .circle {
                        width: 115%;
                        height: 115%;
                        background-position: center;
                        background-size: cover;

                        &.live {
                            width: 150%;
                            height: 150%;
                            margin-top: 7px;
                        }
                    }
                }
            }
        }

        .instagram-stream {
            height: 100%;
            width: 100%;

            display: flex;
            align-items: center;
            justify-content: center;

            border-radius: 30px;

            overflow: hidden;

            video,
            canvas {
                height: 100%;
                filter: brightness(0.9);
            }
        }

        .instagram-stream-ended {
            position: absolute;
            top: 0.3rem;
            right: 0.6rem;

            height: 98.7%;
            width: 95.4%;

            background-color: rgba(0, 0, 0, 0.4);
            backdrop-filter: blur(10px);
            z-index: 21;

            display: flex;
            align-items: center;
            justify-content: center;

            .title {
                font-size: 26px;
                font-weight: 500;
                color: #ffffff;
            }

            svg {
                position: absolute;
                top: 4rem;
                right: 3rem;
                color: #ffffff;

                font-size: 40px;

                cursor: pointer;
                transition: all 0.2s ease-in-out;

                &:hover {
                    filter: brightness(0.8);
                }
            }
        }

        .instagram-story {
            height: 100%;
            width: 100%;

            background-color: #000000;

            .background-image {
                position: absolute;
                top: 3rem;

                height: 82%;
                width: 99%;

                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;

                border-radius: 10px;

                filter: brightness(0.8);
            }

            .story-wrapper {
                padding-top: 3.5rem;
                display: flex;
                flex-direction: column;
                align-items: center;

                .story-header {
                    width: 100%;

                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 0.75rem;

                    z-index: 1;

                    .story-steps {
                        width: 100%;
                        height: 0.2rem;

                        width: 95%;

                        display: flex;
                        align-items: center;
                        gap: 0.5rem;

                        > div {
                            height: 100%;
                            width: 100%;

                            background-color: #ffffff;
                            opacity: 0.5;

                            border-radius: 2px;

                            cursor: pointer;
                            transition: all 0.2s ease-in-out;

                            &.active {
                                opacity: 1;
                            }

                            &:hover:not(.active) {
                                opacity: 0.7;
                            }
                        }
                    }

                    .profile-details {
                        width: 92%;

                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        gap: 0.5rem;

                        padding: 0 1.5rem;

                        .profile {
                            display: flex;
                            align-items: center;
                            gap: 0.5rem;

                            .profile-picture {
                                background-position: center;
                                background-size: cover;
                                width: 2.5rem;
                                height: 2.5rem;
                                border-radius: 50%;
                            }

                            .name {
                                font-size: 18px;
                                font-weight: 500;
                                color: #ffffff;

                                display: flex;
                                align-items: center;
                                gap: 0.2rem;
                            }
                        }

                        .stats {
                            svg {
                                color: rgb(215, 215, 215, 0.9);
                                font-size: 35px;

                                cursor: pointer;
                            }
                        }
                    }
                }

                .story-footer {
                    position: absolute;
                    bottom: 3rem;
                    left: 0;
                    right: 0;
                    margin: 0 auto;

                    width: 90%;

                    z-index: 1;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    gap: 1rem;

                    .input {
                        width: 75%;

                        input {
                            width: 100%;

                            padding: 0.75rem 1rem;
                            border-radius: 20px;

                            font-size: 16px;
                            color: #ffffff;
                            border: 2px solid #dbdbdb;
                            opacity: 0.4;

                            background: transparent;

                            &::placeholder {
                                font-size: 16px;
                                color: #ffffff;
                            }

                            &:active,
                            &:focus {
                                outline: none;
                            }
                        }
                    }

                    svg {
                        cursor: pointer;
                        transition: all 0.1s ease-in-out;

                        color: #ffffff;
                        font-size: 28px;
                        font-weight: 300;

                        margin-right: 0.5rem;
                    }
                }
            }
        }

        .instagram-live {
            z-index: 20;
            height: 100%;
            width: 100%;

            position: absolute;
            top: 0;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;

            .profile-picture {
                background-position: center;
                background-size: cover;
                width: 2.5rem;
                height: 2.5rem;
                border-radius: 50%;
            }

            .live-header {
                margin-top: 3.5rem;

                width: 88%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 0.5rem;

                padding: 0 1.5rem;

                .profile {
                    display: flex;
                    align-items: center;
                    gap: 0.5rem;

                    .name {
                        font-size: 18px;
                        font-weight: 500;
                        color: #ffffff;

                        display: flex;
                        align-items: center;
                        gap: 0.2rem;
                    }
                }

                .stats {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;
                    margin-right: 1rem;

                    div {
                        padding: 0.5rem 0.8rem;
                        background-color: black;
                        border-radius: 10px;
                        color: rgb(215, 215, 215, 0.9);
                    }

                    .live {
                        text-transform: uppercase;
                        font-size: 16px;
                        font-weight: 500;

                        background: linear-gradient(35deg, #ffd600 -20%, #ff7a00 27%, #ff0169 73%, #e6e6fa 120%);
                    }

                    .viewers {
                        display: flex;
                        align-items: center;
                        gap: 0.3rem;

                        text-transform: uppercase;
                        font-size: 16px;
                        font-weight: 400;

                        background-color: rgba(0, 0, 0, 0.4);
                    }

                    .x {
                        margin-left: 0.5rem;
                        color: rgb(215, 215, 215, 0.9);
                        font-size: 30px;
                        font-weight: 300;

                        cursor: pointer;
                    }
                }
            }

            .comment-section {
                width: 85%;
                display: flex;
                flex-direction: column;
                gap: 1.5rem;

                margin-bottom: 2rem;

                .comment-feed {
                    display: flex;
                    flex-direction: column;
                    gap: 0.5rem;

                    max-height: 10rem;
                    overflow-y: auto;

                    &::-webkit-scrollbar {
                        display: none;
                    }

                    .comment {
                        display: flex;
                        align-items: center;
                        gap: 0.75rem;

                        img {
                            width: 2.75rem;
                            height: 2.75rem;
                            border-radius: 50%;
                        }

                        .info {
                            display: flex;
                            flex-direction: column;
                            font-size: 17px;
                            color: #ffffff;
                            max-width: 75%;

                            .name {
                                font-size: 18px;
                                font-weight: 500;

                                display: flex;
                                align-items: center;
                                gap: 0.2rem;

                                .verified {
                                    img {
                                        width: 1.2rem;
                                        height: 1.2rem;
                                    }
                                }
                            }

                            .comment-text {
