                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                        }
                    }

                    .username {
                        font-weight: 600;
                        font-size: 20px;

                        display: flex;
                        align-items: center;
                        gap: 0.2rem;
                    }
                }
            }

            .dm-body-container {
                display: flex;
                flex-direction: column-reverse;
                overflow: auto;

                &::-webkit-scrollbar {
                    display: none;
                }

                overflow: auto;
                scroll-behavior: smooth;
                width: 95%;
                height: 100%;
            }

            .dm-body {
                display: flex;
                flex-direction: column;

                padding: 0.5rem 1rem;
                gap: 0.5rem;

                // height: 40rem; // do something about this

                .message {
                    font-size: 19px;
                    font-weight: 400;
                    width: auto;
                    max-width: 85%;

                    color: var(--instagram-primary-text);

                    display: flex;
                    flex-direction: column;
                    gap: 0.5rem;

                    .attatchments {
                        img,
                        video {
                            width: 100%;
                            border-radius: 15px;
                        }
                    }

                    .profile-picture {
                        img {
                            width: 2.2rem;
                            height: 2.2rem;

                            border-radius: 50%;

                            object-fit: cover;
                            object-position: center;
                        }
                    }

                    .date {
                        padding: 0 0.5rem;
                        font-size: 13px;
                        font-weight: 300;
                        // color: #687684;
                        color: var(--instagram-secondary-text);
                    }

                    &.self {
                        align-items: flex-end;
                        margin-left: auto;

                        .date {
                            text-align: right;
                            margin-left: auto;
                        }

                        .content {
                            display: flex;
                            align-items: center;
                            justify-self: center;

                            padding: 0.5rem 1.2rem;
                            border-radius: 25px;

                            text-align: left;

                            background-color: var(--instagram-highlight);
                            border: 1px solid var(--instagram-border);

                            word-break: break-word;
                        }
                    }

                    &.other {
                        margin-right: auto;

                        .message-with-pfp {
                            display: flex;
                            flex-direction: row;
                            align-items: flex-end;
                            gap: 0.8rem;
                        }

                        .message-content {
                            display: flex;
                            flex-direction: column;
                            align-items: flex-start;

                            .content {
                                display: flex;
                                align-items: center;
                                justify-self: center;

                                padding: 0.5rem 1.1rem;
                                border-radius: 25px;

                                background-color: transparent;
                                // border: 2px solid rgb(239, 239, 239);
                                border: 1px solid var(--instagram-border);

                                word-break: break-word;

                                &:not(img) + .other {
                                    padding: 0;
                                }
                            }

                            .date {
                                margin-left: 3rem;
                                margin-right: auto;
                            }
                        }
                    }
                }
            }

            .attachments {
                position: absolute;
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;

                bottom: 10rem;
                left: 2rem;

                .attachment {
                    position: relative;

                    height: 5rem;
                    width: 5rem;

                    img,
                    video {
                        width: 100%;
                        height: 100%;

                        object-fit: cover;
                        border-radius: 10px;
                    }

                    svg {
                        position: absolute;

                        top: 0.25rem;
                        right: 0.25rem;

                        height: 1.3rem;
                        width: 1.3rem;
                        padding: 0.1rem;
                        border-radius: 50%;

                        display: flex;
                        align-items: center;
                        justify-content: center;

                        background-color: rgba(0, 0, 0, 0.8);
                        color: #ffffff;

                        cursor: pointer;
                        font-size: 13px;
                    }
                }
            }

            .dm-footer {
                display: flex;
                flex-direction: row;
                align-items: center;

                width: 88%;
                height: 2.5rem;

                border: 2px solid var(--instagram-border);

                margin: 0.5rem;

                border-radius: 30px;

                input {
                    width: 100%;
                    height: 2.5rem;
                    padding: 0.2rem 1rem;

                    background-color: transparent;
                    border: none;

                    color: var(--instagram-primary-text);

                    &:focus {
                        outline: none;
                    }
                }

                svg {
                    font-size: 35px;
                    margin-right: 1rem;

                    cursor: pointer;
                    color: var(--instagram-primary-text);
                }

                span {
                    font-size: 15px;
                    font-weight: 500;
                    color: var(--instagram-blue);
                    margin-right: 1rem;

                    cursor: pointer;
                }
            }
        }

        .verified {
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 1.15rem;
                height: 1.15rem;
            }
        }
    }
}
