@import "styles/Colors.scss";
@import "styles/Animations.scss";

[data-theme="light"] {
    .notification {
        .notification-wrapper,
        .stack {
            background-color: rgba(180, 180, 180, 0.7);
            backdrop-filter: blur(15px);
        }

        &[data-locked="true"] {
            & > div {
                background-color: rgba(200, 200, 200, 0.6);
            }
        }
    }
}

[data-theme="dark"] {
    .notification {
        .notification-wrapper,
        .stack {
            background-color: rgba(41, 41, 41, 0.8);
            backdrop-filter: blur(15px);
        }

        &[data-locked="true"] {
            & > div {
                background-color: rgba(20, 20, 20, 0.7);
            }
        }

        .title {
            font-weight: 500;
            opacity: 0.75;
        }
    }
}

.notification-container {
    position: absolute;
    z-index: 500;
    top: 1rem;

    width: 100%;
    overflow: hidden;

    .notification-wrapper {
        margin-top: 3.5rem;
    }
}

.notification {
    display: flex;
    flex-direction: column;
    align-items: center;

    font-family: "Roboto";
    z-index: 500;
    left: 0;
    right: 0;

    width: 22rem;

    position: relative;
    cursor: pointer;

    &[data-locked="true"] {
        margin: 0 auto;
        z-index: 1;
    }

    &[data-locked="false"] {
        margin: auto;
    }

    .stack {
        bottom: -0.45rem;
        position: absolute;

        width: 100%;
        height: 0.75rem;
        z-index: -1 !important;
        // margin-top: -0.5rem;
        border-radius: 0 0 18px 18px;
    }

    .notification-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 0.55rem;
        padding: 0.9rem 1rem;
        border-radius: 18px;
        width: 100%;

        z-index: 1;
    }

    .icon {
        width: 2.75rem;
        height: 2.75rem;
        border-radius: 10px;
    }

    .avatar {
        width: 2.75rem;
        height: 2.75rem;
        border-radius: 50%;
        margin-right: 0.3rem;

        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;

        .app-icon {
            position: absolute;
            z-index: 1;
            bottom: -0.25rem;
            right: -0.25rem;

            width: 1.25rem;
            height: 1.25rem;
            border-radius: 5px;
        }
    }

    .info {
        display: flex;
        flex-direction: column;
        width: 85%;

        &[data-icon="false"] {
            width: 100%;
        }

        .notification-header {
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            color: var(--phone-text-primary);
            font-weight: 500;

            .title {
                font-size: 16px;
                font-weight: 500;

                color: var(--phone-text-primary);
            }

            .time {
                opacity: 0.6;
            }
        }

        .content {
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            opacity: 0.8;

            .text {
                width: 80%;
                overflow-wrap: break-word;

                font-size: 16px;
                font-weight: 500;
                color: var(--phone-text-primary);

            }

            .thumbnail {
                width: 2.5rem;
                height: 2.5rem;
            }

            img,
            video {
                margin-top: 0.25rem;
                width: 100%;
                height: 100%;

                object-fit: cover;
                border-radius: 8px;

            }
        }
    }
}

.lockscreen-notification-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 0.75rem;

    z-index: 1;
    height: 100%;

    border-top-left-radius: 25px;
    border-top-right-radius: 25px;

    padding-bottom: 1rem;

    scroll-snap-align: start;

    .notification-stack {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;

        margin-bottom: 0.5rem;
    }
}

.lockscreen-notification-container-icon {
    background: rgba(0, 0, 0, 0.35);
    border-radius: 50%;
    color: rgb(255, 255, 255);
    font-size: 25px;
    margin-left: auto;
    margin-right: 2vw;
    padding: 0.5rem;
    transition: all 0.2s ease-in-out 0s;
    cursor: pointer;

    &:hover {
        filter: brightness(1.3);
        scale: 1.03;
    }

    &[data-active="true"] {
        background-color: #ffffff;
        color: #000000;
    }
}

@keyframes loadNotification {
    from {
        opacity: 0;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }
    to {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@keyframes clear {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
